<template>
  <div :style="{ width: width, height: height, padding: paddingSty }" class="ds-control">
    <div class="ds-home-panel">
      <div :class="{'card-warp': showType=='card', 'button-warp': showType=='button'}" class="title-warp-com">
        <div v-show="showType=='info'" :class="{ 'has-more': showMore }" class="title-info one-line">{{ title }}</div>
        <div v-show="showType=='card'" class="tab-ul-box">
          <ul class="tab-ul">
            <li class="layui-this only-one">{{ title }}</li>
          </ul>
        </div>
        <div v-show="showType=='button'" class="tab-button-box">
          <el-button-group>
            <el-button class="active" plain>{{ title }}</el-button>
          </el-button-group>
        </div>
        <div v-if="showMore" @click="more" class="title-more-oth">
          <slot name="right">{{ rightFont }}<i class="iconfont" :class="rightIcon"></i></slot>
        </div>
      </div>
      <div
        :class="{ 'design-padding': isDesign }"
        class="pannel-content"
        slot-name="default"
      >
        <slot name="default"></slot>
      </div>
    </div>
  </div>
</template>
<script>
import control from "_platform/mixins/layout";

export default dsf.component({
  name: "DsfHomePanel",
  ctrlCaption: "面板",
  mixins: [control],
  props: {
    title: {
      type: String,
      default: "面板标题",
    },
    // 宽度
    width: {
      type: String,
      default: "100%",
    },
    // 高度
    height: {
      type: String,
      default: "auto",
    },
    // 显示更多按钮
    showMore: {
      type: Boolean,
      default: true,
    },
    // 更多跳转地址
    moreUrl: {
      type: String,
      default: "",
    },
    // 可拖拽容器
    slots: {
      type: Array,
      default() {
        return [{ name: "default", controls: [] }];
      },
    },
    padding: {
      type: Array,
      default() {
        return [0, 0, 0, 0];
      },
    },
    rightFont: {
      type: String,
      default: "更多",
    },
    rightIcon: {
      type: String,
      default: "icon-shuangjiantouyou",
    },
    showType: {
      type: String,
      default: "info",
    },
  },

  data() {
    return {};
  },

  components: {},

  computed: {
    paddingSty: function () {
      return this.padding.join("px ") + "px";
    },
  },

  mounted() {},

  created() {},

  methods: {
    more() {
      if (this.isDesign) {
        return false;
      }
      window.open(dsf.url.getWebPath(this.moreUrl), "_blank");
    },
  },
});
</script>
