<template>
  <div class="page-home-default-header" :class="{mini: mini}">
    <div ref="logoBox" class="logo-box">
      <slot name="logo">
        <img v-if="logo" :src="logo" alt="" class="logo" @error="logoLoadSuccess = true" @load="logoLoaded"/>
        <div v-if="title && title.length" class="site-name">{{ title }}</div>
      </slot>
    </div>
    <div class="header-right">
      <DsfDefaultHomeMenuBar v-show="showMenu" :mini="mini" :menu-list="menuList" :active-key="activeKey" :width="menuWidth"></DsfDefaultHomeMenuBar>
      <div ref="headerUser" class="header-user">
        <div v-if="!dropdownConfig.length">
          <div class="header-user-icon" :style="{'background-image': `url(${defaultHeader})`}">
            <img v-show="showIcon" :src="headIcon" @error="headLoadError" @load="headLoadSuccess" />
          </div>
          <div class="username">
            欢迎您,{{ dsf.cookies.get("user_name") }}
            <i v-if="dropdownConfig.length" :class="[showDropdown ? 'icon-shangjiantou' : 'icon-xiajiantou']" class="iconfont" />
          </div>
        </div>
        <el-dropdown v-else :show-timeout="0" placement="bottom" @visible-change="showDropdown = $event" @command="handleCommand">
          <div>
            <div class="header-user-icon" :style="{'background-image': `url(${defaultHeader})`}">
              <img v-show="showIcon" :src="headIcon" @error="headLoadError" @load="headLoadSuccess" />
            </div>
            <div class="username">
              欢迎您,{{ dsf.cookies.get("user_name") }}
              <i v-if="dropdownConfig.length" :class="[showDropdown ? 'icon-shangjiantou' : 'icon-xiajiantou']" class="iconfont" />
            </div>
          </div>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-for="(item, index) in dropdownConfig" :key="(item.key || '') + '__' + index" :command="index">
              {{ item.name }}
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
  </div>
</template>

<script>
import DsfDefaultHomeMenuBar from "./aside/menuBar";

export default {
  name: 'DsfDefaultHomeHeader',
  inject: ['homeRoot'],
  components: {
    DsfDefaultHomeMenuBar
  },
  props: {
    dropdownConfig: {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    return {
      logo: dsf.config.setting_public_logo_image,
      title:dsf.config.setting_public_logo_title,
      headIcon: '',
      defaultHeader:dsf.config.setting_public_user_default_header,
      showIcon: false,
      showDropdown: false,
      menuWidth: 0,
      logoLoadSuccess: false
    }
  },
  computed: {
    menuList() {
      return this.homeRoot.menuList || [];
    },
    activeKey() {
      if (this.homeRoot.menuKey.length > 0) {
        return this.homeRoot.menuKey[0];
      }
      return '';
    },
    showMenu() {
      return this.menuList.length && (!this.logo || this.logoLoadSuccess);
    },
    mini() {
      return this.$root.setting.defaultHeaderMini;
    }
  },
  watch: {
    'mini' (to) {
      this.homeRoot.setPosition('top', to ? '60px' : '120px');
      this.$nextTick(() => {
        this.updateMenuWidth();
      });
    },
    'showMenu'() {
      this.$nextTick(() => {
        this.updateMenuWidth();
      });
    }
  },
  created() {
    this.homeRoot.setPosition('top', this.mini ? '60px' : '120px');
    this.initHeadIcon();
  },
  mounted() {
    this.updateMenuWidth();
    this.onresize = () => {
      this.updateMenuWidth();
    };
    window.addEventListener('resize', this.onresize);
  },
  destroyed() {
    window.removeEventListener('resize', this.onresize);
  },
  methods: {
    // 初始化头像，从cookie中获取
    initHeadIcon() {
      const headIcon = dsf.getCookie('photoRelativePath');
      if (headIcon) {
        this.headIcon = decodeURIComponent(headIcon);
      }
    },
    // 头像加载成功时
    headLoadSuccess() {
      this.showIcon = true;
    },
    // 头像加载失败时
    headLoadError() {
      this.showIcon = false;
    },
    // 用户头像下的菜单点击
    handleCommand(index) {
      const item = this.dropdownConfig[+index];
      this.$emit('dropdown-click', item);
    },
    // logo载入成功，重新计算菜单宽度
    logoLoaded() {
      this.$nextTick(() => {
        this.logoLoadSuccess = true;
        this.updateMenuWidth();
      })
    },
    // 更新菜单宽
    updateMenuWidth() {
      if (this.showMenu) {
        let logoWidth = (this.$refs.logoBox?.offsetWidth || 0) + 20;
        let userWidth = this.$refs.headerUser?.offsetWidth || 0;
        this.menuWidth = this.$el.offsetWidth - logoWidth - userWidth - 60;
      } else {
        this.menuWidth = 0;
      }
    }
  }
}
</script>