<template>
  <div class="ds-control ds-section" :class="getCss">
    <div class="ds-section-margin" :style="margin">
      <div slot-name="default" class="ds-section-wrap" :style="getStyle()">
        <slot name="default"></slot>
      </div>
    </div>
  </div>
</template>
<script>
import layout from "_platform/mixins/layout";
export default dsf.component({
  name: "DsfSection",
  ctrlCaption: "页面段落",
  mixins: [layout],
  data() {
    return {
      paddingSize: 12,
      marginLevel: {
        small: 1,
        big: 8,
        medium: 3
      }
    }
  },
  props: {
    slots: {
      type: Array,
      default() {
        return [
          { name: 'default', controls: [] }
        ]
      }
    },
    background: {
      type: Object,
      default() {
        return {}
      }
    },
    shadow: {
      type: Boolean,
      default: false
    },
    showModel: {
      type: String,
      default: 'none'
    },
    marginTop: {
      type: Boolean,
      default: false
    },
    marginBottom: {
      type: Boolean,
      default: false
    },
    marginLeft: {
      type: Boolean,
      default: false
    },
    marginRight: {
      type: Boolean,
      default: false
    },
    paddingTop: {
      type: Boolean,
      default: true
    },
    paddingBottom: {
      type: Boolean,
      default: true
    },
    paddingLeft: {
      type: Boolean,
      default: true
    },
    paddingRight: {
      type: Boolean,
      default: true
    },
    marginSize: {
      type: String,
      default: "small"
    }
  },

  computed: {
    getCss() {
      return [
        ...this.$getClass(),
        this.shadow ? 'shadow' : '',
        this.showModel == 'full' ? 'full' : ''
      ]
    },
    margin(){
      let style={};
      if(this.marginTop){
        style['padding-top']=this.marginLevel[this.marginSize] + 'px'
      }
       if(this.marginLeft){
        style['padding-left']=this.marginLevel[this.marginSize] + 'px'
      }
       if(this.marginRight){
        style['padding-right']=this.marginLevel[this.marginSize] + 'px'
      }
       if(this.marginBottom){
        style['padding-bottom']=this.marginLevel[this.marginSize] + 'px'
      }
      
      return style
    }
  },
  updated() {
    if (this.isDesign) {
      this.$dispatch('design-height-change', this.showModel == 'full' ? "100%" : 'auto');
    }
  },
  methods: {
    getStyle() {
      let paddings = [], margins = [];
      paddings.push((this.paddingTop ? this.paddingSize : 0) + 'px');
      paddings.push((this.paddingRight ? this.paddingSize : 0) + 'px');
      paddings.push((this.paddingBottom ? this.paddingSize : 0) + 'px');
      paddings.push((this.paddingLeft ? this.paddingSize : 0) + 'px');
      // margins.push((this.marginTop ? this.marginLevel[this.marginSize] : 0) + 'px');
      // margins.push((this.marginRight ? this.marginLevel[this.marginSize] : 0) + 'px');
      // margins.push((this.marginBottom ? this.marginLevel[this.marginSize] : 0) + 'px');
      // margins.push((this.marginLeft ? this.marginLevel[this.marginSize] : 0) + 'px');
      return {
        padding: paddings.join(' '),
        // margin: margins.join(' ')
      };
    }
  }
})
</script>