<template>
  <div class="ds-control dsf-schedule" :class="getCss">
    <div class="head-box">
      <div v-if="!titleHide" class="title">{{ title }}</div>
      <div class="bottom">
        <div class="left">
          <div class="periods-on-off">
            <span>开启时段：</span>
            <el-checkbox-group v-model="periodsOns">
              <el-checkbox
                v-for="(key, label) in periodsOn"
                :key="label"
                :label="label"
              ></el-checkbox>
            </el-checkbox-group>
          </div>
        </div>
        <div class="inner">
          <div class="select-time-box">
            <div class="icon-but back-time" title="上月">
              <DsfIcon name="shuangjiantouzuo" />
            </div>
            <div class="time">
              <el-date-picker
                v-model="dateGirdData.dateRange"
                size="small"
                type="daterange"
                range-separator="至"
                :clearable="false"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
              />
              <div class="show-time" @click="selectTimeClick">
                {{ dateGirdData.dateRange[0] | dateFormat("yyyy-mm-dd") }} 至
                {{ dateGirdData.dateRange[1] | dateFormat("yyyy-mm-dd") }}
              </div>
            </div>
            <div class="icon-but next-time" title="下月">
              <DsfIcon name="shuangjiantouyou" />
            </div>
          </div>
        </div>
        <div class="right" slot-name="head-right">
          <slot name="head-right"></slot>
        </div>
      </div>
    </div>
    <div class="main-box">
      <DsfDragRange
        class="main-box-drag-range"
        @scroll.native="mainScrollChange"
      >
        <div
          v-if="tabsHide"
          class="tabs-box"
          :style="{ top: `${mainScroll.top}px` }"
        >
          <template
            v-if="
              tabsOpts.tabs &&
                tabsOpts.tabs.length &&
                tabsOpts.ac &&
                tabsOpts.ac.name
            "
          >
            <div class="tabs-head">
              <div
                v-for="(tab, index) in tabsOpts.tabs"
                :key="index"
                class="tab"
                :class="{
                  'tab-ac': tabsOpts.ac.name == tab.name,
                }"
                @click="tabsClick(tab)"
              >
                {{ tab.title }}
              </div>
            </div>
            <div v-if="tabsOpts.ac" class="tabs-operate">
              <div v-if="tabsOpts.ac" class="tab-search">
                <el-input
                  v-model="tabsOpts.ac.search"
                  size="small "
                  placeholder="请输入内容"
                >
                  <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
              </div>
            </div>
            <div class="tabs-main">
              <div
                v-for="(item, index) in tabsOpts.list[tabsOpts.ac.name] || []"
                :key="index"
                class="list-row-box"
              >
                <DsfDrag helper=".list-row-default">
                  <div class="list-row-default">
                    <div class="title">
                      {{ item[`Lesson`] || "无" }}
                    </div>
                    <div class="info">
                      <span>{{ item[`jxxs_text`] || "无" }}</span>
                      |
                      <span>{{ item[`teachername`] || "无" }}</span>
                    </div>
                  </div>
                </DsfDrag>
              </div>
            </div>
          </template>
        </div>
        <div class="date-grid-box">
          <div class="week-head" :style="{ top: `${mainScroll.top}px` }">
            <div
              v-for="(week, index) in weekHead"
              :key="index"
              class="item-box"
              :class="week.class"
              :is-range="true"
              :style="week.style"
            >
              {{ week.value }}
            </div>
          </div>
          <div class="date-box">
            <div
              v-for="(dateRow, rIndex) in dateRows"
              :key="rIndex"
              class="date-row-box"
            >
              <div class="date-head">
                <div
                  v-for="(date, dIndex) in dateRow"
                  :key="dIndex"
                  class="item-box"
                  :class="weekHead[dIndex].class"
                  :is-range="isRange(date)"
                  :style="weekHead[dIndex].style"
                >
                  {{ date | dateHeadBuild }}
                </div>
              </div>
              <div
                v-for="(period, pIndex) in periods"
                :key="pIndex"
                class="date-period"
              >
                <div
                  v-for="(date, dIndex) in dateRow"
                  :key="dIndex"
                  ref="droppable"
                  class="item-box"
                  :is-range="!dIndex || isRange(date)"
                  :style="weekHead[dIndex].style"
                >
                  <span v-if="!dIndex" class="item-box-period">
                    {{ pIndex }}
                  </span>
                  <template v-else>
                    <DsfDrop
                      class="main-box-drop"
                      :disabled="!isRange(date)"
                      @drop="
                        (e, ui) => dropHooks(e, ui, { period: pIndex, date })
                      "
                    >
                      <div class="course-boxs">
                        <template v-if="dateGirdData.list[date.dateStr]">
                          <div
                            v-for="(course, cIndex) in dateGirdData.list[
                              date.dateStr
                            ] || []"
                            :key="cIndex"
                            class="course-box"
                          >
                            <div class="course-box-default">
                              <div class="cbd-time">
                                {{ course._time_range }}
                              </div>
                              <div class="cbd-sk-type" :title="course._sk_type">
                                {{ course._sk_type | textEllipsis(10) }}
                              </div>
                              <div class="cbd-kc-name" :title="course._kc_name">
                                {{ course._kc_name | textEllipsis }}
                              </div>
                              <div
                                class="cbd-skjs-name"
                                :title="course._skjs_name"
                              >
                                {{ course._skjs_name | textEllipsis(10) }}
                              </div>
                              <div
                                class="cbd-skdd-name"
                                :title="course._skdd_name"
                              >
                                {{ course._skdd_name | textEllipsis(10) }}
                              </div>
                              <div v-if="isRange(date)" class="cbd-operate">
                                <DsfDrag
                                  class="cbdo-item-drag"
                                  helper=".course-box-default"
                                  title="拖动"
                                >
                                  <DsfIcon name="yidong" />
                                </DsfDrag>
                                <div
                                  class="cbdo-item"
                                  title="编辑"
                                  @click="
                                    operateCourse(
                                      'edit',
                                      { period: pIndex, date },
                                      course
                                    )
                                  "
                                >
                                  <DsfIcon name="tianxie" />
                                </div>
                                <div
                                  class="cbdo-item"
                                  title="添加"
                                  @click="
                                    operateCourse(
                                      'add',
                                      { period: pIndex, date },
                                      course
                                    )
                                  "
                                >
                                  <DsfIcon name="tianjiawenjian" />
                                </div>
                                <div
                                  class="cbdo-item"
                                  title="删除"
                                  @click="
                                    operateCourse(
                                      'remove',
                                      { period: pIndex, date },
                                      course
                                    )
                                  "
                                >
                                  <DsfIcon name="shanchu" />
                                </div>
                              </div>
                            </div>
                          </div>
                        </template>
                      </div>
                    </DsfDrop>
                  </template>
                </div>
              </div>
            </div>
          </div>
        </div>
      </DsfDragRange>
    </div>
    <div class="footer-box"></div>
  </div>
</template>
<script>
import control from "_platform/mixins/control";
import DsfDragRange from "./dragRange.vue";
import DsfDrag from "./drag.vue";
import DsfDrop from "./drop.vue";
import listJson from "./list.json";
import scheduleMixin from "./mixins";
export default dsf.component({
  name: "DsfSchedule",
  ctrlCaption: "排课组件",
  mixins: [control, scheduleMixin],
  components: {
    DsfDragRange,
    DsfDrag,
    DsfDrop,
  },
  provide() {
    return {};
  },
  props: {
    slots: {
      type: Array,
      default() {
        return [
          {
            name: "head-right",
            controls: [],
          },
        ];
      },
    },
    // 标签列表设置
    setTabsOpts: {
      type: Array,
      default() {
        return [];
      },
    },
    // 标题
    title: {
      type: String,
      default() {
        return "标题";
      },
    },
    // 标题隐藏
    titleHide: {
      type: Boolean,
      default() {
        return false;
      },
    },
    // 时间范围
    timeRange: {
      type: String,
      default() {
        return "2020-11-26 - 2020-11-06";
      },
    },
    // 是否显示 tabs
    tabsHide: {
      type: Boolean,
      default() {
        return true;
      },
    },
    // 标签列表数据前缀
    tabsOptsPrefix: {
      type: String,
      default() {
        return "teas_jwgl_pkgl_pkjk_pdxzkc";
      },
    },
    // 右侧表格数据获取接口
    dateTableUrl: {
      type: String,
      default() {
        return "";
      },
    },
  },
  computed: {
    // 星期头部数据
    weekHead() {
      let { headOpt, weekHead = [] } = this.dateGirdData;
      return weekHead.map((week) => {
        let { width, align } = week;
        if (!align) {
          align = headOpt.align || "center";
        }
        let style = {
          flex: 1,
          textAlign: align,
        };
        if (width) {
          style.maxWidth = width;
        }
        week.style = style;
        return week;
      });
    },
    // 时间数据
    dateRows() {
      let { headOpt, dateRange } = this.dateGirdData;
      let now = new Date();
      let nowArry = dsf.date.getDateArray(now, ["YYYY", "MM"]);
      let monthS = dateRange[0];
      let monthE = dateRange[1];
      let rows = [];
      if (!monthS || !monthE) {
        return rows;
      }
      let intervalDay = dsf.date.getDateIntervalDay(monthS, monthE);
      now = monthS;
      nowArry = dsf.date.getDateArray(monthS).slice(0, 3);
      let monthEBound = monthE.getTime();
      while (now.getTime() <= monthEBound + !!intervalDay * 604800000) {
        let { list } = dsf.date.getMonthWeekDate(...nowArry);
        list = list.map(({ date }) => {
          return {
            isDate: true,
            date,
            dateStr: dsf.date.format(date, "yyyy-mm-dd"),
          };
        });
        list.unshift({
          isDate: false,
          text: "",
        });
        rows.push(list);
        now = new Date(now.getTime() + 604800000);
        nowArry = dsf.date.getDateArray(now).slice(0, 3);
      }
      return rows;
    },
    // 时段数据
    periods() {
      let { classData, periodsData, periodsOns, periodsOn } = this;
      if (classData && classData.xq && periodsData && periodsData.length) {
        let periods = {};
        periodsData.map((period) => {
          let period_text = period.wb.text;
          if (
            Object.keys(periodsOn).some((key) => key == period_text) &&
            !periodsOns.some((key) => key == period_text)
          ) {
            return;
          }
          if (period.xq.value == classData.xq.value) {
            periods[period_text] = period;
          }
        });
        return periods;
      }
      return {};
    },
    // 开启时段数据
    periodsOn() {
      let { periodsHide } = this;
      if (!periodsHide) {
        return [];
      }
      let periods = {};
      periodsHide.split(",").map((item) => {
        let opt = item.split("&");
        periods[opt[0]] = opt[1];
      });
      return periods;
    },
  },
  data() {
    return {
      // 日期表格
      dateGirdData: {
        // 每行的头部设置
        headOpt: {
          // 头部对齐方式
          align: "center",
        },
        // 星期头部数据
        weekHead: [
          {
            width: "45px",
            flex: true,
            value: "午别",
          },
          {
            value: "周一",
          },
          {
            value: "周二",
          },
          {
            value: "周三",
          },
          {
            value: "周四",
          },
          {
            value: "周五",
          },
          {
            class: "is-gala",
            value: "周六",
          },
          {
            class: "is-gala",
            value: "周日",
          },
        ],
        dateRange: [],
        list: {},
      },
      // tbs 配置
      tabsOpts: {
        ac: null,
        tabs: [],
        list: {},
      },
      periodsOpts: {
        ons: [],
      },
      periodsOns: [],
      classData: {},
      periodsData: [],
      mainScroll: {
        left: 0,
        top: 0,
      },
    };
  },
  watch: {
    setTabsOpts: {
      immediate: true,
      deep: true,
      handler(value) {
        let tabsOpts = {
          ac: null,
          tabs: [],
          list: {},
        };
        tabsOpts.tabs = value.map((opt) => {
          if (!tabsOpts.ac && opt.isCheckd) {
            tabsOpts.ac = opt;
          }
          return {
            ...opt,
            search: "",
          };
        });
        this.tabsOpts = tabsOpts;
        this.tabsClick(tabsOpts.ac);
      },
    },
  },
  methods: {
    // 判断是否在有效范围
    isRange({ isDate, date }) {
      let { dateRange } = this.dateGirdData;
      return isDate && date >= dateRange[0] && dateRange[1] >= date;
    },
    // tabs 点击项
    tabsClick(tab) {
      if (!tab) return;
      this.tabsOpts.ac = tab;
      this.tabsOpts.list[tab.name] = [];
      this.getTabsListData(tab.url, (result) => {
        this.tabsOpts.list[tab.name] = this.cleanTabsListDataHooks(
          tab,
          result || []
        );
      });
    },
    // 获取班次信息
    getClassData(url, result) {
      if (url) {
        let url = dsf.express.eval(tab.url);
        this.$http.get(url, {}).done().error().always();
      } else {
        result(listJson.classData || {});
      }
    },
    /**
     * 钩子 --> 获取到班次信息后进行清洗
     * @param tab 选中的tab对象
     * @param res 接口返回的数据集合
     * @return {Array} result 返回数组对象
     */
    cleanClassDataHooks(res) {
      let result = null;
      if (res.success) {
        let data = res.data;
        result = this.cleanDataKeyPrefix(data);
      }
      return result;
    },
    // 获取 tabs 的没项数据
    getTabsListData(url, result) {
      if (url) {
        let url = dsf.express.eval(tab.url);
        this.$http.get(url, {}).done().error().always();
      } else {
        result(listJson.tabsList || {});
      }
    },
    /**
     * 钩子 --> tabs 标签列表数据返回后根据tab进行清洗
     * @param tab 选中的tab对象
     * @param res 接口返回的数据集合
     * @return {Array} result 返回数组对象
     */
    cleanTabsListDataHooks(tab, res) {
      if (res.success) {
        let tabsOptsPrefix = this.tabsOptsPrefix + ".";
        return this.cleanDataKeyPrefix(res.data || []);
      }
      return [];
    },
    /**
     * 钩子 --> 拖拽被放下钩子
     */
    dropHooks(e, ui, position) {
      console.log(position);
    },
    // 获取 日期表格数据
    getGridData() {
      let url = dsf.express.eval(this.dateTableUrl);
      if (url) {
        thiss.$http
          .get(url, {})
          .done(() => {})
          .error(() => {})
          .always();
      } else {
        this.dateGirdData.list =
          this.cleanGridDataDataHooks(listJson.gridList) || {};
      }
    },
    /**
     * 钩子 --> 清洗右侧表格数据
     * @param res 接口返回数据
     * @return {Array} result
     */
    cleanGridDataDataHooks(res) {
      let result = {};
      if (res.success) {
        let data = res.data || [];
        data.map((item) => {
          let obj = this.cleanDataKeyPrefix(item || {});
          if (!result[obj.rq]) {
            result[obj.rq] = [];
          }
          obj._time_range = "";
          if (obj.sksj && obj.sksj.sdate)
            obj._time_range += obj.sksj.sdate.replace(/^.* /, "");
          obj._time_range += " 至 ";
          if (obj.sksj && obj.sksj.sdate)
            obj._time_range += obj.sksj.edate.replace(/^.* /, "");
          if (obj.kc && obj.kc.text) obj._kc_name = obj.kc.text;
          if (obj.skdd && obj.skdd.text) obj._skdd_name = obj.skdd.text;
          if (obj.skjs && obj.skjs.length) {
            let skjss = obj.skjs.map((item) => item.text);
            obj._skjs_name = skjss.join("|");
          }
          if (obj.jxxs && obj.jxxs) obj._sk_type = obj.jxxs;
          result[obj.rq].push(obj);
        });
      }
      return result;
    },
    // 主体滚动事件
    mainScrollChange(e) {
      this.mainScroll.top = $(e.target).scrollTop();
    },
    // 时间选择代理
    selectTimeClick() {
      if (this.isDesign) return;
      let timeDom = document.querySelectorAll(
        ".select-time-box .time .el-date-editor"
      )[0];
      timeDom && timeDom.click();
    },
    /**
     * 课程操作
     * @param type 类型 添加 add | 编辑 edit | 删除 remove
     * @param position 位置
     * @param course 课程
     */
    operateCourse(type, position, course) {
      let { removeCourseHooks, editCourseHooks, addCourseHooks } = this;
      switch (type) {
        case "add":
          if (addCourseHooks) {
            addCourseHooks(course);
          }
          // else {
          // }
          break;
        case "edit":
          if (editCourseHooks) {
            editCourseHooks(course);
          }
          // else {
          // }
          break;
        case "remove":
          if (removeCourseHooks) {
            removeCourseHooks(course);
          } else {
            dsf.layer
              .confirm({
                title: "提示",
                message: "确认是否要删除？",
              })
              .then(() => {})
              .catch(() => {});
          }
          break;
      }
    },
  },
  created() {
    let now = new Date();
    let nowArry = dsf.date.getDateArray(now, ["YYYY", "MM"]);
    this.dateGirdData.dateRange = [
      new Date(nowArry[0], nowArry[1], 1),
      new Date(nowArry[0], nowArry[1] + 1, 0),
    ];
    this.getPeriodsData("", (result) => {
      this.periodsData = this.cleanPeriodsDataHooks(result || {});
    });
    this.getClassData("", (result) => {
      this.classData = this.cleanClassDataHooks(result || {});
      let periodsOns = [];
      for (const key in this.periodsOn) {
        if (this.classData[this.periodsOn[key]].value == "1") {
          periodsOns.push(key);
        }
      }
      this.periodsOns = periodsOns;
    });
  },
  beforeMount() {},
  mounted() {
    if (!this.isDesign) {
      this.getGridData();
    }
  },
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  design: {
    isMask: false,
  },
});
</script>
