<template>
  <div class="ds-control ds-homechart" ref="container" :style="containerStyle"></div>
</template>
<script>
import control from '_platform/mixins/control';
import echarts from 'echarts/lib/echarts';
import 'echarts/lib/chart/bar';
import 'echarts/lib/chart/line';
import 'echarts/lib/chart/pie';
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/legend';

const exampleLineData = [{
  name: '星期一',
  value: 15
},{
  name: '星期二',
  value: 25
},{
  name: '星期三',
  value: 5
}];

export default dsf.component({
  name: 'DsfHomeChart',
  ctrlCaption: '图表',
  mixins: [control],
  props: {
    chartType: {
      type: String,
      default: 'line'
    },
    api: {
      type: String,
      default: ''
    },
    height: {
      type: [String, Number],
      default: '100%2'
    },
    colors: {
      type: String,
      default: '#FF8F1F,#09b094,#015648,#633248,#40001d,#f09913,#6b4100,#c2352c,#6f0700,#1e77b1,#003354'
    },
    tipAsisColor: {
      type: String,
      default: 'rgba(205,9,17,.7)'
    },
    grid: {
      type: String,
      default: '40,30,40,30'    
    },
    radius: {
      type: String,
      default: '80%'
    },
    circleRadius: {
      type: String,
      default: '50%,80%'
    },
    smooth: {
      type: Boolean,
      default: true,
    },
    showLabelLine: {
      type: Boolean,
      default: false,
    },
    tipBackground: {
      type: String,
      default: 'rgba(255,255,255,.8)'
    },
    tipBorder: {
      type: String,
      default: '#6ca6cd'
    },
    tipColor: {
      type: String,
      default: '#333'
    },
    barWidth: {
      type: [String, Number],
      default: 30
    },
    showXLine: {
      type: Boolean,
      default: false,
    },
    showYLine: {
      type: Boolean,
      default: false,
    },
    showXArea: {
      type: Boolean,
      default: false,
    },
    showYArea: {
      type: Boolean,
      default: false,
    },
    axisXLineColor: {
      type: String,
      default: '#999'
    },
    axisXLabelColor: {
      type: String,
      default: '#999'
    },
    axisYLineColor: {
      type: String,
      default: '#999'
    },
    axisYLabelColor: {
      type: String,
      default: '#999'
    },
    axisXTick: {
      type: Boolean,
      default: false,      
    },
    axisYTick: {
      type: Boolean,
      default: false,      
    },
    showSeriesLable: {
      type: Boolean,
      default: false,      
    },  
    showEmphasisSeriesLable: {
      type: Boolean,
      default: false,
    },  
    seriesLabelPosition: {
      type: String,
      default: 'top'
    },
    seriesLabelColor: {
      type: String,
      default: '#333'
    },
    seriesLabelFontSize: {
      type: [String, Number],
      default: 16,
    },
    seriesName: {
      type: String,
      default: ''
    },
    tooltipFormatter: {
      type: String,
      default: null
    } 
  },

  data () {
    return {
      $chart: null,
      data: [],
    };
  },

  computed: {
    containerStyle() {
      return {
        minHeight: '150px',
        height: dsf.isNumber(this.height) ? `${this.height}px` : this.height,
        width: '100%'
      }
    },
  },

  mounted() {
    if(!this.$chart) {
      this.$chart = echarts.init(this.$refs['container']);
      this.$chart.setOption(this.getChartOptions());
      window.addEventListener('resize', this.onResize);
    }
  },

  created() {
    this.api && this.loadData();
    setTimeout(() => {
      this.data = exampleLineData;
      this.rerender()
    }, 1000);
  },

  destroyed() {
    window.removeEventListener('resize', this.onResize);
  },

  methods: {
    onResize() {
      this.$chart?.resize?.();
    },
    clear() {
      this.$chart.clear();
      this.rerender();
    },
    getDefaultOptions() {
      const color = this.colors.split(',');
      const [ top, right, bottom, left ] = this.grid.split(',');
      const grid = { top, right, bottom, left };
      const seriesLabelPosition = this.seriesLabelPosition.includes(',') ? this.seriesLabelPosition.split(',') : this.seriesLabelPosition;
      let radius = '';
      if(this.chartType === 'circle') {
        radius = this.circleRadius.split(',');
      } else if(this.chartType === 'pie') {
        radius = this.radius;
      }
      const option = {
        grid,
        color,
        tooltip: {
          trigger: ['pie','circle'].includes(this.chartType) ? 'item' : 'axis',
          backgroundColor: this.tipBackground,
          borderColor: this.tipBorder,
          borderWidth: 1,
          padding: [5, 10],
          textStyle: {
            color: this.tipColor
          },
          formatter: this.tooltipFormatter ? this.tooltipFormatter : null,
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: this.tipAsisColor,
            }
          }           
        },
        series: [{
          name: this.seriesName,
          data: ['pie', 'circle'].includes(this.chartType) ? this.data : this.data.map(it => it.value),
          type: this.chartType === 'circle' ? 'pie': this.chartType,
          symbolSize: 2,
          smooth: this.smooth,
          barWidth: this.barWidth,
          radius,
          labelLine: {
            show: this.showLabelLine,
          },
          label: {
            normal: {
              show: this.showSeriesLable,
              textStyle: {
                color: this.seriesLabelColor,
                fontSize: this.seriesLabelFontSize,
              },
              position: seriesLabelPosition,
            },
            emphasis: {
              show: this.showEmphasisSeriesLable
            }
          },           
        }]
      }
      if(['line', 'bar'].includes(this.chartType)) {
        Object.assign(option, {
          xAxis: {
            type: 'category',
            splitLine: {
              show: this.showXLine,
            },
            splitArea: {
              show: this.showXArea
            },
            axisLine: {
              lineStyle: {
                color: this.axisXLineColor,
              }
            },
            axisLabel: {
              color: this.axisXLabelColor,
            },
            axisTick: {
              show: this.axisXTick,
            },       
            data: this.data.map(it => it.name),            
          },
          yAxis: {
            type: 'value',
            splitLine: {
              show: this.showYLine,
            },
            splitArea: {
              show: this.showYArea
            },
            axisLine: {
              lineStyle: {
                color: this.axisYLineColor,
              }
            },
            axisLabel: {
              color: this.axisYLabelColor,
            },     
            axisTick: {
              show: this.axisYTick,
            }            
          },          
        });
      }
      return option;
    },
    getChartOptions() {
      return this.createOptions?.() || this.getDefaultOptions();
    },
    rerender() {
      this.$chart.setOption(this.getChartOptions());
    },
    loadData() {
      const loadding = dsf.layer.loading();
      this.$http.get(dsf.url.getWebPath(this.api)).done((res) => {
        if (res.success) {
          this.data = this.formatData ? this.formatData(res.data) : res.data;
          this.rerender();
        }
      })
      .error(function (err) {
        dsf.layer.message(err.message || '出错了，请重试', false);
      })
      .always(function () {
        dsf.layer.closeLoading(loadding)
      });      
    }   
  },
});
</script>