<template>
  <div class="ds-control ds-form-item ds-textbox" :class="getCss">
    <template v-if="simple&&readOnly&&!isDesign">{{ value }}</template>
    <template v-else>
      <label v-if="showLabel" class="ds-form-label" :style="getLabelWidthStyle()">
        {{ label }}
        <i v-if="dataCapion" class="iconfont icon-bangzhuzhongxin ds-control-helper" @click="showHelper"></i>
      </label>
      <div class="ds-form-block" :style="getFormItemBlockMarginLeft()">
        <el-input v-if="!readOnly" v-model="inputValue" :placeholder="placeholder" :rows="rows" type="textarea" :disabled="disabled" @blur="inputBlurHandler" />
        <!--只读状态只显示div-->
        <div v-else-if="readOnly" class="ds-form-readonly">
          <DsfTextProxy v-model="value" v-bind="_self.$props" :show-label="false"></DsfTextProxy>
        </div>
        <slot name="error">
          <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
        </slot>
      </div>
    </template>
  </div>
</template>

<script>
import formControl from "../../../mixins/formControl";

export default dsf.component({
  name: "DsfTextarea",
  mixins: [formControl],
  ctrlCaption: "多行文本",
  props: {
    simple: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: "请输入"
    },
    rows: {
      type: Number,
      default: 4
    },
    metadata: {
      type: Object,
      default() {
        let mdata = dsf.metadata.get("text-meta-data");
        dsf.mix(mdata.valueAttributes[0], { length: 1000 });
        return mdata;
      }
    }

  },
  computed: {
    inputValue: {
      get() {
        return this.value
      },
      set(v) {
        this.emitValueChange(v);
      }
    }
  },
  methods: {
    inputBlurHandler() {
      this.$dispatch("blur");
      this.$nextTick(() => {
        this.emitFormValidate(this.value);
      })
    }
  }
})
</script>