const batchSend = {
  flowSend(res, { targetButton, _self }, Objects) {
    const { line, completed } = targetButton;
    const flowInfo = this.$vm.flows.flowInfo;
    if (!flowInfo) {
      dsf.layer.message("请先配置流程信息控件的'选择流程'", false);
      return;
    }
    // 流程发送按钮
    let id = flowInfo.chooseFlow.dsfa_rm_id;
    if (id) {
      // 增加 sExInfos
      let sExInfos = Object.assign({}, dsf.removePoint(this.$vm.$viewData), this.$route.query);
      // 开始发送
      let _this = this,
        url = "wfr/batchStartSend",
        param = {
          sObjects: JSON.stringify(Objects),
          sWFID: id,
          sCurNodeID: "",
          sExInfos: JSON.stringify(sExInfos),
        };

      //继续发送
      if (this.$route.query.sPID || _self.$vm.$dialog) {
        url = "wfr/batchSend";
        let sObjects = [];
        Objects.forEach(obj => {
          sObjects.push({
            PID: _self.$vm.$dialog ? obj.sPID : this.$route.query.sPID,
            LinkID: _self.$vm.$dialog ? obj.sCurLinkID : this.$route.query.sCurLinkID || "",
          });
        });
        param.sObjects = JSON.stringify(sObjects);
      }
      if (this.$vm && this.$vm.flows && this.$vm.flows.disposeIdea) {
        let disposeIdea = this.$vm.flows.disposeIdea;
        // 发送时有处理意见
        param.sOpinion = {
          Content: disposeIdea.inputValue,
          Attachments: disposeIdea.multipleFiles,
        };
        param.sOpinion = JSON.stringify(param.sOpinion);
      }
      if (line) {
        param.sLineIDs = line.LineID;
      }

      let config = {
        param: param,
        url: url,
      };

      this.$http
        .post(config.url, config.param)
        .then((response) => {
          this.handler(response, completed);
        })
        .catch((response) => {
          dsf.layer.message("发送异常", false);
          this._RequestID = null;
        });
    } else {
      dsf.layer.message("请先配置流程信息控件的'选择流程'", false);
    }
  },
  handler(response, completed) {
    if (response.data.state == 20000) {
      let data = response.data.data;
      this._RequestID = data.RequestID;
      if (
        data.Code == 20000 ||
        data.Code == 20001 ||
        data.Code == 20003 ||
        data.Code == 20004 ||
        data.Code == 20005
      ) {
        // 发送成功 || 发送到汇总节点 || 发送到汇总环节并激活汇总环节 || 办结当前节点 || 办结当前节点，并办结整个流程
        if (this.$vm && this.$vm.$dialog) {
          this.$vm.$dialog.openerVm.reloadData();
        } else if (this.$vm.$openerVm) {
          this.$vm.$openerVm && this.$vm.$openerVm.reloadData && this.$vm.$openerVm.reloadData();
        } else {
          this.$vm.openerVm && this.$vm.openerVm.reloadData && this.$vm.openerVm.reloadData();
        }
        if (this.flowSendOverEvent) {
          this.$dispatch(this.flowSendOverEvent);
        } else {
          this.directlySend(data, completed);
        }
      } else if (data.Code == 81000) {
        // 或选择
        this.chooseSendNode(data);
      } else if (data.Code == 82000) {
        // 与选择
        this.chooseSendTree(data);
      } else {
        let msg = null,
          code = data.Code;
        switch (code) {
          case 20006:
            msg = "流程已被销毁";
            break;
          case 20007:
            msg = "流程已被强制办结";
            break;
          case 91000:
            msg = "无后续可发送节点";
            break;
          case 92000:
            msg = "无可选的发送对象";
            break;
          case 92001:
            msg = "该环节只允许发送给一人，但是必选项超过一个";
            break;
          case 92002:
            msg = "未找到继承环节";
            break;
          case 92003:
            msg = "对象超出可发送范围";
            break;
          case 92004:
            msg = "路径超出可发送范围";
            break;
          case 93000:
            msg = "该事项已被处理";
            break;
          case 93001:
            msg = "保存流转情况信息失败";
            break;
          case 93002:
            msg = "保存流转环节信息失败";
            break;
          case 93003:
            msg = "该文件已被其他人签收";
            break;
          case 94001:
            msg = "与分支不能设置办结节点";
            break;
          case 95001:
            msg = "没有可回收的环节";
            break;
          case 95002:
            msg = "回收环节已接收或已处理，无法回收";
            break;
          case 95003:
            msg = "未填写退回原因";
            break;
          case 99000:
            msg = "获取数据锁失败";
            break;
        }
        dsf.layer.message(msg || "未知流程类型发送", false);
      }
    } else {
      this._RequestID = null;
      dsf.layer.message("发送异常", false);
    }
    batchSend.isDisabled = false;
  },
  directlySend(data, completed) {
    let list = [];
    if (data.Code == 20000) {
      if (data.BatchResults) {
        dsf.layer.message("发送成功");
        if (this.$vm.$dialog) {
          this.$vm.$dialog.openerVm.reloadData(true);
          this.$vm.$dialog.close();
        } else if (window.opener) {
          window.close();
        }
        return;
      }
      const Links = data.Links;
      Links.forEach((line, index) => {
        if (line.Status == 0 && line.LinkUser) {
          let linkUser = line.LinkUser;
          let type = line.LinkUser.Type;
          if (type == 1) {
            // 直接选人
            list.push(linkUser.UserName);
          } else if (type == 2) {
            let GlobalDeptName = linkUser.GlobalDeptName.split(",");
            let DeptName = GlobalDeptName.length >= 3 ? GlobalDeptName[2] : linkUser.DeptName;
            // 部门+角色
            list.push(`${DeptName}-${linkUser.RoleName}`);
          } else if (type == 3) {
            // 岗位
            list.push(linkUser.JobName);
          } else if (type == 4) {
            // 人+关系
            list.push(`${linkUser.UserName}-${linkUser.RelationName}`);
          }
        }
      });
      dsf.layer.openDialog({
        title: "流程已发送到以下人员：",
        width: "300px",
        height: "100px",
        content: "SendFlowDialog",
        params: {
          list: _.uniq(list),
          showCountDown: !this.$vm.$dialog,
          countDownTime: this.$vm.flows.flowInfo?.countDown
        },
        btns: [
          {
            text: "确定",
            class: "sendFlow",
            handler: (component) => {
              if (this.$vm.$dialog) {
                this.$vm.$dialog.openerVm.reloadData(true);
                this.$vm.$dialog.close();
              } else if (window.opener) {
                window.close();
              }
              return true;
            },
          },
        ],
      });
    } else {
      let msg = "发送成功",
        code = data.Code;
      switch (code) {
        case 20004:
          msg = completed || "文件已办结";
          break;
        case 20005:
          msg = completed || "文件已办结";
          break;
      }
      dsf.layer.openDialog({
        title: "提示",
        width: "300px",
        height: "100px",
        content: "SendFlowDialog",
        params: {
          msg: msg,
          countDownTime: this.$vm.flows.flowInfo?.countDown
        },
        btns: [
          {
            text: "确定",
            class: "sendFlow",
            handler: (component) => {
              if (this.$vm.$dialog) {
                this.$vm.$dialog.openerVm.reloadData(true);
                this.$vm.$dialog.close();
              } else if (window.opener) {
                window.close();
              }
              return true;
            },
          },
        ],
      });
    }
  },
  chooseSendNode(data) {
    dsf.layer.openDialog({
      title: "请选择要发送的环节：",
      width: "300px",
      height: "180px",
      overflow: "hidden",
      content: "ChooseSendNode",
      params: {
        lines: data.SendParams.Lines,
        callback: this.handler.bind(this),
        requestID: this._RequestID,
        type: "next",
        isBatch: true
      },
      btns: [
        {
          text: "确定",
          class: "hide choose-send-node",
          handler: (component) => {
            if (component.validate()) {
              const line = component.yes();
              let param = {
                sRequestID: this._RequestID,
                sLineID: line.LineID,
              };
              this.$http.post("wfr/batchSendToLine", param).then((res) => {
                if (res.data.state == 20000) {
                  this.handler(res);
                }
              });
              return true;
            }
            return false;
          },
        },
        {
          class: "close-send-node",
          text: "取消",
        },
      ],
    });
  },
  chooseSendTree(data) {
    dsf.layer.openDialog({
      title: "请选择办理人",
      width: "900px",
      height: "420px",
      overflow: "hidden",
      content: "ChooseSendTree",
      params: {
        lines: data.SendParams.Lines,
      },
      btns: [
        {
          text: "确定",
          handler: (component) => {
            if (component.validate()) {
              const result = component.yes();
              let param = {
                sRequestID: this._RequestID,
                sReceivers: result,
              };
              this.$http.post("wfr/batchSendToReceivers", param).then((res) => {
                if (res.data.state == 20000) {
                  this.handler(res);
                }
              });
              const closebtn = document.querySelector(".close-send-node");
              closebtn && closebtn.click();
              return true;
            }
            return false;
          },
        },
        {
          text: "取消",
        },
      ],
    });
  },
  isDisabled: false
}

const system_batch_flow_send = async function (args) {

  if (batchSend.isDisabled) {
    return;
  }
  batchSend.isDisabled = true;
  let form = args._self.$form;
  if (form) {
    let options = {
      isSilent: true,
      isRefresh: false,
      savedClose: false,
      successMsg: "",
      errorMsg: ""
    }
    const res = await form.formSave(options);
    if (res && res.data) {
      let Objects = [];
      if (args._self.$vm.$dialog) {
        const data = args._self.$vm.$dialog.dialogArgs && args._self.$vm.$dialog.dialogArgs.data || [];
        Objects = data;
      } else {
        let [tableNames, ObjectIDs, ObjectNames] = args.params;
        tableNames = tableNames.split(',');
        ObjectIDs = ObjectIDs.split(',');
        ObjectNames = ObjectNames.split(',');
        tableNames.forEach((table, index) => {
          res.data[table].forEach(item => {
            Objects.push({
              ObjectID: item[ObjectIDs[index] || ObjectIDs[0]],
              ObjectName: item[ObjectNames[index] || ObjectNames[0]],
            });
          });
        });
      }
      Object.assign(args._self, batchSend);
      args._self.flowSend(res, args, Objects);
    } else {
      batchSend.isDisabled = false;
    }
  }
}

export default system_batch_flow_send;