
import Vue from 'vue';
import VueRouter from "vue-router";
import platformPCRouter from "_platform/pc/router";
import Login from '../views/Login';
import ErrorView from '../views/Error';

Vue.use(VueRouter);

const routes = [
  //注入平台路由
  ...platformPCRouter,
  {
    path: "/pc/login",
    meta: {
      title: "登录",
      needLogin: false
    },
    component: Login
  },
  //强制匹配404页面
  {
    path: "*",
    component: ErrorView
  }
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes
});

dsf.init.initRouter(router, 'pc');

export default router;