<script>
import layout from "../../../mixins/layout";
// import DsfSubTableCellContent from './subTableCellContent';
export default dsf.component({
  name: "DsfDataGridColumn",
  inheritAttrs: false,
  mixins: [layout],
  ctrlCaption: "表格列",
  props: {
    column: {
      type: Object,
      default() {
        return {
          label: "",
          children: []
        };
      }
    },
    columns: {
      type: Array,
      default() {
        return [];
      }
    },
    sortable: {
      type: [Boolean, String],
      default: false
    }
  },
  inject: {
    $list: {
      default: null
    }
  },
  data() {
    return {};
  },
  created() {},
  computed: {
    ownerTable() {
      let parent = this.elTable;
      while (parent.$options.name != "DsfDataGridTable") {
        parent = parent.$parent;
      }
      return parent;
    },
    elTable() {
      return this.$parent;
    },
    realColumns() {
      return this.ownerTable.realColumns;
    },
    filterColumns() {
      return this.ownerTable.filterColumns;
    }
  },
  destroyed() {
    // console.log("列被销毁")
  },
  beforeMount() {},
  mounted() {},
  methods: {
    percentageToPx(width, total, isMark) {
      let realWidth = width;
      if (dsf.type(realWidth) == "string" && realWidth && realWidth.endsWith("%")) {
        let w = parseFloat(realWidth.replace("%", "")) / 100;
        let tw = isMark ? this.getContentWidth(total) : total;
        realWidth = parseFloat(total) * w;
      }
      return realWidth;
    },
    getContentWidth(total) {
      let indexColumnWidth = this.getIndexWidth();
      let checkColumnWidth = this.getCheckColumnWidth();
      let operateColumnWidth = this.ownerTable.operateColumn.show ? this.ownerTable.operateColumn.width : 0;
      let tw = total - indexColumnWidth - checkColumnWidth - operateColumnWidth;
      return tw;
    },
    getIndexWidth() {
      if (this.ownerTable.indexColumn.show) {
        if (this.ownerTable.elTable) {
          let indexColumnWidth = null,
            index = 1;
          if (!this.ownerTable.checkboxColumn.show) {
            index = 0;
          }
          indexColumnWidth = this.ownerTable.elTable.columns[index];
          return indexColumnWidth.width;
        } else {
          return this.ownerTable.indexColumn.width;
        }
      } else {
        return 0;
      }
    },
    getCheckColumnWidth() {
      if (this.ownerTable.checkboxColumn.show) {
        if (this.ownerTable.elTable) {
          let checkboxColumn = this.ownerTable.elTable.columns[0];
          return checkboxColumn.width;
        } else {
          return this.ownerTable.checkboxColumn.width;
        }
      } else {
        return 0;
      }
    },
    getWidth(column) {
      let width = column.width;
      let tableWidth = this.ownerTable.tableWidth;
      if (tableWidth) {
        let total = 0;
        let isAuto = false;
        for (let i = 0; i < this.filterColumns.length; i++) {
          if (!this.filterColumns[i].width || this.filterColumns[i].width == "auto") {
            isAuto = true;
            break;
          }
          total += parseFloat(this.percentageToPx(this.filterColumns[i].width, tableWidth, true));
        }
        //如果列表中存在自动宽度列则有自动宽度自己缩减
        if (isAuto) {
          width = parseFloat(this.percentageToPx(width, tableWidth, true));
        } else {
          //将内容区不足的宽度平均分配到每一列中
          let tw = this.getContentWidth(tableWidth);
          width = parseFloat(this.percentageToPx(width, tw));
          if (total < tw) {
            width += (tw - total) / this.filterColumns.length;
          } else {
            width = parseFloat(this.percentageToPx(width, tw));
          }
        }
      }
      return width;
    }
  },
  render(h) {
    return renderHeader.call(this, h, this.column, this.$vnode.data.key);
  }
});

function renderHeader(h, column, key) {
  let _this = this;
  let child = [];
  let scopedSlots = null;
  if (column.children && column.children.length > 0) {
    child = _.map(column.children, (c, i) => {
      return renderHeader.call(this, h, c, i);
    });
  } else {
    child = this.$slots[column.columnId];
    scopedSlots = _this._u(
      [
        {
          key: "header",
          fn(scope) {
            return [
              h("span", {
                domProps: {
                  innerHTML: `${column.prefix || ""}${column.label}${column.suffix || ""}`
                }
              })
            ];
          }
        },
        {
          key: "default",
          fn(scope) {
            return [_this._t(column.columnId || "default", null, null, scope)];
          }
        }
      ],
      null,
      true
    );
  }
  let props = { ...column };
  // props.minWidth=props.width;
  delete props.resizable;
  delete props.width;
  let data = {
    key: key,
    props: props,
    attrs: {
      columnKey: column.columnId,
      ...this.$attrs,
      minWidth: props.minWidth,
      width: this.getWidth(column),
      sortable: this.sortable,
      className: column.hide ? "ds-datagrid-column-hide" : "",
      // resizable: true
    }
  };
  // delete data.props.width;
  if (scopedSlots) {
    data.scopedSlots = scopedSlots;
  }
  if (this.ownerTable) {
    if (this.ownerTable.isDesign) {
      let vnode = h("el-table-column", data, child);
      return vnode;
    } else {
      if (!column.hide) {
        let vnode = h("el-table-column", data, child);
        return vnode;
      }
    }
  }
  return null;
}
</script>
