<template>
  <el-form ref="form" :model="form" label-width="80px">
    <el-form-item label="文件类型" v-if="this.allowList.length">
      <el-radio-group v-model="extName">
        <el-radio :label="item.value" v-for="item in allowList" :key="item.value">{{item.text}}</el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="导出字段">
      <el-checkbox-group v-model="selectedFields">
        <el-checkbox v-for="field in fields" :key="field.id" :label="field.id">{{field.caption}}</el-checkbox>
      </el-checkbox-group>
    </el-form-item>
  </el-form>
</template>
<script>
export default {
  name: "DsfExportSetting",
  data() {
    return {
      form: {},
      allowList: [],
      extName: "",
      selectedFields: []
    };
  },
  props: {
    allow: {
      type: Array,
      default() {
        return [];
      }
    },
    fields: {
      type: Array,
      default() {
        return [];
      }
    },
    fileName: {
      type: String,
      default: ""
    }
  },
  created() {
    // let list = this.allow.split(",");
    // this.allowList = _.filter(list, (v) => v);
    _.each(this.allow, (v) => {
      let v1 = v.split("|");
      let text = "",
        value = "",
        selected = "";
      if (v1.length == 1) {
        text = v1[0];
        value = v1[0];
        selected = false;
      } else if (v1.length == 2) {
        text = v1[0];
        value = v1[1];
        selected = false;
      } else if (v1.length == 3) {
        text = v1[0];
        value = v1[1];
        selected = v1[2] == 1;
        if (selected) {
          this.extName = value;
        }
      }
      var obj = {
        text: text,
        value: value
      };
      this.allowList.push(obj);
    });
    if (!this.extName && this.allowList.length >= 0) {
      this.extName = this.allowList[0].value;
    }

    if (this.fields) {
      this.selectedFields = _.map(this.fields, "id");
    }
  },
  mounted() {},
  methods: {
    yes() {
      let selectedFields = _.map(this.selectedFields, (v) => {
        let f = _.find(this.fields, (field) => {
          return field.id == v;
        });
        if (f) {
          return f;
        }
      });
      selectedFields = _.filter(selectedFields, (v) => v);
      return {
        extName: this.extName,
        fields: selectedFields,
      };
    }
  }
};
</script>