<template>
  <div class="ds-table-cell-set">
    <div class="ds-table-cell-set-box">
      <div class="ds-table-cell-set-box-label">边框</div>
      <div class="ds-table-cell-set-item" style="width: 70%">
        <span style="width: 60px;">样式：</span>
        <div v-for="item in config['border-style']" :key="item" :class="{active: borderStyle === item}" class="border-style-demo" @click="borderStyle === item ? borderStyle = '' : borderStyle = item">
          <template v-if="item === 'none'">无</template>
          <i v-else-if="item === 'double'" :style="{'border-bottom': `3px double #000`}"></i>
          <i v-else :style="{'border-bottom': `1px ${item} #000`}"></i>
        </div>
      </div>
      <div class="ds-table-cell-set-item">
        <span style="width: 60px;">宽度：</span>
        <el-select v-model="borderWidth" size="mini" style="width: 140px" placeholder="请选择">
          <el-option v-for="item in config['border-width']" :key="item" :label="item" :value="item" />
        </el-select>
      </div>
      <div class="ds-table-cell-set-item">
        <span style="width: 60px;">颜色：</span>
        <el-color-picker v-model="borderColor" size="mini" show-alpha></el-color-picker>
      </div>
      <div class="ds-table-cell-set-item" style="width: 100%">
        <span style="width: 60px;">预置：</span>
        <div class="border-position-checkbox" :class="{active: borderPosition[0]}" @click="$set(borderPosition, 0, !borderPosition[0])">
          <i :style="[borderCheckBox.top]"></i><i :style="[borderCheckBox.top]"></i><i></i><i></i>
        </div>
        <div class="border-position-checkbox" :class="{active: borderPosition[1]}" @click="$set(borderPosition, 1, !borderPosition[1])">
          <i :style="[borderCheckBox.bottom]"></i><i :style="[borderCheckBox.bottom]"></i><i></i><i></i>
        </div>
        <div class="border-position-checkbox" :class="{active: borderPosition[2]}" @click="$set(borderPosition, 2, !borderPosition[2])">
          <i></i><i></i><i :style="[borderCheckBox.bottom]"></i><i :style="[borderCheckBox.bottom]"></i>
        </div>
        <div class="border-position-checkbox" :class="{active: borderPosition[3]}" @click="$set(borderPosition, 3, !borderPosition[3])">
          <i :style="[borderCheckBox.left]"></i><i></i><i :style="[borderCheckBox.left]"></i><i></i>
        </div>
        <div class="border-position-checkbox" :class="{active: borderPosition[4]}" @click="$set(borderPosition, 4, !borderPosition[4])">
          <i :style="[borderCheckBox.right]"></i><i></i><i :style="[borderCheckBox.right]"></i><i></i>
        </div>
        <div class="border-position-checkbox" :class="{active: borderPosition[5]}" @click="$set(borderPosition, 5, !borderPosition[5])">
          <i></i><i :style="[borderCheckBox.right]"></i><i></i><i :style="[borderCheckBox.right]"></i>
        </div>
      </div>
      <div class="ds-table-cell-set-item" style="width: 100%">
        <span style="width: 60px;"></span>
        <div class="border-position-demo">
          <i :style="[borderDemo.t, borderDemo.l, borderDemo.cb, borderDemo.cr]">
            文本
          </i><i :style="[borderDemo.t, borderDemo.cb, borderDemo.cr]">
            文本
          </i><i :style="[borderDemo.t, borderDemo.r, borderDemo.cb]">
            文本
          </i><i :style="[borderDemo.l, borderDemo.cb, borderDemo.cr]">
            文本
          </i><i :style="[ borderDemo.cb, borderDemo.cr]">
            文本
          </i><i :style="[borderDemo.r, borderDemo.cb]">
            文本
          </i><i :style="[borderDemo.l, borderDemo.b, borderDemo.cr]">
            文本
          </i><i :style="[borderDemo.b, borderDemo.cr]">
            文本
          </i><i :style="[borderDemo.r, borderDemo.b]">
            文本
          </i>
        </div>
      </div>
    </div>
    <div class="ds-table-cell-set-box">
      <div class="ds-table-cell-set-box-label">对齐</div>
      <!--      <div class="ds-table-cell-set-item">-->
      <!--        <span style="width: 80px;">缩进：</span>-->
      <!--        <el-input-number v-model="style['text-indent']" :step="1" :min="0" style="width: 140px" size="mini" placeholder="跟随上下文"></el-input-number>-->
      <!--      </div>-->
      <div class="ds-table-cell-set-item">
        <span style="width: 80px;">水平对齐：</span>
        <el-select v-model="cellAttr['align']" size="mini" style="width: 140px" placeholder="请选择">
          <el-option v-for="(item, index) in config['align']" :key="index" :label="item[0]" :value="item[1]">
            <span :style="{'font-family': item[1]}">{{ item[0] }}</span>
          </el-option>
        </el-select>
      </div>
      <div class="ds-table-cell-set-item">
        <span style="width: 80px;">垂直对齐：</span>
        <el-select v-model="cellAttr['valign']" size="mini" style="width: 140px" placeholder="请选择">
          <el-option v-for="(item, index) in config['valign']" :key="index" :label="item[0]" :value="item[1]">
            <span :style="{'font-family': item[1]}">{{ item[0] }}</span>
          </el-option>
        </el-select>
      </div>
      <!--      <div class="ds-table-cell-set-item">-->
      <!--        <span style="width: 80px;">文字方向：</span>-->
      <!--        <el-select v-model="style['direction']" size="mini" style="width: 140px" placeholder="请选择">-->
      <!--          <el-option v-for="(item, index) in config['direction']" :key="index" :label="item[0]" :value="item[1]" />-->
      <!--        </el-select>-->
      <!--      </div>-->
    </div>
    <div class="ds-table-cell-set-box">
      <div class="ds-table-cell-set-box-label">填充</div>
      <div class="ds-table-cell-set-item">
        <span style="width: 60px;">背景色：</span>
        <el-color-picker v-model="style['background-color']" size="mini" show-alpha></el-color-picker>
      </div>
    </div>
    <div class="ds-table-cell-set-box">
      <div class="ds-table-cell-set-box-label">字体</div>
      <div class="ds-table-cell-set-item">
        <span style="width: 60px;">字体：</span>
        <el-select v-model="style['font-family']" size="mini" style="width: 140px" placeholder="请选择">
          <el-option v-for="(item, index) in config['font-family']" :key="index" :label="item[0]" :value="item[1]">
            <span :style="{'font-family': item[1]}">{{ item[0] }}</span>
          </el-option>
        </el-select>
      </div>
      <div class="ds-table-cell-set-item">
        <span style="width: 60px;">字号：</span>
        <el-select v-model="style['font-size']" size="mini" style="width: 140px" placeholder="请选择">
          <el-option v-for="(item, index) in config['font-size']" :key="index" :label="item[0]" :value="item[1]">
            <span :style="{'font-size': item[1]}">{{ item[0] }}</span>
          </el-option>
        </el-select>
      </div>
      <div class="ds-table-cell-set-item">
        <span style="width: 60px;">装饰：</span>
        <el-select v-model="style['text-decoration']" size="mini" style="width: 140px" placeholder="请选择">
          <el-option v-for="(item, index) in config['text-decoration']" :key="index" :label="item[0]" :value="item[1]" />
        </el-select>
      </div>
      <div class="ds-table-cell-set-item">
        <span style="width: 60px;">颜色：</span>
        <el-color-picker v-model="style['color']" size="mini" show-alpha></el-color-picker>
      </div>
      <div class="ds-table-cell-set-item" style="width: 100%">
        <span style="width: 60px;">样式：</span>
        <el-checkbox :value="style['font-style'] === 'italic'" @change="$set(style, 'font-style', $event ? 'italic' : 'normal')">斜体</el-checkbox>
        <el-checkbox :value="style['font-weight'] === 'bold'" @change="$set(style, 'font-weight', $event ? 'bold' : 'normal')">粗体</el-checkbox>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DesTableCellSet',
  data() {
    return {
      borderCheckBox: {
        top: { 'border-top': '1px solid #666' },
        left: { 'border-left': '1px solid #666' },
        right: { 'border-right': '1px solid #666' },
        bottom: { 'border-bottom': '1px solid #666' }
      },
      config: {
        'font-family': [
          ['跟随上下文', ''],
          ['仿宋', '仿宋,FangSong'],
          ['黑体', '黑体, SimHei'],
          ['楷体', '楷体,楷体_GB2312, SimKai'],
          ['隶书', '隶书,SimLi'],
          ['宋体', '宋体,SimSun'],
          ['微软雅黑', '微软雅黑,Microsoft YaHei'],
          ['幼圆', '幼圆,YouYuan'],
          ['andale mono', 'andale mono'],
          ['arial', 'arial, helvetica,sans-serif'],
          ['arial black', 'arial black,avant garde'],
          ['comic sans ms', 'comic sans ms'],
          ['impact', 'impact,chicago'],
          ['times new roman', 'times new roman']
        ],
        'font-size': [
          ['跟随上下文', ''],
          ['10px', '10px'],
          ['11px', '11px'],
          ['12px', '12px'],
          ['14px', '14px'],
          ['16px', '16px'],
          ['18px', '18px'],
          ['20px', '20px'],
          ['22px', '22px'],
          ['24px', '24px'],
          ['36px', '36px'],
        ],
        'direction': [
          ['跟随上下文', ''],
          ['从左到右', 'ltr'],
          ['从右到左', 'rtl'],
        ],
        'text-decoration': [
          ['跟随上下文', ''],
          ['无装饰', 'none'],
          ['下划线', 'underline'],
          ['上划线', 'overline'],
          ['贯穿线', 'line-through']
        ],
        'border-style': [
          'none',
          'solid',
          'dotted',
          'dashed'
        ],
        'border-width': [
          '1px',
          '2px',
          '3px',
          '4px',
          '5px',
          '6px'
        ],
        'align': [
          ['跟随上下文', ''],
          ['靠左', 'left'],
          ['居中', 'center'],
          ['靠右', 'right']
        ],
        'valign': [
          ['跟随上下文', ''],
          ['靠上', 'top'],
          ['居中', 'middle'],
          ['靠下', 'bottom'],
          ['基线对齐', 'baseline']
        ]
      },
      style: {
        'font-family': '',
        'font-size': '',
        'font-style': 'normal',
        'font-weight': 'normal',
        'direction': '',
        'text-decoration': '',
        'text-indent': undefined,
        'background-color': '',
        'color': ''
      },
      cellAttr: {
        align: '',
        valign: '',
      },
      borderWidth: '1px',
      borderStyle: 'solid',
      borderColor: '#000',
      borderPosition: [false, false, false, false, false, false]
    }
  },
  computed: {
    borderDemo() {
      const res = {
        t: {},
        l: {},
        r: {},
        b: {},
        cb: {},
        cr: {}
      };
      if (this.borderStyle === '') {
        return res;
      }
      let style = '1px solid transparent';
      if (this.borderStyle !== 'none') {
        style = `${this.borderWidth} ${this.borderStyle} ${this.borderColor}`;
      }
      if (this.borderPosition[0]) {
        res.t['border-top'] = style;
      }
      if (this.borderPosition[1]) {
        res.cb['border-bottom'] = style;
      }
      if (this.borderPosition[2]) {
        res.b['border-bottom'] = style;
      }
      if (this.borderPosition[3]) {
        res.l['border-left'] = style;
      }
      if (this.borderPosition[4]) {
        res.cr['border-right'] = style;
      }
      if (this.borderPosition[5]) {
        res.r['border-right'] = style;
      }
      return res;
    }
  },
  methods: {
    yes() {
      const cellAttr = {
        align: this.cellAttr.align,
        valign: this.cellAttr.valign
      };
      const cellStyle = {};
      if (this.style['font-family'] !== '') {
        cellStyle['font-family'] = this.style['font-family'];
      }
      if (this.style['font-size'] !== '') {
        cellStyle['font-size'] = this.style['font-size'];
      }
      if (this.style['font-style'] !== 'normal') {
        cellStyle['font-style'] = this.style['font-style'];
      }
      if (this.style['font-weight'] !== 'normal') {
        cellStyle['font-weight'] = this.style['font-weight'];
      }
      if (this.style['direction'] !== '') {
        cellStyle['direction'] = this.style['direction'];
      }
      if (this.style['text-decoration'] !== '') {
        cellStyle['text-decoration'] = this.style['text-decoration'];
      }
      if (this.style['text-indent'] !== undefined) {
        cellStyle['text-indent'] = this.style['text-indent'];
      }
      if (this.style['background-color'] !== '') {
        cellStyle['background-color'] = this.style['background-color'];
      }
      if (this.style['color'] !== '') {
        cellStyle['color'] = this.style['color'];
      }
      return {
        cellStyle,
        cellAttr,
        borderPosition: this.borderDemo
      }
    }
  }
}
</script>