<template>
  <dsf-exam-page :res-data="resData" :exam-mark-active="examMarkActive">
    <template slot="body" slot-scope="{subjects}">
      <div class="exam-body">
        <el-input
          v-model="checkValue[subjects.id]"
          type="textarea"
          placeholder="请答题，答题框可在右下角拉伸"
          :rows="12"
          :readonly="readOnly"
          :maxlength="maxLength"
          show-word-limit
          @change="handleCheck(subjects.id, subjects.type)"
        ></el-input>
      </div>
    </template>
  </dsf-exam-page>
</template>

<script>
import DsfExamPage from './page'
import examControl from "_platform/mixins/examControl";

export default dsf.component({
  name: "DsfExamSubjective",
  mixins: [examControl],
  ctrlCaption: "主观题",
  inject: ['exam'],
  props: {
    readOnly: {
      type: Boolean,
      default: false,
    },
    visible: {
      type: Boolean,
      default: true,
    },
    maxLength: {
      type: [String, Number],
      default: 200,
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("exam-subjective-meta-data");
      },
    },
  },
  data() {
    return {

    };
  },
  components: {
    DsfExamPage
  }
});
</script>
