
import listJson from "./list.json";

export default {
  props: {
    // 开启时段设置 晨读&cdpk,晚上&wspk
    periodsHide: {
      true: String,
      default() {
        return "晨读&cdpk,晚上&wspk";
      },
    },
  },
  filters: {
    // 过滤日期头部
    dateHeadBuild(obj) {
      const { date, isDate } = obj;
      if (isDate) {
        return `${date.getDate()}日`;
      }
      return "";
    },
    dateFormat(date, format) {
      return dsf.date.format(date, format);
    },
    // 多余文字...
    textEllipsis(text, number, placeholder) {
      if (text) {
        if (number) {
          let suffix = "";
          if (text.length > number) {
            suffix = "...";
          }
          return text.replace(new RegExp(`^((.){${number}}).*`), "$1") + suffix;
        } else {
          return text;
        }
      }
      return placeholder || "无";
    },
  },
  computed: {
  },
  data() {
    return {

    }
  },
  methods: {
    // 获取时段数据
    getPeriodsData(url, result) {
      if (url) {
        let url = dsf.express.eval(tab.url);
        this.$http.get(url, {}).done().error().always();
      } else {
        result(listJson.periodsData || {});
      }
    },
    /**
     * 钩子 --> 获取到班次信息后进行清洗
     * @param tab 选中的tab对象
     * @param res 接口返回的数据集合
     * @return {Array} result 返回数组对象
     */
    cleanPeriodsDataHooks(res) {
      let result = null;
      if (res.success) {
        let data = res.data;
        result = this.cleanDataKeyPrefix(data);
      }
      return result;
    },
    /**
     * 清洗数据 去掉key 字段 . 前面的字符
     */
    cleanDataKeyPrefix(o) {
      let new_o = o;
      let o_type = dsf.type(o);
      if (o_type == "object") {
        new_o = {};
        for (const key in o) {
          new_o[key.replace(/^.*\./, "")] = this.cleanDataKeyPrefix(o[key]);
        }
      } else if (o_type == "array") {
        new_o = [];
        for (const i in o) {
          new_o[i] = this.cleanDataKeyPrefix(o[i]);
        }
      }
      return new_o;
    },
  }
}