<template>
  <dsf-exam-page :res-data="resData" :exam-mark-active="examMarkActive">
    <template slot="title" slot-scope="{subjects}">
      <div class="exam-title">
        <template v-for="(ele,index) in fillData[subjects.id]">
          {{ ele }}
          <template v-if="index<fillData[subjects.id].length-1 && fillData[subjects.id].length>2">
            <input
              :key="index"
              v-model="checkValue[subjects.id]['text_'+index]"
              type="text"
              class="exam-fill-input"
              maxlength="1"
              :readonly="readOnly"
              @change="handleCheck(subjects.id, subjects.type)"
            />
          </template>
        </template>
      </div>
    </template>
  </dsf-exam-page>
</template>

<script>
import DsfExamPage from './page'
import examControl from "_platform/mixins/examControl";

export default dsf.component({
  name: "DsfExamFill",
  mixins: [examControl],
  ctrlCaption: "填空题",
  inject: ["exam"],
  props: {
    readOnly: {
      type: Boolean,
      default: false,
    },
    visible: {
      type: Boolean,
      default: true,
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("exam-meta-data");
      },
    },
  },
  data() {
    return {
      fillData: {},
    };
  },
  created() {
    for (let subjects of this.resData) {
      this.$set(this.checkValue, subjects.id, {});
      this.fillData[subjects.id] = subjects.content.replace(/（\s*）/gi, "()").split("()");
    }
  },
  methods: {
    getSameNum(val, arr) {
      processArr = arr.filter(function (value) {
        return value == val;
      });
      return processArr.length;
    },
  },
  components: {
    DsfExamPage
  }
});
</script>