<template>
  <div id="app">
    <home :page-suffix="pageSuffix"></home>
  </div>
</template>

<script>
import home from "./views/Home";
export default {
  components: {
    home,
  },
  provide() {
    return {
      $app: this
    }
  },
  data() {
    return {
      pageSuffix: 'else',
    };
  },
  created() {
    let htmlName = window.location.pathname;
    if (htmlName.indexOf("/index.html") >= 0) {
      this.pageSuffix = 'index';
    } else if (htmlName.indexOf("/index1.html") >= 0) {
      this.pageSuffix = 'index1';
    } else {
      this.pageSuffix = 'else';
    }
  },
  mounted() { },
  methods: {},
};
</script>