<template>
  <div class="ds-control">
    <div v-for="(subjects, examIndex) in resData" :key="subjects.id" :exam-id="subjects.id" class="ds-exam-content">
      <div class="exam-index">{{ ++examIndex }}、</div>
      <div class="exam-content">
        <slot :subjects="subjects" name="title">
          <div class="exam-title">{{ subjects.content }}</div>
        </slot>
        <slot :subjects="subjects" name="body"></slot>
        <!-- if="($answer&&$answer.length)" 以下数据做判断显示 -->
        <div class="exam-answer">
          <span>
            标准答案： 
            answer -> values
            <!-- {{ getAnswer().join(" , ") }} -->
          </span>
        </div>
        <!-- analysis_content   -->
        <div class="exam-analysis">
          <span class="title">解析:</span>
          <br />
          <span>当前解析为 answer数据里 parse</span>
        </div>
        <div class="exam-tools" @click="showPrompt(`${subjects.prompt}`)">
          <span class="prompt">
            <i class="iconfont icon-Magnifier"></i>
            <span class="ds_font_4">查看提示</span>
          </span>
        </div>
      </div>
      <i
        class="iconfont icon-biaoji exam-mark"
        title="标记"
        :class="{active: examMarkActive.indexOf(subjects.id)!=-1}"
        @click="doMark(subjects.id)"
      ></i>
    </div>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfExamPage",
  ctrlCaption: "考试Page",
  props: {
    resData: {
      type: Array,
      default() {
        return []
      },
    },
    examMarkActive: {
      type: Array,
      default() {
        return []
      },
    },
  },
  data() {
    return {
      
    };
  },
  created() {
  },
  methods: {
    // 查看提示
    showPrompt(promt) {
      dsf.layer.alert(
        `<pre style="word-break: break-all;white-space: normal;text-align: justify;"> ${promt} </pre>`
      )
    },
    // 标记
    doMark(id) {
      if (this.examMarkActive.indexOf(id) != -1) {
        this.examMarkActive.splice(this.examMarkActive.indexOf(id), 1)
      } else {
        this.examMarkActive.push(id)
      }
    },
  },
});
</script>
