<template>
  <div class="ds-control ds-subtable" :class="getCss">
    <div v-if="showLabel||buttonBars.length" class="ds-subtable-tools">
      <dsf-title v-if="showLabel" :value="label" class="ds-no-padding" style="float:left;margin-top:5px" :font-type="3" :show-weight="true" :show-underline="showLabel&&label?labelShowUnderline:false" :show-color-lump="showLabel&&label?labelShowColorLump:false">
      </dsf-title>
      <div v-if="buttonBars.length" class="head-right-item" style=" float: right;">
        <DsfButtonBar :buttons="buttonBars" :more-button-style="buttonsMoreButtonStyle" :max-number="buttonsMaxNumber" :more-button-text="buttonsMoreButonText" :owner="_self" :local="local" :size="'small'" :class="{'ds-no-padding':true}"></DsfButtonBar>
      </div>
    </div>
    <div v-if="isDesign" class="ds-subtable-box">
      <!--设计时子表-->
      <el-table v-if="isDesign" ref="table" :data="templateData" :border="true" :row-key="setRowKey" :header-cell-class-name="headerCellClassName" :cell-class-name="cellClassName">
        <el-table-column
          v-if="isShowIndexColumn"
          :key="'indexColumn'"
          :type="'index'"
          :index="indexMethod"
          :label="'序号'"
          :width="59"
          :resizable="false"
          :header-align="'center'"
          :align="'center'"></el-table-column>
        <!-- <el-table-column v-if="isShowIndexColumn" :type="'index'" :index="indexMethod" :label="'序号'" :width="80" :header-align="'center'" :align="'center'">
        </el-table-column> -->
        <slot></slot>
        <template v-if="!isReDrawHander">
          <DsfSubTableColumn v-for="(col, $index) in columns" ref="header" :key="$index" :column="col">
            <template v-for="column in getLastColumns(col)" v-slot:[column.columnId]>
              <slot :name="column.columnId"></slot>
            </template>
          </DsfSubTableColumn>
        </template>
        <el-table-column v-if="isShowOperateColumn && !readOnly" :key="'operateColumn'" :resizable="false" :label="'操作'" :width="130" :header-align="'center'" align="center">
          <template v-slot:default="scope">
            <DsfButton v-if="scope._self.allowEdit && !scope._self.foreverEditor" btn-style="icon-text" size="small" icon="tianxie" title="修改" style="min-width: auto;"></DsfButton>
            <DsfButton btn-style="icon-text" size="small" class="ds-subtable-button" icon="shanchu" title="删除" style="min-width: auto;"></DsfButton>
            <DsfButton btn-style="icon-text" size="small" class="ds-subtable-button" icon="shang" title="上移" style="min-width: auto;"></DsfButton>
            <DsfButton btn-style="icon-text" size="small" class="ds-subtable-button" icon="xia" title="下移" style="min-width: auto;"></DsfButton>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div v-else-if="!isDesign" class="ds-subtable-box" @click.stop>
      <!--运行时子表-->
      <el-table ref="table" :border="true" :data="list" :row-key="setRowKey" :row-class-name="setRowClass" @focus.native="editBlurHandler" @row-dblclick="rowDblClickHander($event)">
        <el-table-column v-if="isShowIndexColumn" :type="'index'" :index="indexMethod" :label="'序号'" :resizable="false" :width="59" :header-align="'center'" :align="'center'"></el-table-column>
        <slot></slot>
        <template v-if="!isReDrawHander">
          <template v-for="(col, $index) in columns">
            <DsfSubTableColumn v-if="renderTableHandler(col)!==false" ref="header" :key="$index" :column="col">
              <template v-for="column in getLastColumns(col)" v-slot:[column.columnId]="scope">
                <slot :name="column.columnId" v-bind="scope"></slot>
              </template>
            </DsfSubTableColumn>
          </template>
        </template>
        <el-table-column v-if="isShowOperateColumn && !readOnly" :label="'操作'" :width="130" :resizable="false" :header-align="'center'" align="center">
          <template v-slot:default="scope">
            <template v-if="!scope.row.$editing || scope._self.foreverEditor">
              <DsfButton
                v-if="scope._self.allowEdit && !scope._self.foreverEditor&&renderRowButton('edit',scope.row)"
                class="ds-subtable-button edit-button"
                :class="['edit-button-'+scope.row.$hash]"
                btn-style="icon-text"
                size="small"
                icon="tianxie"
                title="修改"
                style="min-width: auto;"
                @click="rowEditHandler(scope)"></DsfButton>
              <DsfButton
                v-if="renderRowButton('delete',scope.row)"
                btn-style="icon-text"
                size="small"
                class="ds-subtable-button"
                icon="shanchu"
                :class="['delete-button-'+scope.row.$hash]"
                title="删除"
                style="min-width: auto;"
                @click="rowDeleteHandler(scope)"></DsfButton>
              <DsfButton
                v-if="renderRowButton('up',scope.row)"
                btn-style="icon-text"
                size="small"
                class="ds-subtable-button"
                icon="shang"
                title="上移"
                :class="['up-button-'+scope.row.$hash]"
                style="min-width: auto;"
                @click="rowUpHandler(scope)"></DsfButton>
              <DsfButton
                v-if="renderRowButton('down',scope.row)"
                btn-style="icon-text"
                size="small"
                class="ds-subtable-button"
                icon="xia"
                title="下移"
                :class="['down-button-'+scope.row.$hash]"
                style="min-width: auto;"
                @click="rowDownHandler(scope)"></DsfButton>
            </template>
            <template v-else>
              <DsfButton btn-style="icon-text" size="small" class="ds-subtable-button" icon="dui" title="确定" style="min-width: auto;" @click="rowEditCompleteHandler(scope)"></DsfButton>
              <DsfButton btn-style="icon-text" size="small" class="ds-subtable-button" icon="cancel" title="撤销" style="min-width: auto;" @click="rowEditCancelHandler(scope)"></DsfButton>
            </template>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--设计状态下分页-->
    <div v-if="showPagination && isDesign" class="ds-subtable-footer">
      <el-pagination :hide-on-single-page="false" :total="1" background :layout="'total,prev,pager,next,jumper'"></el-pagination>
    </div>
    <!--运行状态下分页-->
    <div v-else-if="showPagination && !isDesign" class="ds-subtable-footer">
      <el-pagination
        :hide-on-single-page="true"
        :current-page="currentPage"
        :page-count="pageCount"
        :page-size="pageSize"
        :total="value.length"
        :pager-count="pagerCount"
        background
        :layout="'total,prev,pager,next,jumper'"
        @current-change="currentChange"></el-pagination>
    </div>
  </div>
</template>
<script>
import oneToMany from "_platform/mixins/oneToMany";
// import DsfSubTableColumn from './subTableColumn';
import DsfSubTableCellContent from "./subTableCellContent";

//注入一个全局的点击事件，结束所有正在编辑的子表
document.addEventListener("click", () => {
  let subTables = document.querySelectorAll(".ds-subtable");
  //取消所有子表编辑
  _.each(subTables, (sb) => {
    if (sb && sb.__vue__ && sb.__vue__.editAllEnd) {
      sb.__vue__.editAllEnd();
    }
  });
});

export default dsf.component({
  name: "DsfSubTable",
  mixins: [oneToMany],
  ctrlCaption: "子表",
  props: {
    showLabel: {
      type: Boolean,
      default: false
    },
    labelShowUnderline: {
      type: Boolean,
      default: true
    },
    labelShowColorLump: {
      type: Boolean,
      default: true
    },
    value: {
      type: Array,
      default() {
        return [];
      }
    },
    isEditabled: {
      type: Boolean,
      default() {
        return true;
      }
    },
    columns: {
      type: Array,
      default() {
        return [];
      }
    },
    //是否显示操作列
    isShowOperateColumn: {
      type: Boolean,
      default: true
    },
    //是否显示索引列
    isShowIndexColumn: {
      type: Boolean,
      default: true
    },
    // 是否强制全表可编辑
    foreverEditor: {
      type: Boolean,
      default: false
    },
    slots: {
      type: Array,
      default() {
        return [
          {
            name: "default",
            scope: "scope",
            controls: []
          }
        ];
      }
    },
    //是否允许分页
    showPagination: {
      type: Boolean,
      default: true
    },
    //分页页码按钮最多显示个数
    pagerCount: {
      type: Number,
      default: 5
    },
    //一页显示多少条记录
    pageSize: {
      type: Number,
      default: 10
    },
    buttonBars: {
      type: Array,
      default() {
        return [];
      }
    },
    buttonsMoreButonText: {
      type: String,
      default: "更多"
    },
    buttonsMoreButtonStyle: {
      type: String,
      default: ""
    },
    buttonsMaxNumber: {
      type: Number,
      default: 4
    },
    removeRowBefore: {
      type: Function,
      default() {
        return null;
      }
    },
    renderRowButton: {
      type: Function,
      default() {
        return () => true;
      }
    },
    // 需要隐藏的列，传递列索引
    hiddenColumns: {
      type: Array,
      default() {
        return [];
      }
    },
    renderHeader: {
      type: Function,
      default: dsf.noop
    },
    //数据过滤
    filterData: {
      type: Function,
      default: null
    },
    //导出配置
    exportOptions: {
      type: [Object, Array],
      default() {
        return {
          "rule-1": []
        };
      }
    }
  },
  data() {
    return {
      isReDrawHander: false,
      currentPage: 1,
      drawTable: false,
      editingData: null,
      oldData: {},
      deleted: [],
      dataWatch: [],
      elTable: null,
      // columnMaps: {},
      subControls: {},
      templateData: [{ _id: dsf.uuid() }],
      tableWidth:0
    };
  },
  computed: {
    pageCount() {
      return Math.ceil(this.value.length / this.pageSize);
    },
    list() {
      let value = this.allList;
      let showPagination = this.showPagination;
      let pageSize = this.pageSize;
      let currentPage = this.currentPage;
      if (showPagination) {
        let s = (currentPage - 1) * pageSize;
        let e = s + pageSize;
        return value.slice(s, e);
      }
      return value;
    },
    allList() {
      let value = this.value;
      if (this.filterData) {
        value = this.filterData(value);
      }
      return value;
    },
    getColumnSlots() {
      return _.filter(slots, (s) => s.name.indexOf("column-") == 0);
    },
    columnMaps() {
      let arr = {};
      this.deep((c, i, colIndex, level) => {
        if (!arr[level]) {
          arr[level] = [];
        }
        arr[level].push(c);
      }, this.columns);
      return arr;
    },
    allowEdit() {
      let readOnly = this.readOnly;
      let foreverEditor = this.foreverEditor;
      let isEditabled = this.isEditabled;
      if (readOnly) {
        return false;
      } else {
        if (foreverEditor) {
          return true;
        } else {
          return isEditabled;
        }
      }
    }
  },
  beforeCreate() {
    this.outputColumns = [];
  },
  created() {
    createDesignDefaultColumns.call(this);
    this.refreshHeader();
  },

  mounted() {
    this.elTable = this.$refs.table;
  },
  beforeUpdate() {
    this.outputColumns = [];
  },
  updated() {
    this.doLayout();
  },
  methods: {
    /*******对外方法 START********/
    // 追加行
    pushRow(row) {
      this.value.push(row);
      this.$nextTick(() => {
        this.editBegin(row);
      });
    },
    // 插入行
    insertRow(row, index) {
      this.value.splice(index, 0, row);
      this.$nextTick(() => {
        this.editBegin(row);
      });
    },
    // 删除行
    removeRow(data, isAppendToDeletedRows) {
      this.editAllEnd();
      if (isAppendToDeletedRows) {
        dsf.array.remove(this.value, data);
        this.deleted.push(data);
      } else {
        dsf.array.remove(this.value, data);
      }
      if (!this.list.length && this.currentPage > 1) {
        this.currentPage--;
      }
    },
    // 删除多条数据
    removeRows(rows) {
      _.eachRight(rows, (row) => {
        row._id ? this.removeRow(row, true) : this.removeRow(row, false);
      });
    },
    //清空所有行
    clearRows() {
      _.eachRight(this.value, (row) => {
        row._id ? this.removeRow(row, true) : this.removeRow(row, false);
      });
    },
    /*******对外方法 END********/
    getLastColumns(column) {
      let arr = [];
      this.deep((c) => {
        if (!c.children || !c.children.length) {
          c.isLast = true;
          arr.push(c);
        } else {
          c.columnId = "";
          c.isLast = false;
        }
      }, column);
      return arr;
    },
    deep(callback, column) {
      const fn = function (columns, level) {
        let columnIndex = 0;
        _.each(columns, (c, i) => {
          callback && callback(c, i, columnIndex, level);
          fn(c.children, level + 1);
          columnIndex++;
        });
      };
      let cols = column ? column : this.columns;
      cols = dsf.type(cols) !== "array" ? [cols] : cols;
      fn(cols, 0);
    },
    //分页组件页码变化
    currentChange(evt) {
      this.currentPage = evt;
    },
    //更新索引
    indexMethod(index) {
      return (this.currentPage - 1) * this.pageSize + index + 1;
    },
    setRowKey(row) {
      return row.$hash;
    },
    setRowClass() {},
    addColumn(column, index, parentColumn) {
      let defaultColumn = {
        label: "",
        columnId: "",
        level: 0,
        isLast: false,
        hide: false,
        headerAlign: "center",
        align: "center",
        resizable: false,
        width: "auto",
        children: []
      };
      let col = dsf.mix({}, defaultColumn, column);
      if (parentColumn) {
        if (dsf.isUnDef(index)) {
          parentColumn.children.push(col);
          col.level = parentColumn.level + 1;
        } else {
          parentColumn.children.splice(index, 0, col);
        }
      } else {
        dsf.isUnDef(index) ? this.columns.push(col) : this.columns.splice(index, 0, col);
      }
      return col;
    },
    doLayout() {
      this.elTable = this.$refs.table;
      if (this.elTable) {
        this.elTable.doLayout();
      }
    },
    headerCellClassName(info) {
      let className = [];
      if (this.isDesign) {
        let columnIndex = info.columnIndex;
        if (info.rowIndex === 0) {
          if (this.isShowIndexColumn) {
            columnIndex = columnIndex - 1;
          }
        }
        if (this.columnMaps) {
          let row = this.columnMaps[info.rowIndex];
          let col = row[columnIndex];
          if (col && col.hide && col.label == info.column.label) {
            className.push("ds-design-hide");
          }
        }
      }
      return className.join(" ");
    },
    cellClassName(info) {
      let className = [];
      if (this.isDesign) {
        let columnIndex = info.columnIndex;
        let lastColumns = this.getLastColumns();
        if (this.isShowIndexColumn) {
          columnIndex = columnIndex - 1;
        }
        let col = lastColumns[columnIndex];
        if (col && col.hide) {
          className.push("ds-design-hide");
        }
      }
      return className;
    },
    //更新表头和slot
    refreshHeader() {
      let arr = this.getLastColumns();
      let useSlots = {};
      _.each(arr, (c, i) => {
        let columnId = "table-column-" + i;
        let slot = _.find(this.slots, (s) => s.name == columnId);
        if (!slot) {
          let newSlot = (slot = {
            name: columnId,
            scope: "scope",
            controls: [
              //此处默认追加一层组件,el-table貌似没有行数据加载过程中的控制事件，所以需要控制单元格中的子元素只能通过追加的一层组件完成
              dsf.designer.createComponent(DsfSubTableCellContent, { "slot-name": "default" })
            ]
          });
          this.slots.push(newSlot);
          slot = newSlot;
        } else {
          slot.name = columnId;
        }
        c.columnId = columnId;
        slot.scope = "scope";
        slot.repeat = true;
        slot.textProxy = false;
        slot.dataKey = "row";
        useSlots[slot.name] = 1; //push(slot.name);
      });
      _.eachRight(this.slots, (s) => {
        if (s.name.indexOf("table-column-") == 0 || s.name.indexOf("column-") == 0) {
          if (!useSlots[s.name]) {
            dsf.array.remove(this.slots, s);
          }
        }
      });
    },
    rowDeleteHandler(scope) {
      let fn = this.removeRowBefore;
      let result = dsf.toPromise(
        fn ||
          function (next) {
            next();
          }
      );
      function deletedRow() {
        dsf.layer
          .confirm({
            message: "确认是否要删除？"
          })
          .then(() => {
            this.removeRow(scope.row, true);
          })
          .catch(() => {});
      }
      result
        .then((res) => {
          if (res === true || dsf.isUnDef(res)) {
            deletedRow.call(this);
          }
        })
        .catch(() => {});
    },
    rowEditHandler(scope) {
      this.editBegin(scope.row);
    },
    rowEditCompleteHandler() {
      this.editAllEnd();
    },
    rowEditCancelHandler(scope) {
      let row = scope.row;
      let hash = row.$hash;
      // dsf.mix(scope.row, this.oldData[hash]);
      let oldData = this.oldData[hash];
      for (let k in scope.row) {
        if (dsf.isDef(oldData[k])) {
          scope.row[k] = oldData[k];
        } else {
          scope.row[k] = null;
        }
      }
      this.editAllEnd();
    },
    rowDblClickHander(row) {
      if (!row.$editing) {
        let button = "edit-button-" + row.$hash;
        let buttonEl = this.$el.querySelector("." + button);
        if (buttonEl) {
          buttonEl.click();
        }
        // this.editBegin(row);
      }
    },
    rowUpHandler(scope) {
      let indexInCurrentPage = scope.$index;
      sort.call(this, scope, "up", () => {
        if (indexInCurrentPage == 0 && this.currentPage > 1) {
          this.currentPage--;
        }
      });
    },
    rowDownHandler(scope) {
      let indexInCurrentPage = scope.$index;
      sort.call(this, scope, "down", () => {
        if (indexInCurrentPage >= this.list.length - 1 && this.currentPage > 1) {
          this.currentPage++;
        }
      });
    },
    editBegin(row) {
      if (this.allowEdit) {
        this.foreverEditor || this.editAllEnd();
        // document.addEventListener("click", this.addDocumentClickEndEditHandler);
        // this.editingData = row;
        this.oldData[row.$hash] = dsf.mix(true, {}, row);
        row.$editing = true;
        let subControls = this.getSubControls(row.$hash);
        _.each(subControls, (s) => {
          this.updateSubControls(s.props, s);
        });
      }
    },
    editAllEnd() {
      for (let i = 0; i < this.value.length; i++) {
        let v = this.value[i];
        v.$editing = false;
        let subControls = this.getSubControls(v.$hash);
        _.each(subControls, (s) => {
          this.updateSubControls(s.props, s);
        });
      }
      for (let k in this.oldData) {
        delete this.oldData[k];
      }
    },
    addDocumentClickEndEditHandler() {
      document.removeEventListener("click", this.addDocumentClickEndEditHandler);
      this.editAllEnd();
    },
    updateSubControls(ctrlProps, ctrl) {
      this.addSubControls(ctrl);
      let scope = ctrl.local;
      if (!this.isDesign && scope && ctrlProps.isFormControl) {
        if (ctrlProps.metadata_fullcode) {
          let mcode = ctrlProps.metadata_fullcode;
          let defaultValue = this?.$vm?.$getDefaultValue(mcode);
          if ((scope[mcode] === undefined) & dsf.isDef(defaultValue)) {
            this.$vm.$set(scope, mcode, defaultValue);
          }
        }
        if (this.$vm && this.$vm?.$setControlPropsByWorkFlow) {
          this.$vm.$setControlPropsByWorkFlow(ctrlProps, "formControl");
        }
        if (dsf.isUnDef(ctrlProps.defaultReadOnly)) {
          ctrlProps.defaultReadOnly = ctrlProps.readOnly;
        }
        if (ctrlProps.isFormControl) {
          if (!this.readOnly && this.foreverEditor) {
            scope.$editing = true;
          }
          if (!ctrlProps.defaultReadOnly) {
            //子表只读，则强制全部只读无视一切状态
            if (this.readOnly) {
              ctrlProps.readOnly = true;
            }
            //this.foreverEditor如果强制可写无视编辑状态
            else if (this.foreverEditor) {
              ctrlProps.readOnly = false;
            } else {
              //否则按编辑状态
              ctrlProps.readOnly = !scope.$editing;
            }
            // ctrlProps.readOnly = this.readOnly || !this.foreverEditor && (!scope.$editing);
          }
        }
        return ctrlProps;
      }
    },
    removeSubControls(ctrl, hash) {
      if (ctrl.designId && (hash || ctrl.local.$hash)) {
        let key = ctrl.designId + "-" + (hash || ctrl.local.$hash);
        if (this.subControls[key]) {
          this.subControls[key] = null;
          delete this.subControls[key];
        }
      }
    },
    getSubControls(hash) {
      let sub = [];
      for (let k in this.subControls) {
        if (k.indexOf(hash) > 0) {
          sub.push(this.subControls[k]);
        }
      }
      return sub;
    },
    addSubControls(ctrl) {
      if (ctrl.designId && ctrl.local.$hash) {
        let key = ctrl.designId + "-" + ctrl.local.$hash;
        this.subControls[key] = ctrl;
      }
    },
    editBlurHandler() {
      // console.log("dfsfsfsdf")
    },
    renderTableHandler(col) {
      let fn = this.renderHeader || dsf.noop;
      if (col.hide === true) {
        return false;
      }
      let result = fn(col);
      if (result !== false && col.isLast) {
        dsf.array.ensure(this.outputColumns, col);
      }
      return result;
    },
    resize() {
      this.doLayout();
    }
  },
  beforeDestroy() {
    // if (this.$root.editors) {
    //   dsf.array.remove(this.$root.editors, this);
    // }
  },
  watch: {
    value: {
      handler(v, o) {
        for (let i = 0; i < v.length; i++) {
          !("$editing" in v[i]) && this.$set(v[i], "$editing", false);
          !("$hash" in v[i]) && this.$set(v[i], "$hash", dsf.uuid());
          if ("$order" in v[i]) {
            this.$set(v[i], "$order", i);
          } else {
            v[i]["$order"] = i;
          }
          v[i].$index = i;
        }
        if (o && v) {
          this.emitValueChange(v);
        }
      },
      immediate: true
    },
    columns: {
      handler() {
        this.refreshHeader();
        // this.$refs.table.doLayout();
        this.isReDrawHander = true;
        this.$nextTick(() => {
          this.isReDrawHander = false;
        });
      },
      deep: true
    },
    "elTable.bodyWidth"(v) {
       let wrap=this.elTable.$el.querySelector(".el-table__body-wrapper")
      this.tableWidth = wrap.offsetWidth;
    }
  }
});
function createDesignDefaultColumns() {
  if (!this.isDesign) {
    return;
  }
  if (this.columns.length == 0) {
    let size = 4;
    for (let i = 0; i < size; i++) {
      this.addColumn({
        label: `列${i}`,
        level: 0
      });
    }
  }
}
//排序
function sort(scope, type, callback) {
  let row = this.list[scope.$index];
  let index = _.findIndex(this.value, (r) => r == row);
  let targetIndex = type == "up" ? index - 1 : index + 1;
  let targetRow = this.value[targetIndex];
  if (targetRow) {
    row.$order = targetIndex;
    targetRow.$order = index;
    this.$set(this.value, targetIndex, row);
    this.$set(this.value, index, targetRow);
    callback && callback(scope);
  }
}
</script>
