<script>
import control from "_platform/mixins/control";
/**
 * @class DsfContainer
 * @description 容器
 * @example
 * <DsfContainer target="div">
 *  我是div
 * </DsfContainer>
 */
export default dsf.component({
  /**
   * @memberof DsfContainer
   * @name Slots
   * @property {*} default 主体内容部分
   */
  name: "DsfContainer",
  mixins: [control],
  ctrlCaption: "容器",
  /**
   * @memberof DsfContainer
   * @name Props
   * @property {String} [target = div] 容器标签
   */
  props: {
    target: {
      type: String,
      default: "div",
    },
  },
  created() {},
  render: function (h) {
    //`<target>[子组件]</target>`
    let defaultSlot = this.$slots.default || [];
    let arr = [...defaultSlot];
    let attrs = this.$attrs;
    let target = this.target;
    let el = h(
      target,
      {
        staticClass: "ds-control ds-container",
        attrs: attrs,
      },
      arr
    );
    return el;
  },
  design: {
    parseStaticProps() {
      return {
        target: this.$props.target == "div" ? undefined : this.$props.target,
      };
    },
  },
});
</script>
