<template>
  <div class="ds-control">
    <el-checkbox :indeterminate="isIndeterminate()" v-model="checkAll" class="el-checkbox-column" @change="handleCheckAllChange">全选</el-checkbox>
    <el-checkbox-group v-model="selectedFields" class="el-checkbox-columns">
      <el-checkbox class="el-checkbox-column" v-for="col in columns" :key="col.columnId" :label="col.columnId">{{col.label}}</el-checkbox>
    </el-checkbox-group>
  </div>
</template>
<script>
export default {
  name: "DsfDataGridColumnsFilter",
  data() {
    return {
      form: {},
      checkAll: false,
      selectedFields: []
    };
  },
  computed: {},
  props: {
    columns: {
      type: Array,
      default() {
        return [];
      }
    },
    filter: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  beforeCreate() {},
  created() {
    _.each(this.filter, (c) => {
      this.selectedFields.push(c);
    });
  },
  beforeMount() {},
  mounted() {},
  methods: {
    handleCheckAllChange(val) {
      if (val) {
        this.selectedFields = _.map(this.columns, "columnId");
      } else {
        this.selectedFields.splice(0, this.selectedFields.length);
      }
    },
    isIndeterminate() {
      return this.selectedFields.length > 0 && this.columns.length > this.selectedFields.length;
    },
    yes() {
      let selectedFields = _.map(this.selectedFields, (v) => {
        return _.find(this.columns, (c) => {
          return c.columnId == v;
        });
      });
      selectedFields = _.filter(selectedFields, (v) => v);
      return _.map(selectedFields, "columnId");
    }
  },
  beforeDestroy() {},
  destroyed() {},
  watch: {
    selectedFields: {
      handler(v, ov) {
        if (this.columns.length <= this.selectedFields.length) {
          this.checkAll = true;
        } else {
          this.checkAll = false;
        }
        // this.checkAll = this.selectedFields.length > 0;
      },
      immediate: true
    }
  }
};
</script>
<style lang="scss" scoped>
.el-checkbox-column {
  width: 120px;
  margin-top: 10px;
  margin-bottom: 10px;
  margin-right: 30px;
}
.el-checkbox-columns {
  display: flex;
  flex-direction: row;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
</style>