<template>
  <div v-if="!isDesign" class="ds-control ds-viewpart ds-no-padding" :class="getCss" :path="getPath()" :style="{'background-color':backgroundColor}">
    <component :is="componentName" v-if="componentName" ref="view" :name-space="nameSpace" :page-name="pageName" :query-string="params" v-bind="getProps" @yes="yes"></component>
  </div>
  <div v-else class="ds-control ds-viewpart ds-no-padding" :class="getCss">
    <div class="ds-viewparent-desgine-panel">
      <span>{{ path }}页面片段</span>
    </div>
  </div>
</template>
<script>
import control from "_platform/mixins/control";
import dynamicComponent from "_platform/output/dynamicComponent";
export default dsf.component({
  inheritAttrs: false,
  name: "DsfViewPart",
  ctrlCaption: "页面视图",
  mixins: [control],
  provide() {
    return {
      $frame: this
    };
  },
  props: {
    path: {
      type: String,
      default: ""
    },
    showModel: {
      type: String,
      default: "none"
    },
    backgroundColor: {
      type: String,
      default: ""
    },
    forceUpdate: {
      type: Boolean,
      default: true
    },
    readOnly: {
      type: Boolean,
      default: false
    },
    syncSave: {
      type: Boolean,
      default: false
    },
    isPathParamsPenetrate: {
      type: Boolean,
      default: true
    },
    setProps: {
      type: Function,
      default: null
    },
    isOpenDialog: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      componentName: "",
      nameSpace: "",
      pageName: "",
      params: {}
    };
  },
  beforeCreate() {},
  created() {
    if (this.$vm && this.$vm.frames) {
      this.$vm.frames[this.caption] = this;
    }
  },
  computed: {
    getProps() {
      return !this.setProps ? this.$attrs : this.setProps() || {};
    },
    getCss() {
      return [...this.$getClass(), this.shadow ? "shadow" : "", this.showModel == "full" ? "full" : ""];
    }
  },
  updated() {
    if (this.isDesign) {
      this.$dispatch("design-height-change", this.showModel == "full" ? "100%" : "auto");
    }
  },
  methods: {
    getPath() {
      if (this.path) {
        let path2 = this.path;
        let urlInfo = dsf.url.parse(path2);
        let url = urlInfo.href.replace(urlInfo.search, "");
        let query = [];
        for (let k in urlInfo.searchQuery) {
          query.push(k + "=" + urlInfo.searchQuery[k]);
        }
        if (dsf.isUnDef(urlInfo.searchQuery.isview)) {
          if (this.readOnly) {
            query.push("isview=1");
          }
        }
        url += query.length > 0 ? "?" + query.join("&") : "";
        return this.$replace(url);
      }
      return "";
    },
    reset() {
      this.$refs.view.reset();
    },
    reloadData() {
      this.$refs.view.reloadData();
    },
    analysisUrl() {
      let paramsStart = this.path.indexOf("?");
      var arr = [this.path.substr(0, paramsStart < 0 ? this.path.length : paramsStart), paramsStart < 0 ? "" : this.path.substr(paramsStart + 1)];
      var path = arr[0];
      path = path.startsWith("/") ? path.substr(1) : path;
      var params = arr[1];
      let pathArr = path.split("/");
      let ns = path.replace(/\//g, ".");
      let nameSpace = pathArr.slice(0, pathArr.length - 1).join(".");
      let pageName = pathArr[pathArr.length - 1];
      let parseResult = dsf.url.queryStringParse(params);
      //动态替换表达式
      let localParams = {};
      _.each(parseResult, (param, i) => {
        localParams[i] = this.$replace(param);
      });
      let mergeParams = {};
      if (this.isOpenDialog) {
        //id参数为表单特殊参数在弹出层时不会默认继承给子页面
        mergeParams = dsf.mix({}, this.queryString, { id: null }, localParams);
      } else {
        mergeParams = dsf.mix({}, this.queryString, localParams);
      }
      if (this.readOnly) {
        mergeParams.isview = "1";
      }
      //如果id为null或者undefined清除id参数
      if (dsf.isUnDef(mergeParams.id)) {
        delete mergeParams.id;
      }
      return {
        ns: ns,
        nameSpace: nameSpace,
        pageName: pageName,
        params: mergeParams
      };
    },
    async yes() {
      if (this.$refs.view.yes) {
        return await this.$refs.view.yes();
      }
    }
  },
  watch: {
    path: {
      handler(v) {
        if (v && !this.isDesign) {
          let urlInfo = this.analysisUrl();
          let ns = urlInfo.ns;
          this.nameSpace = urlInfo.nameSpace;
          this.pageName = urlInfo.pageName;
          this.params = urlInfo.params;
          dynamicComponent(ns, "pc")
            .then((result) => {
              if (this.componentName == result.name) {
                if (this.forceUpdate) {
                  this.componentName = "";
                  this.$nextTick(() => {
                    this.componentName = result.name;
                  });
                } else {
                  this.reset();
                  this.reloadData();
                }
              } else {
                this.componentName = "";
                this.$nextTick(() => {
                  this.componentName = result.name;
                });
              }
            })
            .catch((error) => {
              dsf.layer.pc.message("无法加载组件", false);
            });
        }
      },
      immediate: true
    },
    "$route.query"(to, from) {
      let urlInfo = this.analysisUrl();
      this.params = urlInfo.params;
    }
  }
});
</script>