<template>
  <div class="page-home-template1-header">
    <div class="page-home-template1-header-info">
      <div class="logo-box">
        <slot name="logo">
          <img v-if="logo" :src="logo" alt="" class="logo">
          <div v-if="title && title.length" class="site-name">{{ title }}</div>
        </slot>
      </div>
      <div class="header-right">
        <div class="header-user">
          <div class="header-user-info">
            <div class="username">{{ dsf.cookies.get("user_name") }}, 你好</div>
            <div class="welcome">欢迎进入{{ title }}</div>
          </div>
          <div v-if="!dropdownConfig.length" class="header-user-icon" :style="{'background-image': `url(${defaultHeader})`}">
            <img v-show="showIcon" :src="headIcon" @error="headLoadError" @load="headLoadSuccess" />
          </div>
          <el-dropdown v-else :show-timeout="0" placement="bottom" @visible-change="showDropdown = $event" @command="handleCommand">
            <div class="header-user-icon" :style="{'background-image': `url(${defaultHeader})`}">
              <img v-show="showIcon" :src="headIcon" @error="headLoadError" @load="headLoadSuccess" />
            </div>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item v-for="(item, index) in dropdownConfig" :key="(item.key || '') + '__' + index" :command="index">
                {{ item.name }}
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
    <div class="page-home-template1-header-menu">
      <dsf-template1-home-menu :last-level-click="lastLevelClick"></dsf-template1-home-menu>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DsfTemplate1HomeHeader',
  inject: ['homeRoot'],
  props: {
    dropdownConfig: {
      type: Array,
      default() {
        return []
      }
    },
    // 是否只响应最后一级菜单的点击事件
    lastLevelClick: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      logo: dsf.config.setting_public_logo_image,
      title: dsf.config.setting_public_logo_title,
      headIcon: '',
      defaultHeader: dsf.config.setting_public_user_default_header,
      showIcon: false,
      showDropdown: false
    }
  },
  watch: {
    '$root.setting.fontSize'(to) {
      this.homeRoot.setPosition('top', to == 'bigFont' ? '140px' : '136px');
    }
  },
  created() {
    this.homeRoot.setPosition('top', this.$root.setting.fontSize == 'bigFont' ? '140px' : '136px');
    this.initHeadIcon();
  },
  methods: {
    // 初始化头像，从cookie中获取
    initHeadIcon() {
      const headIcon = dsf.getCookie('photoRelativePath');
      if (headIcon) {
        this.headIcon = decodeURIComponent(headIcon);
      }
    },
    // 头像加载成功时
    headLoadSuccess() {
      this.showIcon = true;
    },
    // 头像加载失败时
    headLoadError() {
      this.showIcon = false;
    },
    // 用户头像下的菜单点击
    handleCommand(index) {
      const item = this.dropdownConfig[+index];
      this.$emit('dropdown-click', item);
    }
  }
}
</script>