<template>
  <div class="ds-control dsf-drop"><slot></slot></div>
</template>
<script>
import control from "_platform/mixins/control";
import { jqui } from '_platform/pc/common/importJqueryUI';
export default dsf.component({
  name: "DsfDrop",
  ctrlCaption: "放置组件",
  mixins: [control],
  components: {},
  provide() {
    return {};
  },
  filters: {},
  props: {
    // 边界标识
    range: {
      type: String,
      default() {
        return "dragrange";
      },
    },
    // 是否禁用
    disabled: {
      type: Boolean,
      default() {
        return false;
      },
    },
  },
  data() {
    return {
      droppableDom: null,
      offset: {
        top: 0,
        left: 0,
      },
      dragRanges: {},
    };
  },
  watch: {
    disabled: {
      handler(state) {
        let { droppableDom } = this;
        if (droppableDom)
          state ? droppableDom.disable() : droppableDom.enable();
      },
    },
  },
  methods: {
    getDragRange() {
      let parent = this.$parent;
      while (parent) {
        if (parent.ctrlType == "dsf.dragrange") {
          parent.drops[this.id] = this;
          this.dragRanges[parent.name] = parent;
        }
        parent = parent.$parent;
      }
      return null;
    },
  },
  created() {
    this.id = dsf.UUID();
    this.getDragRange();
  },
  beforeMount() {},
  mounted() {
    
    jqui.then(async () => {
    let jqDom = $(this.$el);
    this.offset = jqDom.offset() || {};
    this.offset = {
      top: this.offset.top || 0,
      left: this.offset.left || 0,
    };
    this.size = {
      width: jqDom.width() || 0,
      height: jqDom.height() || 0,
    };
    this.matrix = {
      top: this.offset.top,
      left: this.offset.left,
      right: this.offset.left + this.size.width,
      buttom: this.offset.top + this.size.height,
    };
    this.droppableDom = jqDom.droppable({
      disabled: this.disabled,
      tolerance: "pointer",
      hoverClass: "dsf-drop-hover",
      addClasses: false,
      create: (e, ui) => {
        this.$emit("create", e, ui);
      },
      drop: (event, ui) => {
        this.$emit("drop", event, ui);
      },
      over: (event, ui) => {
        this.$emit("over", event, ui);
      },
      out: (event, ui) => {
        this.$emit("out", event, ui);
      },
    });
 
    }); },
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  design: {},
});
</script>
