<template>
  <div class="header-menu" :style="menuStyle">
    <div v-show="showBt" class="header-menu-bt __right" @click="doPagination(-1)">
      <i class="iconfont icon-ln_jiantouyou"></i>
    </div>
    <div v-show="showBt" class="header-menu-bt __left" @click="doPagination(1)">
      <i class="iconfont icon-ln_jiantouzuo"></i>
    </div>
    <div ref="box" class="header-menu-box">
      <div ref="content" class="header-menu-content"><div
        v-for="(item, index) in menuList"
        :key="item.menu_id"
        :class="{ active: item.menu_id === activeKey }"
        class="header-menu-item"
        @click="linkTo(item, index)"
      >
        <div class="header-menu-item-box">
          <i :class="[ item.menu_icon ]" class="iconfont"></i>
          <div>{{ item.menu_name }}</div>
        </div>
      </div></div>
    </div>
  </div>
</template>

<script>
import menuUtils from "_platform/pc/home/menu/utils";

let timer = false;

export default {
  name: 'DsfDefaultHomeMenuBar',
  inject: ['homeRoot'],
  props: {
    menuList: {
      type: Array,
      default() {
        return []
      }
    },
    activeKey: {
      type: String,
      default: null
    },
    width: {
      type: Number,
      default: 0
    },
    mini: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      showBt: false
    }
  },
  watch: {
    'width'() {
      this.reSize();
    }
  },
  computed: {
    menuStyle() {
      let res = {};
      if (this.mini) {
        res['width'] = this.width + 'px';
      } else {
        res['max-width'] = this.width + 'px';
      }
      if (!this.showBt) {
        res.padding = 0;
      }
      return res;
    }
  },
  methods: {
    reSize(){
      this.$nextTick(() => {
        let boxw = this.$refs.box.offsetWidth;
        let conw = this.$refs.content.scrollWidth;
        this.showBt = conw > boxw;
      });
    },
    linkTo(item, index) {
      if (this.activeIndex === index) return;
      menuUtils.openPage.call(this.homeRoot, menuUtils.findMenu(item));
    },
    doPagination(type) {
      if (timer) {
        return;
      }
      timer = true;
      let itemw = this.$refs.content.querySelector(".header-menu-item").offsetWidth;
      let boxw = this.$refs.box.offsetWidth;
      let conw = this.$refs.content.scrollWidth;
      let scrollLeft = this.$refs.box.scrollLeft;
      let l = (parseInt(boxw / itemw) * itemw) || itemw;
      let xx = conw - boxw - scrollLeft;
      if (type < 0 && xx > 0) {
        l = l  < xx ? l : xx;
        l -= scrollLeft % itemw;
        l += scrollLeft;
        $(this.$refs.box).animate({scrollLeft: l}, 300)
      } else if (type > 0 && scrollLeft > 0) {
        l = l  < scrollLeft ? l : scrollLeft;
        l -= xx % itemw;
        l = scrollLeft - l;
        $(this.$refs.box).animate({scrollLeft: l}, 300)
      }
      setTimeout(() => {
        timer = false;
      }, 300);
    }
  }
}
</script>