<template>
  <li class="ds-aside-menu-item" :class="{active: isActive, show: showChildren}">
    <div :style="{'padding-left': level * 20 + 'px'}" class="ds-aside-menu-item-box" @click="clickEvent">
      <i v-if="level === 1" :class="[menu.menu_icon]" class="ds-aside-menu-item-icon iconfont" />
      <span class="ds-aside-menu-item-name">{{ menu.menu_name }}</span>
      <i v-if="menu.children" :class="[{show: showChildren}, showChildren ? 'icon-xia1' : 'icon-zuo' ]" class="iconfont ds-aside-menu-item-show" />
    </div>
    <div v-if="menu.children" v-show="showChildren">
      <dsf-default-home-menu :menu-list="menu.children" :level="level + 1"></dsf-default-home-menu>
    </div>
  </li>
</template>
<script>
import menuUtils from "../../utils";

export default {
  name: 'DsfDefaultHomeMenuItem',
  inject: ['homeRoot', 'homeAside'],
  props: {
    menu: {
      type: Object,
      required: true
    },
    level: {
      type: Number,
      required: true
    },
    index: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      isActive: false,
      showChildren: false,
      parent: {},
    }
  },
  mounted() {
    this.parent = this.$parent;
  },
  methods: {
    clickEvent() {
      if (this.menu.children) {
        this.showChildren = !this.showChildren;
        if (this.showChildren) {
          this.homeAside.setShow(this, true);
        } else {
          this.homeAside.setShow(this, false);
          this.showChildren = false;
        }
      } else {
        this.linkTo(this.menu);
      }
    },
    linkTo(item) {
      menuUtils.openPage.call(this.homeRoot, menuUtils.findMenu(item));
    }
  }
}
</script>