<script>
import layout from "../../../mixins/layout";
// import DsfSubTableCellContent from './subTableCellContent';
export default dsf.component({
  name: "DsfSubTableColumn",
  inheritAttrs: false,
  mixins: [layout],
  ctrlCaption: "子表列",
  props: {
    column: {
      type: Object,
      default() {
        return {
          label: "",
          children: []
        };
      }
    },
    columns: {
      type: Array,
      default() {
        return [];
      }
    }
    // totalWidth: {
    //   type: Number,
    //   default: 0
    // }
  },
  provide() {
    return {
      $tableColumn: this
    };
  },
  data() {
    return {
      // realWidth: this.column.width
      tableWidth: 0
    };
  },
  created() {},
  computed: {
    ownerTable() {
      let parent = this.$parent;
      while (parent.$options.name != "DsfSubTable") {
        parent = parent.$parent;
      }
      return parent;
    },
    bodyWidth() {
      return this.elTable.bodyWidth;
    },

    elTable() {
      return this.$parent;
    }
  },
  beforeMount() {},
  mounted() {},
  methods: {
    getWidth(column) {
      let width = column.width;
      let totalWidth = this.ownerTable.tableWidth;
      if (totalWidth) {
        if (dsf.type(width) == "string" && width && width.endsWith("%")) {
          let w = parseFloat(width.replace("%", "")) / 100;
          let indexColumnWidth = this.ownerTable.isShowIndexColumn ? 60 : 0;
          let operateColumnWidth = this.ownerTable.isShowOperateColumn ? 130 : 0;
          let tw = totalWidth - indexColumnWidth - operateColumnWidth;
          width = parseFloat(tw) * w;
        }
      }
      return width;
    }
  },
  updated() {
    console.log("ddd");
  },
  render(h) {
    // let table=this.ownerTable;
    return renderHeader.call(this, h, this.column, this.$vnode.data.key);
  }
  // watch: {
  //   "ownerTable.totalWidth"(v, ov) {
  //     if (v != ov) {
  //       return this.ownerTable.totalWidth;
  //     }
  //   }
  // }
});

function renderHeader(h, column, key) {
  let _this = this;
  let child = [];
  let scopedSlots = null;
  if (column.children && column.children.length > 0) {
    child = _.map(column.children, (c, i) => {
      return renderHeader.call(this, h, c, i);
    });
  } else {
    child = this.$slots[column.columnId];
    scopedSlots = _this._u(
      [
        {
          key: "header",
          fn(scope) {
            return [
              column.required
                ? h("i", {
                    staticClass: "required",
                    staticStyle: { "margin-right": "5px" }
                  })
                : _this._e(),
              h("span", {
                domProps: {
                  innerHTML: `${column.prefix || ""}${column.label}${column.suffix || ""}`
                }
              })
            ];
          }
        },
        {
          key: "default",
          fn(scope) {
            return [_this._t(column.columnId, null, null, scope)];
          }
        }
      ],
      null,
      true
    );
  }
  let props = { ...column };
  delete props.width;
  let data = {
    key: key,
    props: { ...props },
    attrs: {
      "class-name": column.columnId,
      columnKey: column.columnId,
      ...this.$attrs,
      minWidth: props.minWidth,
      width: this.getWidth(column)
      // sortable: this.sortable
    }
  };
  if (scopedSlots) {
    data.scopedSlots = scopedSlots;
  }
  if (this.ownerTable) {
    let vnode = h("el-table-column", data, child);
    return vnode;
  }
  return null;
}
</script>
