<template>
  <el-table v-if="isDesign" ref="table" :border="true" :fit="true" :data="data" :height="tableHeight" :header-cell-class-name="headerCellClassName" stripe>
    <el-table-column v-if="checkboxColumn.show" type="selection" label="" :width="checkboxColumn.width" header-align="center" align="center"></el-table-column>
    <el-table-column v-if="indexColumn.show" :type="'index'" :index="$list.indexMethod" :label="'序号'" :resizable="false" :width="indexColumn.width" :header-align="'center'" :align="'center'"></el-table-column>
    <slot>
      <template v-if="!isReDrawHander">
        <DsfDataGridColumn v-for="(col, $index) in columns" header-cell-class-name="" ref="header" :key="$index" :column="col" :sortable="getColumnSort(col)">
          <template v-for="column in getLastColumns(col)" v-slot:[column.columnId]="scope">
            <slot :name="column.columnId" v-bind="scope"></slot>
          </template>
        </DsfDataGridColumn>
      </template>
    </slot>
    <el-table-column v-if="operateColumn.show" label="操作" :width="operateColumn.width" header-align="center" align="center">
      <template v-slot:default="scope">
        <DsfButtonBar size="small" :max-number="rowButtonsMaxNumber" :more-button-text="rowButtonsMoreText" :more-button-style="rowButtonsMoreStyle" class="ds-no-padding ds-no-drop" :is-design="isDesign" :buttons="scope._self.rowButtons" align="center"></DsfButtonBar>
      </template>
    </el-table-column>
  </el-table>
  <el-table
    v-else
    ref="table"
    :border="true"
    :fit="true"
    :data="data"
    :height="tableHeight"
    :row-key="rowKey"
    :header-cell-class-name="headerCellClassName"
    :span-method="spanMethod"
    :render-header="renderHeader"
    stripe
    @select="handleSelection"
    @select-all="handleSelectionAll"
    @header-dragend="headerDragend"
    @row-dblclick="rowDblClickHandler"
    @sort-change="tableSort"
    @selection-change="handleSelectionChange">
    <el-table-column v-if="checkboxColumn.show && !isSingleChecked" :selectable="selectable" :reserve-selection="true" :width="checkboxColumn.width" type="selection" header-align="center" align="center" fixed></el-table-column>
    <el-table-column v-if="checkboxColumn.show && isSingleChecked" :reserve-selection="true" :width="checkboxColumn.width" header-align="center" align="center" fixed>
      <!--列表单选 start-->
      <template v-slot:header>
        <el-checkbox disabled></el-checkbox>
      </template>
      <template v-slot:default="scope">
        <el-radio class="table-selection-radio" :value="multipleSelection && multipleSelection._id || null" :label="scope.row._id" :disabled="selectable && selectable(scope.row, scope.$index)" @input="multipleSelection = scope.row"></el-radio>
      </template>
      <!--列表单选  end-->
    </el-table-column>
    <el-table-column
      v-if="indexColumn.show"
      type="index"
      label="序号"
      :width="indexColumn.width"
      resizable
      header-align="center"
      :index="$list.indexMethod"
      align="center"
      fixed>
      <template v-slot:default="scope">
        <div :title="$list.indexMethod(scope.$index)">{{$list.indexMethod(scope.$index)}}</div>
      </template>
    </el-table-column>
    <slot>
      <template v-if="!isReDrawHander">
        <template v-for="(col, $index) in columns">
          <DsfDataGridColumn v-if="getColumnRenderResult(col,true)!==false" ref="header" :key="$index" :column="col" :sortable="getColumnSort(col)">
            <template v-for="column in getLastColumns(col)" v-slot:[column.columnId]="scope">
              <slot :name="column.columnId" v-bind="scope"></slot>
            </template>
          </DsfDataGridColumn>
        </template>
      </template>
    </slot>
    <el-table-column v-if="operateColumn.show" label="操作" :width="operateColumn.width" header-align="center" align="center" fixed="right">
      <template v-slot:default="scope">
        <slot name="buttonColumns" v-bind="scope">
          <DsfButtonBar
            size="small"
            :max-number="rowButtonsMaxNumber"
            :more-button-text="rowButtonsMoreText"
            :more-button-style="rowButtonsMoreStyle"
            :buttons="getRowButtons(scope._self.rowButtons)"
            :emit-prefix="'row-button'"
            :local="scope.row"
            class="ds-no-padding"
            align="center">
          </DsfButtonBar>
        </slot>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import layout from "_platform/mixins/layout";

export default dsf.component({
  name: "DsfDataGridTable",
  ctrlCaption: "数据列表表格",
  mixins: [layout],
  inject: {
    $list: {
      default: null
    }
  },
  props: {
    selectable: {
      type: Function,
      required: false
    },
    columns: {
      type: Array,
      default() {
        return [];
      }
    },
    data: {
      type: Array,
      default() {
        return [];
      }
    },
    isReDrawHander: {
      type: Boolean,
      default: false
    },
    rowButtons: {
      type: Array,
      default() {
        return [];
      }
    },
    rowButtonsMaxNumber: {
      type: [Number, String],
      default: 4
    },
    // 数据行更多按钮形态
    rowButtonsMoreStyle: {
      type: String,
      default: "icon-text"
    },
    // 数据行更多按钮文字
    rowButtonsMoreText: {
      type: String,
      default: "更多"
    },
    tableHeight: {
      type: String,
      default() {
        return "100%";
      }
    },
    stripe: {
      type: Boolean,
      default: true
    },
    slots: {
      type: Array,
      default() {
        return [
          {
            name: "default",
            controls: []
          }
        ];
      }
    },
    checkboxColumn: {
      type: Object,
      default() {
        return {};
      }
    },
    indexColumn: {
      type: Object,
      default() {
        return {};
      }
    },
    operateColumn: {
      type: Object,
      default() {
        return {};
      }
    },
    border: {
      type: Boolean,
      default: true
    },
    fit: {
      type: Boolean,
      default: true
    },
    isSingleChecked: {
      type: Boolean,
      default: false
    },
    rowKey: {
      type: [String, Function],
      default: ""
    },
    headerCellClassName: {
      type: [String, Function],
      default: ""
    },
    // 合并行或列的计算方法。
    // Function({ row, column, rowIndex, columnIndex })
    spanMethod: {
      type: Function,
      required: false
    },
    customerOrder: {
      type: Array,
      default() {
        return [];
      }
    },
    renderHeader: {
      type: Function,
      default: dsf.noop
    }
  },
  data() {
    return {
      tableWidth: 0,
      elTable: null,
      filterResult: [],
      multipleSelectionCopy: null
    };
  },
  computed: {
    lastColumns() {
      let columns = this.columns;
      return this.getLastColumns() || [];
    },
    //列表真实显示的列
    realColumns() {
      let cols = this.lastColumns;
      return _.filter(cols, (c) => {
        return this.getColumnRenderResult(c);
      });
    },
    //用户通过列表列过滤后的结果
    filterColumns() {
      let cols = this.realColumns;
      let filterCols = this.filterResult;
      return _.filter(cols, (col) => {
        return this.filterResult.indexOf(col.columnId) >= 0;
      });
    },
    multipleSelection: {
      get() {
        if (this.$list) {
          return this.$list.multipleSelection;
        } else {
          return this.multipleSelectionCopy;
        }
      },
      set(val) {
        if (this.$list) {
          this.$list.multipleSelection = val;
        } else {
          this.multipleSelectionCopy = val;
        }
      }
    }
  },
  mounted() {
    this.elTable = this.$refs.table;
    //非设计模式下将用户过滤列结果从storage加载出来，为null则加载全部列
    this.filterResult = _.map(this.realColumns, "columnId");
    if (!this.isDesign && this.$list && this.$list.designId) {
      let filter = dsf.storage.session.get(this.$list.designId + "-column-filter");
      if (filter && filter.length > 0) {
        this.filterResult = filter;
      }
    }
  },
  updated() {
    this.doLayout();
  },
  methods: {
    getColumnRenderResult(col, isFilter) {
      let result = true;
      if (!col.hide) {
        let result = this.renderHeader(col);
        result === false ? false : true;
      } else {
        result = false;
      }
      if (result != false && col.isLast && isFilter) {
        result = this.filterResult.indexOf(col.columnId) >= 0;
      }
      return result;
    },
    //获取列头中各个单元格最大的单元格
    getLastColumns(column) {
      let arr = [];
      this.deep((c) => {
        if (!c.children || !c.children.length) {
          c.isLast = true;
          arr.push(c);
        } else {
          c.columnId = "";
          c.isLast = false;
        }
      }, column);
      return arr;
    },
    deep(callback, column) {
      var fn = function (columns, level) {
        let columnIndex = 0;
        _.each(columns, (c, i) => {
          callback && callback(c, i, columnIndex, level);
          fn(c.children, level + 1);
          columnIndex++;
        });
      };
      let cols = column ? column : this.columns;
      cols = dsf.type(cols) !== "array" ? [cols] : cols;
      fn(cols, 0);
    },
    doLayout() {
      this.elTable = this.$refs.table;
      if (this.elTable) {
        this.elTable.doLayout();
      }
    },
    resize() {
      this.doLayout();
    },
    // elTable 多选监听(单个选项被勾选)
    handleSelection(val, row) {
      this.$emit("select", val, row);
    },
    // elTable 多选监听(全选)
    handleSelectionAll(val) {
      this.$emit("select-all", val);
    },
    headerDragend() {
      //更新一下列表的布局，否则宽高会出问题
      this.doLayout();
    },
    rowDblClickHandler(row, column, event) {
      // this.$emit("row-dblclick", row, column, event);
      let tr = event.currentTarget;
      let buttonbarEl = $(tr).find("td:last div[ctrl_type='dsf.buttonbar']");
      if (buttonbarEl.length > 0) {
        let buttonbar = buttonbarEl.get(0).__vue__;
        let dblClickButtons = _.filter(buttonbar.buttons, (btn) => {
          return btn.isDblClick;
        });
        if (dblClickButtons.length > 0) {
          buttonbar.triggerButtonClick(dblClickButtons[0]);
        }
      }
    },
    tableSort(args) {
      this.$emit("sort-change", args);
    },
    getRowButtons(buttons) {
      return dsf.mix(true, [], buttons || []);
    },
    clearSelection() {
      this.elTable && this.elTable.clearSelection();
    },
    toggleRowSelection(row, selected) {
      this.elTable && this.elTable.toggleRowSelection(row, selected);
    },
    getColumnSort(column) {
      if (this.customerOrder) {
        let result = _.filter(this.customerOrder, (col) => {
          if (column.columnId == col.column) {
            return true;
          }
        });
        if (result.length > 0) {
          return "custom";
        }
      }
      return false;
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    }
  },
  watch: {
    "elTable.bodyWidth": {
      handler(v, ov) {
        if (this.elTable) {
          let wrap = this.elTable.$el.querySelector(".el-table__body-wrapper");
          this.tableWidth = wrap.offsetWidth;
        }
      },
      immediate: true
    }
  }
});
</script>