export default dsf.component({
  name: "DsfCascader",
  mixins: [formControl],
  ctrlCaption: "下拉框级联",
  props: {
    simple: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: "请选择",
    },
    // 值
    value: {
      type: [Object, Array],
      required: false
    },
    // 选项
    items: {
      type: Array,
      default() {
        return [];
      }
    },
    filterable: {
      type: Boolean,
      default: false,
      desc: "是否可搜索"
    },
    filterMethod: {
      type: Function,
      required: false,
      desc: "自定义搜索方法"
    },
    clearable: {
      type: Boolean,
      default: true,
      desc: "是否支持选项清空"
    },
    showAllLevels: {
      type: Boolean,
      default: true,
      desc: "输入框中是否显示选中值的完整路径"
    },
    collapseTags: {
      type: Boolean,
      default: false,
      desc: "多选模式下是否折叠Tag"
    },
    // props
    expandTrigger: {
      type: String,
      default: 'click',
      desc: "次级菜单的展开方式 click / hover"
    },
    multiple: {
      type: Boolean,
      default: false,
      desc: "是否多选"
    },
    checkStrictly: {
      type: Boolean,
      default: false,
      desc: "是否可选择任意一级"
    },
    lazy: {
      type: Boolean,
      default: false,
      desc: "是否懒加载"
    },
    lazyLoad: {
      type: Function,
      required: false,
      desc: "加载动态数据的方法"
    },
    propsValue: {
      type: String,
      default: 'value',
      desc: "指定选项的值为选项对象的某个属性值"
    },
    propsLabel: {
      type: String,
      default: 'text',
      desc: "指定选项标签为选项对象的某个属性值"
    },
    propsChildren: {
      type: String,
      default: 'children',
      desc: "指定选项的子选项为选项对象的某个属性值"
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("items-meta-data");
      },
    },
  },
  data() {
    return {
      valueCopy: null,
      isFocus: false
    };
  },
  computed: {
    bindAttr() {
      let res = {};
      if (this.filterMethod) {
        res.filterMethod = this.filterMethod.bind(this);
      }
      return res;
    },
    myProps() {
      let res = {
        expandTrigger: this.expandTrigger === 'click' ? 'click' : 'hover',
        checkStrictly: this.checkStrictly,
        multiple: this.multiple,
        value: this.propsValue,
        label: this.propsLabel,
        children: this.propsChildren,
        lazy: this.lazy
      };
      if (this.lazyLoad) {
        res.lazyLoad = this.lazyLoad.bind(this);
      }
      return res
    },
    $items() {
      if (this.isDesign) {
        return [];
      }
      return this.items || [];
    },
    inputValue: {
      get() {
        if (dsf.isUnDef(this.value)) {
          return this.multiple ? [] : null;
        }
        if (this.multiple) {
          if (dsf.type(this.value) == "array") {
            return this.value.map(v => v.value.split('/'));
          } else if (dsf.type(this.value) == "object") {
            return [this.value.value.split('/')];
          }
        } else {
          if (dsf.type(this.value) == "array") {
            return this.value[0].value.split('/');
          } else if (dsf.type(this.value) == "object") {
            return this.value.value.split('/');
          }
        }
        return this.multiple ? [] : null;
      },
      set(v) {
        if (!v || !v.length) {
          this.emitValueChange(this.multiple ? [] : null);
          return;
        }
        if (!this.multiple) {
          let val = this.getValueByPath(v);
          this.emitValueChange(val);
        } else {
          let val = v.map(v2 => this.getValueByPath(v2));
          this.emitValueChange(val);
        }
      }
    }
  },
  watch: {
    'readOnly' (to) {
      if (!to) {
        this.bindInputChange();
      } else {
        this.input && this.input.removeEventListener('input', this.inputChange);
      }
    }
  },
  mounted() {
    if (!this.isDesign && !this.simple && !this.readOnly) {
      this.bindInputChange();
    }
  },
  beforeDestroy() {
    this.input && this.input.removeEventListener('input', this.inputChange);
  },
  methods: {
    bindInputChange() {
      this.$nextTick(() => {
        if (this.$refs.elCascader) {
          this.input = this.$refs.elCascader.$el.querySelector('.el-input__inner');
          this.input.addEventListener('input', this.inputChange);
        }
      })
    },
    inputChange() {
      if (this.timeout != undefined) {
        clearTimeout(this.timeout);
      }
      this.timeout = setTimeout(() => {
        this.$dispatch("input-change", this.input.value);
      }, 300);
    },
    getValueByPath(path) {
      let text = [], value = [];
      let items = this.$items;
      for (let i = 0; i < path.length; i++) {
        let p = path[i];
        if (items == null) {
          return null;
        } else {
          for (let i = 0; i < items.length; i++) {
            let item = items[i];
            if (item[this.propsValue] === p) {
              value.push(p);
              text.push(item[this.propsLabel]);
              if (item[this.propsChildren]) {
                items = item[this.propsChildren];
              } else {
                items = null;
              }
              break;
            }
          }
        }
      }
      return {
        text: text.join('/'),
        value: value.join('/')
      }
    },
    change() {
      this.$dispatch("change", this.value);
      this.$nextTick(() => {
        this.emitFormValidate(this.value);
      })
    },
    blur(...arg){
      this.isFocus = false;
      this.$dispatch("blur", ...arg);
    },
    focus(...arg){
      this.isFocus = true;
      this.$dispatch("focus", ...arg);
    },
    // 下拉框隐藏时
    visibleChange(...arg) {
      this.$dispatch("visible-change", ...arg);
    },
    // 在多选模式下，移除Tag时触发
    removeTag(...arg) {
      this.$dispatch("remove-tag", ...arg);
    }
  },
});
</script>
