<template>
  <div class="ds-personalization" :class="{show: show}">
    <div v-if="show" class="ds-personalization-editor">
      <div class="ds-personalization-editor-title">
        个性化设置
      </div>
      <el-form label-width="100px">
        <el-form-item label="主题色：">
          <div class="theme-radio-list">
            <template v-for="(theme, key) in themes">
              <div :key="key" class="theme-radio-item" :style="radioStyle(theme, key)" @click="radioClick(key)"></div>
            </template>
          </div>
        </el-form-item>
        <el-form-item label="字体大小：">
          <el-radio-group v-model="themeConfig.fontSize">
            <el-radio label="normal">正常</el-radio>
            <el-radio label="bigFont">大号</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item v-if="hasDefaultHeader" label="顶部导航栏：">
          <el-radio-group v-model="themeConfig.defaultHeaderMini">
            <el-radio :label="false">宽条</el-radio>
            <el-radio :label="true">窄条</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <div style="text-align: center">
        <dsf-button text="关 闭" size="small" @click="$emit('update:show', false)"></dsf-button>
      </div>
    </div>
  </div>
</template>

<script>
import themes from "_project/assets/styles/common";

export default {
  name: "DsfPersonalization",
  inject: ['homeRoot'],
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      themes,
      left: window.innerWidth,
      top: window.innerHeight / 2,
      hasDefaultHeader: false,
      themeConfig: {
        theme: null,
        fontSize: null,
        defaultHeaderMini: null
      }
    }
  },
  computed: {
    style() {
      return {
        top: this.top + "px",
        left: this.left + "px"
      };
    }
  },
  watch: {
    'themeConfig.theme' (to) {
      this.$root.setTheme(to);
    },
    'themeConfig.fontSize' (to) {
      this.$root.setFontSize(to);
    },
    'themeConfig.defaultHeaderMini' (to) {
      this.$root.setDefaultHeaderMini(to);
    }
  },
  created() {
    this.$header = this.homeRoot?.$slots?.header[0];
    if (this.$header && this.$header.elm.classList.contains('page-home-default-header')) {
      this.hasDefaultHeader = true;
    }
    this.themeConfig = {
      theme: this.$root.setting.theme,
      fontSize: this.$root.setting.fontSize,
      defaultHeaderMini: this.$root.setting.defaultHeaderMini
    };
  },
  mounted() {
    document.body.appendChild(this.$el);
  },
  methods: {
    radioStyle({main, normal}, key) {
      let isActive = key == this.themeConfig.theme;
      return {
        "border-color": isActive ? normal : '#ccc',
        "background-image": `linear-gradient(45deg, ${main} 30%, ${normal} 70%)`
      };
    },
    radioClick(key) {
      this.$set(this.themeConfig, "theme", key);
    }
  }
}
</script>