<template>
  <div class="ds-control ds-page " :class="getCss" :style="pageStyle">
    <!--页头-->
    <div class="ds-page-head">
      <div ref="head" class="head-inner" :slot-name="slots[1].name">
        <slot :name="slots[1].name"></slot>
      </div>
    </div>
    <div :ref="'conent'" class="ds-page-content">
      <div class="ds-page-content-wrap" :style="pageContentStyle">
        <div class="ds-page-left" :style="leftStyle" :slot-name="slots[3].name">
          <slot :name="slots[3].name"></slot>
        </div>
        <div class="ds-page-center" :slot-name="slots[0].name" :style="conentStyle">
          <slot :name="slots[0].name">
          </slot>
        </div>
        <div class="ds-page-right" :style="rightStyle" :slot-name="slots[4].name">
          <slot :name="slots[4].name"></slot>
        </div>
      </div>
    </div>
    <!--页脚-->
    <div v-if="hasFooter" class="ds-page-foot" :style="{'background-color':footerBackground}">
      <div ref="foot" class="foot-inner" :slot-name="slots[2].name">
        <slot :name="slots[2].name"></slot>
      </div>
    </div>
    <div style="display:none" v-html="customCss"></div>
    <a v-show="!isDesign" v-if="gotoIcon" ref="eleDesgin" v-drag data-flag="true" :title="`进入${title}设计模式`" class="to_desgin" @click="gotoPreview">
      <i class="iconfont icon-shezhi"></i>
    </a>
  </div>
</template>
<script>
import view from "_platform/mixins/view";
import vDrag from "_platform/mixins/vDrag";
// import schema from 'async-validator';
// import container from '_platform/pc/bhc/container/container';
export default dsf.component({
  name: "DsfPage",
  ctrlCaption: "页面",
  mixins: [view, vDrag],
  // provide() {
  //   return {
  //     // $page: this,
  //     // $formItemLabelWidth: this.formItemLabelWidth,
  //   }
  // },
  data() {
    return {
      gotoIcon: dsf.config.setting_public_gotoIcon == "1",
      headHeight: 0,
      footHeight: 0,
      centerHeight: "auto",
      headerStyle: {},
      footerStyle: {},
      contentHeight: 0
    };
  },
  props: {
    formItemLabelWidth: {
      type: Number,
      default: 120
    },
    needLogin: {
      type: Boolean,
      default: true
    },
    slots: {
      type: Array,
      default() {
        return [
          { name: "center", controls: [] },
          { name: "head", controls: [] },
          { name: "foot", controls: [] },
          { name: "left", controls: [] },
          { name: "right", controls: [] }
        ];
      }
    }
  },
  beforeCreate() {
    // console.time("加载页面");
  },
  computed: {
    leftViewWidth() {
      let views = this.viewSplit.split(",");
      let leftWidth = views[0] ? parseInt(views[0]) : 0;
      return leftWidth;
    },
    rightViewWidth() {
      let views = this.viewSplit.split(",");
      let rightWidth = views[1] ? parseInt(views[1]) : 0;
      return rightWidth;
    },
    leftViewShow() {
      let w = parseFloat(this.leftViewWidth);
      return w != 0;
    },
    rightViewShow() {
      let w = parseFloat(this.rightViewWidth);
      return w != 0;
    },
    splitWidth() {
      if (this.leftRight == "split-small") {
        return 5;
      } else if (this.leftRight == "split-medium") {
        return 10;
      } else if (this.leftRight == "split-big") {
        return 15;
      } else if (this.leftRight == "split-highbig") {
        return 20;
      } else {
        return 0;
      }
    },
    splitWidthHorizontal() {
      if (this.topBottom == "split-small") {
        return 5;
      } else if (this.topBottom == "split-medium") {
        return 10;
      } else if (this.topBottom == "split-big") {
        return 15;
      } else if (this.topBottom == "split-highbig") {
        return 20;
      } else {
        return 0;
      }
    },
    getCss() {
      return [
        ...this.$getClass(),
        this.fit === "1" || this.fit === true ? "fit" : ""
        // this.topBottom ? "top-bottom-" + this.topBottom : "",
        // this.leftRight ? "left-right-" + this.leftRight : ""
      ];
    },
    pageStyle() {
      var headHeight = this.headHeight;
      var footHeight = this.footHeight;
      if (!this.isDesign) {
        headHeight = headHeight + this.splitWidthHorizontal;
        footHeight = footHeight + this.splitWidthHorizontal;
      }
      // var leftWidth = this.leftViewWidth > 0 ? this.leftViewWidth + this.splitWidth : 0;
      // var rightWidth = this.rightViewWidth > 0 ? this.rightViewWidth + this.splitWidth : 0
      return {
        "padding-top": headHeight + "px",
        "padding-bottom": footHeight + "px"
        // "padding-left": leftWidth + "px",
        // "padding-right": rightWidth + "px",
      };
    },
    pageContentStyle() {
      return {
        width: this.isDesign ? "auto" : this.pageWidth == "100%" || !this.pageWidth ? "auto" : this.pageWidth,
        margin: "auto"
        // "margin-bottom": this.footHeight + "px"
      };
    },
    conentStyle() {
      var leftWidth = this.leftViewWidth;
      var rightWidth = this.rightViewWidth;
      let splitWidth = "0px";
      if (!this.isDesign) {
        splitWidth=this.splitWidth+'px'
      }
      return {
        width: "calc(100% - " + (leftWidth + rightWidth) + "px" + ")",
        "padding-left": splitWidth,
        "padding-right": splitWidth
      };
    },
    leftStyle() {
      let style = {};
      let headHeight = this.headHeight;
      let footHeight = this.footHeight;
      let leftWidth = this.leftViewWidth;
      // let height = this.contentHeight;
      return {
        width: leftWidth + "px"
        // 'height': height + "px"
        // 'height':"100%"
      };
    },
    rightStyle() {
      let style = {};
      let headHeight = this.headHeight;
      let footHeight = this.footHeight;
      let rightWidth = this.rightViewWidth;
      // let height = this.contentHeight;
      return {
        width: rightWidth + "px"
        // 'height': height + "px"
        // 'height':"100%"
      };
    }
  },
  created() {},
  beforeDestroy() {
    if (this.$refs.eleDesgin) {
      this.$refs.eleDesgin.parentNode && this.$refs.eleDesgin.parentNode.removeChild(this.$refs.eleDesgin);
    }
  },
  destroyed() {},
  mounted() {
    // console.timeEnd("加载页面");
    if (this.isDesign) {
      this.watchCenterHeight();
      this.resize();
    }
    if (this.$refs.eleDesgin) {
      // console.log(document.body.append)
      document.body.appendChild(this.$refs.eleDesgin);
    }
  },
  updated() {},
  methods: {
    watchCenterHeight() {
      // this.resize();
      this.setCenterHeight();
      window.setTimeout(this.watchCenterHeight, 1000);
    },
    setCenterHeight() {
      let head = this.$refs.head ? this.$refs.head : null;
      let foot = this.$refs.foot ? this.$refs.foot : null;
      this.headHeight = head ? $(head).outerHeight() : 0;
      this.footHeight = foot ? $(foot).outerHeight() : 0;
      // this.contentHeight = $(this.$refs.conent).innerHeight() - this.footHeight;
    },
    resize() {
      // this.setContainerSize();
      this.setCenterHeight();
      this.sizeComplete = true;
    },
    gotoPreview() {
      let isDrag = this.$refs.eleDesgin.getAttribute("data-flag");
      if (isDrag === "true") {
        let previewUrl = `${dsf.url.getWebPath("~/designer.html#/pc/page?B=" + this.context.B + "&M=" + this.context.M + "&pname=" + this.name + "&title=" + this.title)}`;
        window.open(previewUrl);
      }
    }
  }
});
</script>
