<template>
  <div :style="pageStype" class="ds-page-home">
    <div v-if="hasHeader" :style="{height: position.top}" class="ds-page-home-header">
      <slot name="header"></slot>
    </div>
    <div v-if="hasLeft" :style="{width: position.left, top: position.top, bottom: position.bottom}" class="ds-page-home-left">
      <slot name="left"></slot>
    </div>
    <div v-if="hasRight" :style="{width: position.right, top: position.top, bottom: position.bottom}" class="ds-page-home-right">
      <slot name="right"></slot>
    </div>
    <div v-if="hasFooter" :style="{height: position.bottom}" class="ds-page-home-footer">
      <slot name="footer"></slot>
    </div>
    <div :style="mainStyle" class="ds-page-home-main">
      <div v-if="hasBreadcrumb" :style="{height: position.breadcrumb}" class="ds-page-home-breadcrumb">
        <slot name="breadcrumb"></slot>
      </div>
      <div v-if="viewLoaded" class="ds-page-home-view">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
import menuUtils from "./utils";

export default {
  name: "DsfHome",
  provide() {
    return {
      homeRoot: this
    };
  },
  props: {
    menuList: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  data() {
    return {
      hasHeader: false,
      hasLeft: false,
      hasRight: false,
      hasFooter: false,
      hasBreadcrumb: false,
      position: {
        top: null,
        left: null,
        right: null,
        bottom: null,
        breadcrumb: null
      },
      menuAction: [],
      menuIndex: [],
      menuKey: [],
      // 视图载入状态
      viewLoaded: true
    };
  },
  computed: {
    pageStype() {
      const opt = {};
      if (!this.hasHeader) {
        opt["padding-top"] = "0";
      } else if (this.position.top) {
        opt["padding-top"] = this.position.top;
      }
      if (!this.hasFooter) {
        opt["padding-bottom"] = "0";
      } else if (this.position.bottom) {
        opt["padding-bottom"] = this.position.bottom;
      }
      if (!this.hasLeft) {
        opt["padding-left"] = "0";
      } else if (this.position.left) {
        opt["padding-left"] = this.position.left;
      }
      if (!this.hasRight) {
        opt["padding-right"] = "0";
      } else if (this.position.right) {
        opt["padding-right"] = this.position.right;
      }
      return opt;
    },
    mainStyle() {
      const opt = {};
      if (!this.hasBreadcrumb) {
        opt["padding-top"] = "0";
      } else if (this.position.breadcrumb) {
        opt["padding-top"] = this.position.breadcrumb;
      }
      return opt;
    }
  },
  watch: {
    $route(to, from) {
      this.initMenu(to);
    },
    menuList(v) {
      this.initMenu(this.$route);
    }
  },
  created() {
    this.hasHeader = !!this.$slots.header;
    this.hasLeft = !!this.$slots.left;
    this.hasRight = !!this.$slots.right;
    this.hasFooter = !!this.$slots.footer;
    this.hasBreadcrumb = !!this.$slots.breadcrumb;
    if (location.hash == '#' + this.$route.fullPath) {
      this.initMenu(this.$route);
    }
  },
  updated() {
    this.hasHeader = !!this.$slots.header;
    this.hasLeft = !!this.$slots.left;
    this.hasRight = !!this.$slots.right;
    this.hasFooter = !!this.$slots.footer;
    this.hasBreadcrumb = !!this.$slots.breadcrumb;
    if (!this.hasHeader) this.setPosition("top", null);
    if (!this.hasLeft) this.setPosition("left", null);
    if (!this.hasRight) this.setPosition("right", null);
    if (!this.hasFooter) this.setPosition("bottom", null);
    if (!this.hasBreadcrumb) this.setPosition("breadcrumb", null);
  },
  methods: {
    setPosition(key, value) {
      this.$set(this.position, key, value);
    },
    initMenu($route) {
      let menuAction = [];
      let menuIndex = [];
      let menuKey = [];
      this.filterMenu(menuAction, menuIndex, menuKey, this.menuList, $route, 0);
      this.menuAction = menuAction;
      this.menuIndex = menuIndex;
      this.menuKey = menuKey;
      // 如果检测到当前为空路由，即route.fullPath等于'/'
      // 则跳转到第一个菜单去
      if (menuKey.join("") === "" && $route.fullPath === "/" && this.menuList.length) {
        menuUtils.openPage.call(this, menuUtils.findMenu(this.menuList), true);
      }
    },
    /**
     * 判断当前选中的菜单
     * @param menuAction
     * @param menuIndex
     * @param menuKey
     * @param menuList  菜单列表
     * @param route     route
     * @param n         递归层级
     */
    filterMenu(menuAction, menuIndex, menuKey, menuList, route, n) {
      menuAction[n] = {};
      menuIndex[n] = -1;
      menuKey[n] = "";
      for (let i = 0; i < menuList.length; i++) {
        const item = menuList[i];
        if (item.menu_url && this.urlsIsSame(item, route)) {
          menuAction[n] = item;
          menuIndex[n] = i;
          menuKey[n] = item.menu_id;
          if (!item.children || !item.children.length) {
            break;
          }
        }
        if (item.children && item.children.length) {
          this.filterMenu(menuAction, menuIndex, menuKey, item.children, route, n + 1);
          if (menuIndex[n + 1] !== -1) {
            menuAction[n] = item;
            menuIndex[n] = i;
            menuKey[n] = item.menu_id;
            break;
          }
        }
      }
    },
    /**
     * url2是否符合url1
     * path相同，url1的query在url2中都能找到
     * @param item
     * @param route
     */
    urlsIsSame(item, route) {
      let currentUrl = dsf.url.parse(window.location.href);
      let obj = item.__url_obj || null;
      let url = item.menu_url;
      if (!obj && url) {
        let isRouter = false;
        if (url.startsWith("/#")) {
          url = url.substr(2);
          isRouter = true;
        } else if (url.indexOf("#") === 0) {
          url = url.substr(1);
          isRouter = true;
        }
        if (isRouter) {
          url = currentUrl.pathname + "#" + url;
        }
        obj = item.__url_obj = dsf.url.parse(url);
        // if(obj.hashKey.startsWith("/iframeloader?src=")){
        //   let c=dsf.url.queryStringParse(obj.hash.substr(obj.hash.indexOf("?")+1));

        // }
      }
      if (!obj || obj.path !== currentUrl.path) {
        return false;
      }
      if (!obj || obj.hashKey !== currentUrl.hashKey) {
        return false;
      }

      for (let k in obj.searchQuery) {
        if (k.indexOf("_") == 0) continue;
        var rq = currentUrl.searchQuery[k];
        var iq = obj.searchQuery[k];
        if (rq != iq) {
          return false;
        }
        if (!iq) {
          return false;
        }
      }
      for (let k in obj.hashQuery) {
        if (k.indexOf("_") == 0) continue;
        var rq = currentUrl.hashQuery[k];
        var iq = obj.hashQuery[k];
        if (rq != iq) {
          return false;
        }
        if (!iq) {
          return false;
        }
      }
      return true;
      // let obj = item.__url_obj || null;
      // let url = item.menu_url;
      // if (!obj && url) {
      //   if (url.indexOf('/#') === 0) {
      //     url = url.substr(2);
      //   } else if (url.indexOf('#') === 0) {
      //     url = url.substr(1);
      //   }
      //   obj = item.__url_obj = dsf.url.analyzing(url);
      // }
      // if (!obj || obj.path !== route.path) {
      //   return false;
      // }
      // let rq = null, iq = null;
      // for (let k in obj.query) {
      //   if (k.indexOf('_') == 0) continue;
      //   rq = route.query[k];
      //   iq = obj.query[k];
      //   if (!rq) return false;
      //   else if (dsf.type(iq) !== 'array' && rq !== iq) return false;
      //   else if (dsf.type(iq) === 'array' && iq.indexOf(rq) === -1) return false;
      // }
      // return true;
    }
  }
};
</script>