<template>
  <div class="ds-control ds-label-box" :class="getCss" @dblclick="beginEdit">
    <label v-if="!isDesign" class="ds_label ds_font_5" v-text="value" />
    <template v-else>
      <label v-if="!editting" class="ds_label ds_font_5" v-text="value || '无内容'" />
      <div v-else ref="contenteditable" class="contenteditable" style="display: block" contenteditable @blur="endEdit" v-text="value"></div>
    </template>
  </div>
</template>

<script>
import text from '../../../mixins/text';

export default dsf.component({
  name: 'DsfLabel',
  mixins: [text],
  inheritAttrs: false,
  ctrlCaption: "文本",
  props: {
    value: {
      type: [String, Number],
      default: '文本标签'
    },
    textAlign: {
      type: String,
      default: ''
    },
    fontFamily: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      editting: false,
      $required: false
    }
  },
  computed: {
    getCss() {
      const $class = this.$getClass();
      $class.push(
        this.textAlign,
        'ds_font_' + this.fontFamily,
        this.$required ? 'ds_required' : ''
      );
      return $class;
    }
  },
  methods: {
    beginEdit() {
      if (this.isDesign) {
        this.editting = true;
        setTimeout(() => {
          this.$refs.contenteditable.focus();
        }, 0);
      }
    },
    endEdit(e) {
      if (this.isDesign) {
        this.editting = false;
        this.value = e.target.innerHTML;
      }
    }
  }
});
</script>