<template>
  <div class="ds-control ds-html">
    <div v-if="isDesign" style="text-align:center;background-color:#fff;padding:3px 0px;font-style:italic;color:#c0c0c0">html片段</div>
    <div v-else style="text-align:center;background-color:#fff;padding:20px 0px;font-style:italic;color:#c0c0c0;">
      html组件因为绑定数据原因只能在正式运行下查看
    </div>
  </div>
</template>
<script>
import control from '_platform/mixins/control';
/**
 * @class DsfHtml
 * @description 加载html片段
 * @example 
 * <DsfHtml html="<p>我是HTML代码片段</p>"></DsfHtml>
 */
export default dsf.component({
  name: 'DsfHtml',
  ctrlCaption: 'Html',
  mixins: [control],
  /**
   * @memberof DsfHtml
   * @name Props
   * @property {String} [html = ""] html 代码片段
   */
  props: {
    html: {
      type: String,
      default: ""
    }
  }
});
</script>