<template>
  <div class="ds-control dsf-drag"><slot></slot></div>
</template>
<script>
import control from "_platform/mixins/control";
import { jqui } from '_platform/pc/common/importJqueryUI';
export default dsf.component({
  name: "DsfDrag",
  ctrlCaption: "拖动组件",
  mixins: [control],
  components: {},
  provide() {
    return {};
  },
  filters: {},
  props: {
    range: {
      type: String,
      default() {
        return "dragrange";
      },
    },
    helper: {
      type: String || Function,
      default() {
        return null;
      },
    },
    disabled: {
      default() {
        return false;
      },
    },
  },
  data() {
    return {
      id: null,
      dragRanges: {},
    };
  },
  watch: {},
  methods: {
    getDragRange() {
      let parent = this.$parent;
      while (parent) {
        if (parent.ctrlType == "dsf.dragrange") {
          parent.drags[this.id] = this;
          this.dragRanges[parent.name] = parent;
        }
        parent = parent.$parent;
      }
      return null;
    },
  },
  created() {
    this.id = dsf.UUID();
  },
  beforeMount() {},
  mounted() {
    jqui.then(async () => {
      this.getDragRange();
      const { $el, range, dragRanges } = this;
      let jqDom = $($el);
      let dragRangeDom = dragRanges[range];
      if (dragRangeDom && dragRangeDom.$el) {
        jqDom.draggable({
          distance: 10,
          opacity: 0.5,
          revert: true,
          disabled: this.disabled,
          scroll: true,
          containment: dragRangeDom.$el,
          addClasses: true,
          appendTo: dragRangeDom.$el || "body",
          helper: (e, ui) => {
            let helperDom = jqDom;
            if (this.helper) {
              if (dsf.type(this.helper) == "string") {
                let parent = helperDom.parents(this.helper)[0];
                let children = helperDom.children(this.helper)[0];
                if (children) {
                  helperDom = $(children);
                } else if (parent) {
                  helperDom = $(parent);
                }
              } else if (dsf.type(this.helper) == "function") {
                helperDom = this.helper(e, ui);
              }
            }
            helperDom =
              helperDom &&
              helperDom
                .clone()
                .removeClass("ds-control dsf-drag")
                .attr("drag-move", "true")
                .width(helperDom.width())
                .height(helperDom.height())
                .css({ zIndex: 100 });
            return helperDom;
          },
          // 当 droppable 被创建时触发
          create: (e, ui) => {
            this.$emit("create", e, ui);
          },
          // 在拖拽期间当鼠标移动时触发
          drag: (e, ui) => {
            this.$emit("drag", e, ui);
          },
          // 当拖拽开始时触发
          start: (e, ui) => {
            this.$emit("start", e, ui);
          },
          // 当拖拽停止时触发
          stop: (e, ui) => {
            this.$emit("stop", e, ui);
          },
        });
      } else {
        console.error(`没有找到${range}拖拽区域`);
      }
    });
  },
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  design: {},
});
</script>
