<template>
  <div class="ds-control ds-flexible-box" :class="boxClass" :style="boxStyle">
    <div ref="bar" class="ds-flexible-box-bar" :style="barStyle">
      <div ref="barBox" class="ds-flexible-box-bar-box" :style="barStyle" @transitionend="transitionend">
        <div class="ds-flexible-box-bar-box-content" slot-name="bar">
          <slot name="bar"></slot>
        </div>
        <div v-if="!hiddenBt" class="ds-flexible-box-bar-bt">
          <div @click="close" :title="isClosed?'展开':'折叠'">
            <span>||</span>
          </div>
        </div>
      </div>
    </div>
    <div class="ds-flexible-box-main">
      <div v-if="isDesign" class="ds-flexible-box-main-box" slot-name="default">
        <slot name="default"></slot>
      </div>
      <template v-else>
        <slot name="default"></slot>
      </template>
    </div>
  </div>
</template>

<script>
import layout from "_platform/mixins/layout";

export default dsf.component({
  name: "DsfFlexibleBox",
  ctrlCaption: "伸缩盒",
  mixins: [layout],
  design: {
    isMask: false
  },
  props: {
    // 布局方式
    layout: {
      type: String,
      default: "row"
    },
    // 是否隐藏折叠按钮
    hiddenBt: {
      type: Boolean,
      default: false
    },
    boxHeight: {
      type: String,
      default: "100%"
    },
    barWidth: {
      type: String,
      default: "250px"
    },
    barHeight: {
      type: String,
      default: "250px"
    },
    margin: {
      type: String,
      default: "5px"
    },
    padding: {
      type: Boolean,
      default: false
    },
    // 是否默认折叠
    isClosed: {
      type: Boolean,
      default: false
    },
    slots: {
      type: Array,
      default: function () {
        return [
          {
            name: "bar",
            controls: []
          },
          {
            name: "default",
            controls: []
          }
        ];
      }
    }
  },
  data() {
    return {
      // 是否动画
      animation: true,
      // 动画是否进行中
      animating: false
    };
  },
  computed: {
    boxClass() {
      let res = [this.layout];
      if (!this.isDesign && this.isClosed) {
        res.push('close')
      }
      if (this.animation) {
        res.push('is-transition')
      }
      if (this.animating) {
        res.push('animating')
      }
      return res;
    },
    boxStyle() {
      let res = {
        "flex-direction": this.layout,
        height: this.boxHeight
      };
      if (this.isDesign) {
        this.$dispatch("design-height-change", this.boxHeight);
        res.height = "100%";
      }
      if (!this.padding && !this.isDesign) {
        res.padding = "0";
      }
      return res;
    },
    barStyle() {
      let res = {};
      let { barWidth, barHeight, margin } = this;
      barWidth = barWidth.trim();
      barHeight = barHeight.trim();
      switch (this.layout) {
        case "row":
          res["width"] = barWidth;
          if (["0", "0px", "0%"].indexOf(barWidth) == -1) {
            res["margin-right"] = margin;
          }
          break;
        case "row-reverse":
          res["width"] = barWidth;
          if (["0", "0px", "0%"].indexOf(barWidth) == -1) {
            res["margin-left"] = margin;
          }
          break;
        case "column":
          res["height"] = barHeight;
          if (["0", "0px", "0%"].indexOf(barWidth) == -1) {
            res["margin-bottom"] = margin;
          }
          break;
        case "column-reverse":
          res["height"] = barHeight;
          if (["0", "0px", "0%"].indexOf(barWidth) == -1) {
            res["margin-top"] = margin;
          }
          break;
      }
      return res;
    }
  },
  created() {
    if (dsf.client.type.indexOf("IE") >= 0) {
      this.animation = false;
    }
  },
  methods: {
    close() {
      if (this.isDesign) return;
      let isClosed = !this.isClosed;
      if (this.animation) {
        this.animating = true;
      }
      this.$emit("update:isClosed", isClosed);
      this.$dispatch("close", isClosed);
      if (!this.animation || isClosed) {
        this.$nextTick(() => {
          this.$page && dsf.resizeComponent(this.$page);
        })
      }
    },
    transitionend(evt) {
      if (!this.isDesign &&
        this.animation &&
        evt.target === this.$refs.barBox &&
        evt.propertyName == "transform") {
        this.animating = false;
        this.$nextTick(() => {
          this.$page && dsf.resizeComponent(this.$page);
        })
      }
    }
  }
});
</script>