<template>
  <div v-show="showBar" class="ds-control ds-button-bar" :class="getCss">
    <div class="bar-wrap">
      <template v-for="(button, $index) in showButtonsReal">
        <template v-if="button.isUpload">
          <el-upload
            :key="$index"
            class="ds-button-update"
            :headers="getHeaders()"
            :accept="getFileAccept(button)"
            :data="getUploadData(button)"
            :show-file-list="false"
            :action="uploadUrl(button)"
            :on-success="handleAvatarSuccess(button)"
            :before-upload="beforeAvatarUpload(button)"
          >
            <DsfButton
              :icon="getIcon(button)"
              :title="button.title"
              :disabled="button.disabled"
              :size="size"
              :type="button.type"
              :btn-style="button.btnStyle"
            >
              {{ button.text }}
            </DsfButton>
          </el-upload>
        </template>
        <template
          v-else-if="
            button.isLinkSend && button.isLinkSendFold && (button.actionName === 'system_flow_send' || button.actionName === 'system_batch_flow_send')
          "
        >
          <el-popover
            :key="$index"
            v-model="button.popVisible"
            :popper-class="'ds-morebutton-popover'"
            trigger="manual"
            placement="top"
            :class="['ds-button__flow']"
          >
            <template v-if="flowButtons.length > 0" v-slot:default>
              <el-row :style="{ width: flowButtons.length > 5 ? '240px' : '80px' }">
                <el-col v-for="(flow, index) in flowButtons" :key="index" :span="flowButtons.length > 5 ? 8 : 24" style="text-align: center">
                  <template>
                    <DsfButton
                      :key="index"
                      style="width: 80px; margin-bottom: 5px; margin-top: 5px"
                      :icon="getIcon(flow)"
                      :disabled="flow.disabled"
                      :size="size"
                      :type="flow.type"
                      :title="flow.title"
                      :btn-style="'icon-text'"
                      @click="clickHandler(flow, $event)"
                    >
                      {{ flow.text }}
                    </DsfButton>
                  </template>
                </el-col>
              </el-row>
            </template>
            <DsfButton
              slot="reference"
              :icon="getIcon(button)"
              :title="button.title"
              :disabled="button.disabled"
              :size="size"
              :type="button.type"
              :btn-style="button.btnStyle"
              @click="addFlowBtns(button)"
            >
              {{ button.text }}
            </DsfButton>
          </el-popover>
        </template>
        <template v-else>
          <DsfButton
            :key="$index"
            :icon="getIcon(button)"
            :title="button.title"
            :disabled="button.disabled"
            :size="size"
            :type="button.type"
            :btn-style="button.btnStyle"
            :style="button.style"
            @click="clickHandler(button, $event)"
          >
            {{ button.text }}
          </DsfButton>
        </template>
      </template>
      <el-popover
        v-if="moreButtonsReal.length"
        :popper-class="'ds-morebutton-popover'"
        width="auto"
        trigger="click"
        :class="['ds-button__more', size]"
        @show="popoverShow"
        @hide="popoverClose"
      >
        <template v-slot:default>
          <el-row :style="{ width: moreButtonsReal.length > 5 ? '240px' : '80px' }">
            <el-col v-for="(button, $index) in moreButtonsReal" :key="$index" :span="moreButtonsReal.length > 5 ? 8 : 24" style="text-align: center">
              <template v-if="button.isUpload">
                <el-upload
                  :key="$index"
                  class="ds-button-update"
                  :headers="getHeaders()"
                  :accept="getFileAccept(button)"
                  :data="getUploadData(button)"
                  :show-file-list="false"
                  :action="uploadUrl(button)"
                  :on-success="handleAvatarSuccess(button)"
                  :before-upload="beforeAvatarUpload(button)"
                >
                  <DsfButton
                    :icon="getIcon(button)"
                    :title="button.title"
                    :disabled="button.disabled"
                    :size="size"
                    :type="button.type"
                    :btn-style="'icon-text'"
                    style="width: 80px; margin-bottom: 5px; margin-top: 5px"
                  >
                    {{ button.text }}
                  </DsfButton>
                </el-upload>
              </template>
              <template v-else>
                <DsfButton
                  :key="$index"
                  style="width: 80px; margin-bottom: 5px; margin-top: 5px"
                  :icon="getIcon(button)"
                  :disabled="button.disabled"
                  :size="size"
                  :type="button.type"
                  :title="button.title"
                  :btn-style="'icon-text'"
                  @click="clickHandler(button, $event)"
                >
                  {{ button.text }}
                </DsfButton>
              </template>
            </el-col>
          </el-row>
        </template>
        <DsfButton
          v-if="moreButtonsReal.length && moreButtonStyle != 'icon-text'"
          slot="reference"
          :title="moreButtonText"
          :btn-style="moreButtonStyle"
          :icon="moreFold ? 'shuangjiantouxia' : 'xiangshangzhanhang'"
          :size="size"
          class="plain"
          :class="[moreButtonStyle]"
        >
          {{ moreButtonText }}
        </DsfButton>
        <DsfButton
          v-else-if="moreButtonsReal.length && moreButtonStyle == 'icon-text'"
          slot="reference"
          :title="moreButtonText"
          :btn-style="moreButtonStyle"
          icon="gengduo1"
          :size="size"
          class="plain"
          :class="[moreButtonStyle]"
        >
          {{ moreButtonText }}
        </DsfButton>
      </el-popover>
    </div>
  </div>
</template>
<script>
import { getButtonHander } from "../../../register/buttonRegister";
import control from "_platform/mixins/control";
/**
 * @class DsfButtonBar
 * @description 按纽组
 * @example
 * <DsfButtonBar></DsfButtonBar>
 */
export default dsf.component({
  name: "DsfButtonBar",
  ctrlCaption: "按纽组",
  mixins: [control],
  inject: {
    $buttonOwner: {
      default: null,
    },
  },
  /**
   * @memberof DsfButtonBar
   * @name Props
   * @property {Object} [owner = null] 所有者
   * @property {String} [size = ''] 大小
   * @property {String} [align = center] 居中方式
   * @property {Array} [buttons = []] 按钮
   * @property {String} [emitPrefix = ''] 前缀
   * @property {Function} [renderButton = null] 渲染按钮回调掉方法
   */
  props: {
    size: {
      type: String,
      default: "",
    },
    align: {
      type: String,
      default: "center",
    },
    buttons: {
      type: Array,
      default() {
        return [];
      },
    },
    emitPrefix: {
      type: String,
      default: "",
    },
    renderButton: {
      type: Function,
      default: null,
    },
    maxNumber: {
      type: Number,
      default: 4,
    },
    moreButtonStyle: {
      type: String,
      default: "",
    },
    moreButtonText: {
      type: String,
      default: "更多",
    },
  },
  computed: {
    owner() {
      return this.$buttonOwner;
    },
    showBar() {
      if (this.isDesign) {
        return true;
      } else {
        return this.validButtons.length > 0;
      }
    },
    getCss() {
      return {
        ...this.$getClass(),
        ["align-" + this.align]: true,
      };
    },
    validButtons() {
      let buttons = _.filter(this.buttons, (button, index) => {
        if (this.showAndHide(button)) {
          this.disabledFactor(button);
          return true;
        }
        return false;
      });
      return buttons;
    },
    moreButtonsReal() {
      return _.filter(this.validButtons, (button, index) => {
        return index >= this.maxNumber;
      });
    },
    showButtonsReal() {
      return _.filter(this.validButtons, (button, index) => {
        return index < this.maxNumber;
      });
    },
  },
  data() {
    return {
      showButtons: [],
      moreButtons: [],
      moreFold: true,
      flowButtons: [],
    };
  },
  created() {},
  mounted() {
    if (!this.isDesign) {
      this.buttons.forEach((button, index) => {
        //将按钮执行状态设置为false
        button.execing = false;
        // 按钮是流程发送按钮或者是批量发送按钮 配置了分环节发送 设置了不折叠后需要加载
        let isLinkSend = button.isLinkSend;
        let isLinkSendFold = button.isLinkSendFold;
        let actionName = button.actionName;
        let sendButtons = ["system_flow_send", "system_batch_flow_send"];
        if (isLinkSend && !isLinkSendFold && sendButtons.indexOf(actionName) >= 0) {
          // 为了解决ie显示一般问题
          if (dsf.client.type && dsf.client.type.indexOf("IE") > -1) {
            button.style = {
              transform: "scale(0.99)",
            };
          }
          this.addFlowBtns(button, this.buttons, index);
        }
      });
    }
  },
  updated() {
    // console.log(this.buttons)
  },
  beforeDestroy() {},
  methods: {
    getButtonParams(button) {
      let owner = this.owner || this;
      let params = _.map(button.params, (p) => {
        if (p.value) {
          let rfunction = /^\s*function(\s+\w*)?\(.*\)\s*{/g;
          let v = p.value.trim();
          if (rfunction.test(v)) {
            let code = [
              "var result= (" + v + ").call(this,data,button,owner,self);",
              "data=null;",
              "button=null;",
              "owner=null;",
              "self=null;",
              "return result;",
            ];
            let fn = new Function("data", "button", "owner", "self", code.join("\n"));
            let result = fn.call(this.$vm, this.local, button, owner, this);
            if (dsf.type(result) == "string") {
              result = this.$replace(result, this.local);
            }
            return result;
          } else {
            let v = this.$replace(p.value, this.local);
            if (v === "false") {
              v = false;
            } else if (v === "true") {
              v = true;
            }
            return v;
          }
        } else {
          let v = p.default;
          if (v === "false") {
            v = false;
          } else if (v === "true") {
            v = true;
          }
          return dsf.isDef(v) ? v : null;
        }
      });
      return params;
    },
    reloadData() {
      this.$forceUpdate();
    },
    disabledFactor(button) {
      let result = false;
      if (!this.isDesign) {
        //非设计模式下运行表达式
        if (button.disabledFactor) {
          let obj = {
            $vm: {
              value: this.$vm,
            },
          };
          let data = Object.create(this.local, obj);
          result = this.$eval(button.disabledFactor, data) !== false;
        } else {
          result = false;
        }
      }
      button.disabled = result;
    },
    showAndHide(button) {
      let result = true;
      if (!this.isDesign) {
        //非设计模式下运行表达式
        if (button.if) {
          let obj = {
            $vm: {
              value: this.$vm,
            },
          };
          let data = Object.create(this.local, obj);
          result = this.$eval(button.if, data) !== false;
        } else {
          result = true;
        }
      }
      if (button.visible === false) {
        result = false;
      }
      if (this.renderButton) {
        result = this.renderButton(button, result);
      }
      return result;
    },
    clickHandler(button, $event, response) {
      if (this.isDesign) {
        return;
      }
      if (!button.execing) {
        button.execing = true;
        if (button.disabled || this.isDesign) {
          return;
        }
        if (button.secondConfirm) {
          dsf.layer.pc
            .confirm(button.secondConfirmText || "确认提交？")
            .then(() => {
              this.buttonAction(button, $event, response);
            })
            .catch(() => {
              button.execing = false;
            });
        } else {
          this.buttonAction(button, $event, response);
        }
      }
    },
    async buttonAction(button, $event, response) {
      let owner = this.owner || this;
      //如果是系统按钮
      if (button.isSystem) {
        let handler = getButtonHander(button.actionName, "pc");
        let params = this.getButtonParams(button);
        if (handler && dsf.isFunction(handler)) {
          //系统按钮
          let param2 = {
            owner: owner,
            data: this.local,
            _self: this,
            targetButton: button,
            params: params,
          };
          if (response) {
            param2.response = response;
          }
          await handler.call(this.$vm, param2, $event);
        }
      } else {
        //自定义按钮
        let eventName = (this.emitPrefix ? this.emitPrefix + "-" : "") + button.actionName;
        let handler = owner.$listeners[eventName];
        if (handler && dsf.isFunction(handler)) {
          let params = {
            owner: owner,
            data: this.local,
            _self: this,
            targetButton: button,
          };
          if (response) {
            params.response = response;
          }
          await handler(params, $event);
        }
      }
      button.execing = false;
    },
    getIcon(button) {
      let icon = button.icon;
      if (icon && icon.startsWith("icon-")) {
        return icon.replace("icon-", "");
      }
      return icon;
    },
    popoverShow() {
      this.moreFold = false;
    },
    popoverClose() {
      this.moreFold = true;
    },
    //触发指定按钮的点击事件
    triggerButtonClick(button) {
      this.clickHandler(button);
    },
    addFlowBtns(button, btns, index) {
      if (!btns && this.flowButtons.length > 0) {
        button.popVisible = !button.popVisible;
        setTimeout(() => {
          document.addEventListener("click", this.hidePopVisible);
        }, 0);
        return;
      }
      let sWFID;
      dsf.recursionComponentProps(this.$page.$props, (data) => {
        if (data.isFormControl && data.caption === "wfinfo") {
          sWFID = data.chooseFlow && data.chooseFlow.id;
        }
      });
      if (sWFID) {
        let data = {};
        if (this.$vm.$dialog && this.$vm.$dialog.dialogArgs && this.$vm.$dialog.dialogArgs.data) {
          data = this.$vm.$dialog.dialogArgs.data[0];
        }
        this.$http
          .get("/wfr/getLines", {
            sWFID: sWFID,
            sPID: data.sPID || this.$route.query.sPID,
            sCurNodeID: data.sCurNodeID || this.$route.query.sCurNodeID || "",
            sCurLinkID: data.sCurLinkID || this.$route.query.sCurLinkID || "",
            sExInfos: "",
          })
          .then((res) => {
            if (res.data.success) {
              const data = res.data.data;
              const lines = data && data.SendParams && data.SendParams.Lines;
              if (lines) {
                lines.forEach((line) => {
                  if (line.Name === "审核通过") {
                    line.order = 0;
                  } else if (line.Name === "返回修改") {
                    line.order = 1;
                  } else if (line.Name === "审核不通过") {
                    line.order = 2;
                  }
                });
                lines.sort((a, b) => a.order - b.order);
                lines.forEach((line, i) => {
                  const newButton = Object.assign({}, button, {
                    caption: line.Name,
                    text: line.Name,
                    title: line.Name,
                    line: line,
                  });
                  if (!btns) {
                    this.flowButtons.push(newButton);
                    this.$set(button, "popVisible", true);
                    document.addEventListener("click", this.hidePopVisible);
                  } else {
                    // 重排页面
                    setTimeout(() => {
                      newButton.style = {
                        width: "auto",
                        transform: "scale(1)",
                      };
                    }, 0);
                    btns.splice(index + i + 1, 0, newButton);
                  }
                });
                btns && btns.splice(btns.indexOf(button), 1);
              }
            }
          })
          .catch((res) => {});
      }
    },
    //上传类按钮处理 start
    uploadUrl(button) {
      let params = this.getButtonParams(button);
      let url = params[0];
      //this.$replace(button.params[0].value || button.params[0].default);
      return dsf.url.getWebPath(url);
    },
    getFileAccept(button) {
      let params = this.getButtonParams(button);
      let url = params[0];
      let allowList = params[2].split("|");
      let map = _.map(allowList, (el) => {
        return dsf.mime[el.toLowerCase()];
      });
      return map.join(",");
    },
    beforeAvatarUpload(button) {
      return ((file) => {
        let params = this.getButtonParams(button);
        let allowList = params[2].split("|");
        let mark = false;
        for (let i = 0; i < allowList.length; i++) {
          if (file.name.toLowerCase().endsWith("." + allowList[i].toLowerCase())) {
            mark = true;
            break;
          }
        }
        if (!mark) {
          dsf.layer.message("只能上传" + allowList.join(",") + "类型的文件", false);
          return false;
        }
      }).bind(this);
    },
    handleAvatarSuccess(button) {
      return ((res, file) => {
        this.clickHandler(button, undefined, res);
      }).bind(this);
    },
    getHeaders() {
      return dsf.getToken();
    },
    getUploadData(button) {
      let params = this.getButtonParams(button);
      let data = params[1];
      data = dsf.url.queryStringParse(data);
      return data;
    },
    hidePopVisible(button) {
      this.buttons.forEach((btn) => {
        if (btn.popVisible) {
          btn.popVisible = false;
        }
      });
      document.removeEventListener("click", this.hidePopVisible);
    },
    //上传类按钮处理 end
  },
});
</script>
