<template>
  <div v-if="isDesign || (sPID && sCurLinkID)" class="ds-control ds-form-item ds-textbox" :class="getCss">
    <label v-if="showLabel" class="ds-form-label" :style="getLabelWidthStyle()">
      {{ label }}
      <i v-if="dataCapion" class="iconfont icon-bangzhuzhongxin ds-control-helper" @click="showHelper"></i>
    </label>
    <div class="ds-form-block" :style="getFormItemBlockMarginLeft()">
      <template v-if="!readOnly">
        <p v-for="(item, index) in data" :key="index" class="form-comment-box-value">
          <!-- <img
            class="icon"
            :src="require(`_platform/assets/styles/themes/workFlow/bgs${item.nodeid === sCurLinkID ? '1' : ''}.png`)"
          /> -->
          <img class="icon" :src="require(`_platform/assets/styles/themes/workFlow/ry1.png`)" />
          <span class="name">{{ item.rusername }}</span>
          <span class="content">{{ (item.opinionatts && item.opinionatts.Content) }}</span>
          <span class="time">{{ item.receivetime }}</span>
        </p>
      </template>
    </div>
  </div>
</template>
<script>
import formControl from "../../../mixins/formControl";
export default dsf.component({
  name: "DsfFormCommentBox",
  mixins: [formControl],
  ctrlCaption: "表单意见框",
  props: {
    caption: {
      type: String,
      default() {
        return "";
      },
    },
    label: {
      type: String,
      default() {
        return "";
      },
    },
    placeholder: {
      type: String,
      default() {
        return "请输入";
      },
    },
    showLabel: {
      type: Boolean,
      default() {
        return true;
      },
    },
    readOnly: {
      type: Boolean,
      default() {
        return false;
      },
    },
    disabled: {
      type: Boolean,
      default() {
        return false;
      },
    },
    visible: {
      type: Boolean,
      default() {
        return true;
      },
    },
    tbNoPadding: {
      type: Boolean,
      default() {
        return false;
      },
    },
    lrNoPadding: {
      type: Boolean,
      default() {
        return false;
      },
    },
    scope: {
      type: Number,
      default() {
        return 0;
      },
    },
    nodes: {
      type: Array,
      default() {
        return [];
      },
    },
    order: {
      type: Number,
      default() {
        return 0;
      },
    },
    template: {
      type: Number,
      default() {
        return 0;
      },
    },
    metadata: {
      type: Object,
      default() {
        return {
          id: "",
          name: "表单意见框",
          type: {
            value: 3,
            text: "业务元数据",
          },
          valueAttributes: [
            {
              name: "文本",
              code: "text",
              type: dsf.metadata.getDataType("string"),
              length: "50",
              defaultValue: null,
              unit: null,
            },
          ],
        };
      },
    },
  },
  data() {
    return {
      sPID: this.$route.query.sPID,
      sCurLinkID: this.$route.query.sCurLinkID,
      data: [],
    };
  },
  watch: {
    "$vm.flows.formCommentBoxData"(val) {
      let data = [];
      val.forEach((item) => {
        if (item.ruserid && this.nodes) {
          this.nodes.forEach((id) => {
            if (id === item.nodeid) {
              data.push(item);
            }
          });
        }
      });
      this.data = data;
    },
  },
  mounted() {
    this.init();
  },
  updated() {
    // console.log("修改文本")
  },
  destroyed() {
    // console.log("文本框被销毁")
  },
  methods: {
    init() {
      if (this.isDesign || !this.sPID) {
        this.data = [
          {
            nodename: "拟稿",
            STATUS: 0,
            linkid: "faedda583f7742948b0714f0f17d1085",
            opinionatts: JSON.stringify({
              Content: "同意",
            }),
            ruserid: "",
            pid: "af60e455757d48dd801d07b1538eb806",
            rusername: "平台管理员",
            rdeptname: "平台管理组",
            reachtime: "2020年01月06日 18:21:21",
            receivetime: "2020年1月2日 10:21:21",
          },
        ];
      } else {
        // 保证每个表单只拉取一次流程进度
        if (this.$vm && this.$vm.flows && this.$vm.flows.isLoadFdata) {
          return;
        }
        this.$vm.flows.isLoadFdata = true;
        this.$http
          .get("wfr/getProcessInfoByPID", {
            sPID: this.sPID,
          })
          .then((res) => {
            if (res.data.success) {
              res.data.data.forEach((item) => {
                item.opinionatts = JSON.parse(item.opinionatts);
                if (item.linkid === this.sCurLinkID && item.pid === this.sPID) {
                  if (item.STATUS != 0 && item.STATUS != 1) {
                    let params = dsf.mix({}, this.$route.query, {
                      isHide: true,
                    });
                    //更新url添加id参数
                    this.$router.replace({
                      path: this.$route.path,
                      query: params,
                    });
                  }
                }
              });
              if (this.$vm && this.$vm.flows) {
                this.$set(this.$vm.flows, "formCommentBoxData", res.data.data);
              }
            }
          });
      }
    },
  },
});
</script>
