<template>
  <div v-loading="!hasInfiniteScroll && loading" class="ds-control ds-datagrid" :class="getCss">
    <DsfPanel class="ds-datagrid-panel" :style="getListStyle" :has-title="showTitle" :has-header="showHead" :title-border="true">
      <template v-slot:title>
        {{ label }}
      </template>
      <template v-slot:header-left>
        <slot name="header-left">
          <div class="ds-datagrid-header-left">
            <span class="ds-datagrid-header-left-showType">
              <i v-for="item in layoutInfoCopy" :key="item.id" :class="['iconfont', item.icon, {active: currentShowLayout.id == item.id}]" @click="currentShowLayout = item"></i>
            </span>
          </div>
        </slot>
      </template>
      <template v-slot:header>
        <slot name="header">
          <div class="ds-datagrid-header">
            <div class="ds-datagrid-header-right">
              <div v-if="buttons.length&&buttonBarPosition=='head'" class="head-right-item" style="margin-left:10px">
                <DsfButtonBar ref="buttonbar" :more-button-style="buttonsMoreStyle" :more-button-text="buttonsMoreText" :max-number="buttonsMaxNumber" :buttons="buttons" :is-design="isDesign" :local="local" :class="{'ds-no-padding':true}"></DsfButtonBar>
              </div>
              <div v-if="showSearch" class="head-right-item" style="margin-left:10px">
                <form @submit.stop.prevent="search">
                  <el-input v-model="searchStr" :placeholder="searchPlaceholder" class="search-input"></el-input>
                  <button class="search-button"><i class="iconfont icon-fangdajing"></i></button>
                </form>
              </div>
              <div v-if="checkboxColumn.show" class="head-right-item" style="margin-left:10px">
                <span v-if="!isSingleChecked" class="ds-datagrid-checkednumber ds_font5">
                  <i class="icon-093info iconfont layui-icon icon-color ds_icon_4" style="margin-right:5px;"></i>已选：<font style="margin:0 5px;">{{ multipleSelection.length }}</font>条
                </span>
                <span v-else-if="multipleSelection" class="ds-datagrid-checkednumber ds_font5">
                  <i class="icon-093info iconfont layui-icon icon-color ds_icon_4" style="margin-right:5px;"></i>已选
                </span>
              </div>
              <div class="head-right-item" slot-name="header-attach">
                <slot name="header-attach"></slot>
              </div>
            </div>
          </div>
        </slot>
      </template>
      <template v-slot:default>
        <div class="ds-datagrid-search-wrap">
          <slot name="search-bar">
            <div v-if="superSearchInfo.length>0" :style="{height:tableMarginTop+'px'}">
              <form @submit.stop.prevent="search">
                <div style="float:left;min-height:1px;" :style="{'width':'100%'}">
                  <!--高级检索-->
                  <DsfSuperSearch
                    ref="superSearchBar"
                    :super-search-row-border="superSearchRowBorder"
                    :show-fitler="showSuperSearchFilter"
                    :search-item-setting="superSearchInfo"
                    :owner="_self"
                    :search-height.sync="superSearchHeight"
                    :default-rows-number="superSearchDefaultRowsNumber"
                    :first-has-button="buttonBarPosition=='superSearchRight'"
                    @loaded="superSearchLoaded"
                    @change="superSearchChange($event,!superSearchButtonTrigger)">
                    <template v-if="buttonBarPosition=='superSearchRight'" v-slot:firstRowButtonBar>
                      <DsfButtonBar ref="buttonbar" :more-button-style="buttonsMoreStyle" :more-button-text="buttonsMoreText" :max-number="buttonsMaxNumber" :buttons="buttons" :is-design="isDesign" :local="local" :class="{'ds-no-padding':true}"></DsfButtonBar>
                    </template>
                  </DsfSuperSearch>
                </div>
              </form>
            </div>
          </slot>
        </div>
        <!--设计状态-->
        <template v-if="isDesign">
          <template v-for="item in layoutInfoCopy">
            <div v-if="item.type == 'table'" :key="item.id" v-show="currentShowLayout.id == item.id" class="ds-datagrid-content-wrap" :style="{'height':contentWrapHeight, 'background-color': item.backgroundColor}">
              <DsfDataGridTable
                :is-design="isDesign"
                ref="table"
                :columns="columns"
                :checkbox-column="checkboxColumn"
                :index-column="indexColumn"
                :operate-column="operateColumn"
                :row-buttons="rowButtons"
                :border="true"
                :fit="true"
                :header-cell-class-name="getHeaderClass"
                :data="templateData"
                :height="tableHeight"
                :customer-order="customerOrder"
                :is-re-draw-hander="isReDrawHander"
                stripe>
                <template v-slot:[column.columnId]="scope" v-for="column in getLastColumns()">
                  <slot :name="column.columnId" v-bind="scope"></slot>
                </template>
              </DsfDataGridTable>
            </div>
            <div v-else :key="item.id" v-show="currentShowLayout.id == item.id" class="ds-datagrid-content-wrap" :style="{'height':contentWrapHeight, 'background-color': item.backgroundColor}">
              <el-checkbox v-if="checkboxColumn.show && !isSingleChecked" v-model="checkAll" :indeterminate="false" style="padding-left: 8px">
                全选
              </el-checkbox>
              <div v-for="(row, $index) in templateData" :key="row._id" class="ds-datagrid-layout-design ds-datagrid-slot" :slot-name="item.id">
                <slot :name="item.id" v-bind="{row, $index}"></slot>
              </div>
              <div class="ds-datagrid-hide-slot ds-datagrid-slot" slot-name="datagrid-hide">
                <slot name="datagrid-hide"></slot>
              </div>
            </div>
          </template>
        </template>
        <!--非设计状态-->
        <template v-else>
          <template v-for="item in layoutInfoCopy">
            <div v-if="item.type == 'table'" :key="item.id" v-show="currentShowLayout.id == item.id" class="ds-datagrid-content-wrap" :style="{'height':contentWrapHeight, 'background-color': item.backgroundColor}">
              <DsfDataGridTable
                ref="table"
                :columns="columns"
                :is-design="false"
                :border="true"
                :fit="true"
                :data="data"
                :height="tableHeight"
                :row-key="getRowKey"
                :checkbox-column="checkboxColumn"
                :index-column="indexColumn"
                :operate-column="operateColumn"
                :row-buttons="rowButtons"
                :is-single-checked="isSingleChecked"
                :header-cell-class-name="getHeaderClass"
                :span-method="spanMethod"
                :customer-order="customerOrder"
                :is-re-draw-hander="isReDrawHander"
                :selectable="selectable"
                stripe
                @select="handleSelection"
                @select-all="handleSelectionAll"
                @sort-change="tableSort">
                <slot></slot>
                <template v-slot:[column.columnId]="scope" v-for="column in getLastColumns()">
                  <slot :name="column.columnId" v-bind="scope"></slot>
                </template>
              </DsfDataGridTable>
            </div>
            <div v-else-if="isShow && currentShowLayout.id == item.id" :key="item.id" :style="{'height':contentWrapHeight, 'background-color': item.backgroundColor}" class="ds-datagrid-content-wrap" v-infinite-scroll="loadData" :infinite-scroll-immediate="false" :infinite-scroll-distance="100" :infinite-scroll-disabled="infiniteScrollDisabled">
              <el-checkbox v-if="checkboxColumn.show && !isSingleChecked" v-model="checkAll" :indeterminate="multipleSelection.length > 0 && checkedDatas.length < data.length" style="padding-left: 8px" @change="handleCheckAllChange">
                全选
              </el-checkbox>
              <div :ref="'layout-' + item.id" class="ds-datagrid-layout" :style="{width: `calc(100% + ${item.attr.marginLeft})`, 'margin-left': `calc(0px - ${item.attr.marginLeft})`}">
                <div v-for="(row, $index) in data" :key="row._id" class="ds-datagrid-layout-item" :style="item.style">
                  <slot :name="item.id" v-bind="{row, $index}"></slot>
                </div>
                <div v-for="i in (item.alternateNum || 0)" :key="i" :style="[item.style, {height: 0, 'margin-bottom': 0}]"></div>
              </div>
              <template v-if="hasInfiniteScroll">
                <p v-if="data.length < pageCount || loading" style="text-align: center;">加载中...</p>
                <p v-else style="text-align: center;">没有更多了</p>
              </template>
            </div>
          </template>
        </template>
        <div v-if="showPagination" class="ds-datagrid-pager">
          <el-pagination ref="pagination" :hide-on-single-page="false" :total="pageCount" :page-sizes="limits" :current-page.sync="currentPage" background :layout="pagerLayout" @size-change="pageSizeChange"></el-pagination>
        </div>
      </template>
    </DsfPanel>
  </div>
</template>
<script>
import control from "_platform/mixins/control";
import dataResult from "_platform/mixins/dataResult";
import DsfDataGridTable from "./datagridTable";
import DsfDataGridCellContent from "./datagridCellContent";
import DsfSuperSearch from "./superSearch";
// import elTableInfiniteScroll from 'el-table-infinite-scroll';
let tableColumnSlotName = "table-column";
let cardSlotName = "card";

export default dsf.component({
  name: "DsfDataGrid",
  ctrlCaption: "数据列表",
  mixins: [control, dataResult],
  components: { DsfSuperSearch, DsfDataGridTable },
  provide() {
    return {
      $list: this,
      $buttonOwner: this,
      $dataPrefix: this.dataPrefix
    };
  },
  props: {
    /********基础属性*********/
    // 标题
    label: {
      type: String,
      default: "数据列表"
    },
    // 检索框提示文字
    searchPlaceholder: {
      type: String,
      default: "请输入检索关键字"
    },
    // 高度是否自适应
    fit: {
      type: Boolean,
      default: true
    },
    // 按钮栏设置
    buttons: {
      type: Array,
      default() {
        return [];
      }
    },
    // 最大按钮数量
    buttonsMaxNumber: {
      type: Number,
      default: 4
    },
    // 按钮栏更多按钮形态
    buttonsMoreStyle: {
      type: String,
      default: ""
    },
    // 按钮栏更多按钮文字
    buttonsMoreText: {
      type: String,
      default: "更多"
    },
    // 按钮栏位置
    buttonBarPosition: {
      type: String,
      default: "head"
    },
    // 是否显示头部
    showHead: {
      type: Boolean,
      default: true
    },
    // 是否显示标题
    showTitle: {
      type: Boolean,
      default: true
    },
    // 是否显示检索框
    showSearch: {
      type: Boolean,
      default: true
    },
    // 是否显示checkbox列
    checkboxColumn: {
      type: Object,
      default() {
        return {
          show: true,
          width: dsf.config.list.checkColumn.width
        };
      }
    },
    // 是否仅单选
    isSingleChecked: {
      type: Boolean,
      default: false
    },

    /********数据查询属性*********/
    // 挂载后立即请求数据
    mountedRequestData: {
      type: Boolean,
      default: true
    },
    // 查询条件配置
    searchInfo: {
      type: Array,
      default() {
        return [];
      }
    },
    // 高级查询设置
    superSearchInfo: {
      type: Array,
      default() {
        return [];
      }
    },
    // 高级查询行间隔线
    superSearchRowBorder: {
      type: Boolean,
      default: true
    },
    // 高级查询默认初始化行数
    superSearchDefaultRowsNumber: {
      type: Number,
      default: 2
    },
    // 默认排序
    defaultOrder: {
      type: Array,
      default() {
        return [];
      }
    },
    // 自定义排序
    customerOrder: {
      type: Array,
      default() {
        return [];
      }
    },
    // 是否显示分页(最多显示100条)
    showPagination: {
      type: Boolean,
      default: true
    },
    // 每页显示条数(逗号分隔)
    limits: {
      type: Array,
      default() {
        return [15, 20, 30, 40, 50, 100];
      }
    },
    // 分页组建的布局,自定义分页排版(逗号分隔)
    pagerLayout: {
      type: String,
      default: "total,prev,pager,next,jumper,sizes"
    },
    // 是否滚动加载
    scrollLoad: {
      type: Boolean,
      default: false
    },
    scrollPageSize: {
      type: Number,
      default: 15
    },

    /********通用属性*********/
    // 数据展示方式
    layoutInfo: {
      type: Array,
      default() {
        return [
          {
            id: "default",
            name: "表格",
            icon: "icon-biaodanzujian-biaoge",
            type: "table",
            typeName: "表格",
            backgroundColor: "#fff"
          }
        ];
      }
    },
    rowButtons: {
      type: Array,
      default() {
        return [];
      }
    },
    // 数据行按钮最大数量
    rowButtonsMaxNumber: {
      type: Number,
      default: 4
    },
    // 数据行更多按钮形态
    rowButtonsMoreStyle: {
      type: String,
      default: "icon-text"
    },
    // 数据行更多按钮文字
    rowButtonsMoreText: {
      type: String,
      default: "更多"
    },

    /********table属性*********/
    // 是否显示索引列
    indexColumn: {
      type: Object,
      default() {
        return {
          show: true,
          width: 60
        };
      }
    },
    // 是否显示操作按钮
    operateColumn: {
      type: Object,
      default() {
        return {
          show: true,
          width: 100
        };
      }
    },
    // 表头设置
    columns: {
      type: Array,
      default() {
        return [];
      }
    },
    /********高级属性*********/
    // 浏览范围
    browseScope: {
      type: Object,
      default() {
        return {
          scope: "2",
          unit: []
        };
      }
    },
    //导出配置
    exportOptions: {
      type: [Object, Array],
      default() {
        return {
          "rule-1": []
        };
      }
    },

    /********其他*********/
    // 数据字段前缀
    dataPrefix: {
      type: String,
      default: ""
    },
    superSearchButtonTrigger: {
      type: Boolean,
      default() {
        return false;
      }
    },
    showSuperSearchFilter: {
      type: Boolean,
      default: false
    },
    renderHeader: {
      type: Function,
      default: dsf.noop
    },
    //分页页码按钮最多显示个数
    pagerCount: {
      type: Number,
      default: 5
    },
    //一页显示多少条记录
    pageSize: {
      type: Number,
      default: 15
    },
    //设计模式下的模板
    templateData: {
      type: Array,
      default() {
        return [{}];
      }
    },
    //数据列表
    data: {
      type: Array,
      default() {
        return [];
      }
    },
    slots: {
      type: Array,
      default() {
        return [
          {
            name: "default",
            controls: []
          }
        ];
      }
    },
    getSecrchStr: {
      type: Function,
      default: null
    },
    // 仅对 type=selection 的列有效，类型为 Function，Function 的返回值用来决定这一行的 CheckBox 是否可以勾选
    // Function(row, index)
    selectable: {
      type: Function,
      default: null
    },
    contentWrapMargin: {
      type: String,
      default: "0px"
    },
    // 针对表格，合并行或列的计算方法。
    // Function({ row, column, rowIndex, columnIndex })
    spanMethod: {
      type: Function,
      required: false
    }
  },
  data() {
    return {
      isShow: false,
      currentPage: 1,
      pageCount: 0,
      isReDrawHander: false,
      searchStr: "",
      removeTable: false,
      loading: false,
      // 多选选中值
      multipleSelectionCopy: [],
      //高级检索高度
      superSearchHeight: 0,
      superValue: {},
      // 默认的展示形式
      currentShowLayout: {},
      // 多选
      checkAll: false,
      isIndeterminate: false,
      checkedDatas: [],
      elTable: null
    };
  },
  computed: {
    // 当前页数据所有的id
    dataIds() {
      return this.data.map(({ _id }) => _id);
    },
    // 多选/单选选中值
    multipleSelection: {
      get() {
        let sel = this.multipleSelectionCopy;
        if (this.isSingleChecked) {
          return (sel && sel.length && sel[0]) || null;
        } else {
          return sel || [];
        }
      },
      set(to) {
        if (this.isSingleChecked) {
          this.multipleSelectionCopy = (to && [to]) || [];
        } else {
          this.multipleSelectionCopy = to || [];
        }
      }
    },
    // 是否有滚动加载场景
    hasInfiniteScroll() {
      return !this.isDesign && !this.showPagination && this.layoutInfo.length > 1 && this.scrollLoad;
    },
    // 是否禁用滚动加载
    infiniteScrollDisabled() {
      if (!this.hasInfiniteScroll) {
        return true;
      }
      return this.loading || this.data.length >= this.pageCount;
    },
    layoutInfoCopy() {
      if (this.isDesign || this.layoutInfo.length < 2) {
        return this.layoutInfo;
      }
      return this.layoutInfo.filter((info) => {
        return info.type != "table" || (!this.hasInfiniteScroll && !info.hide);
      });
    },
    contentWrapHeight() {
      if (this.fit) {
        return `calc(100% - ${this.tableMarginTop}px - ${this.showPagination ? "57px" : "0px"} - ${this.contentWrapMargin})`;
      }
      return "auto";
    },
    // elTable() {
    //   return this.$refs.table;
    // },
    tableMarginTop() {
      return this.superSearchHeight > 0 ? this.superSearchHeight : 0;
    },
    tableHeight() {
      let fit = this.fit;
      if (fit) {
        return "100%";
      }
      return "auto";
    },
    panelHeight() {
      let fit = this.fit;
      if (this.isDesign) {
        return "100%";
      } else {
        return fit ? "100%" : "auto";
      }
    },
    getCss() {
      return [
        this.$getClass(),
        {
          fit: this.fit,
          "has-pagination": this.showPagination,
          "has-checkbox": this.checkboxColumn.show
        }
      ];
    },
    getListStyle() {
      return {
        visibility: this.isDesign || this.isShow ? "visible" : "hidden",
        height: this.panelHeight
      };
    },
    checkedNumber() {
      if (!checkboxColumn.show) {
        return 0;
      }
      if (!isSingleChecked) {
        return this.multipleSelection.length;
      }
      return this.multipleSelection ? 1 : 0;
    },
    superSearchBar() {
      return this.$refs.superSearchBar;
    }
  },
  created() {
    if (this.checkboxColumn && dsf.isUnDef(this.checkboxColumn.width)) {
      this.checkboxColumn.width = dsf.config.list.checkColumn.width;
    }
    createDesignDefaultColumns.call(this);
    this.updateSlots(this.layoutInfo);
    let pSize = _.find(this.limits, (s) => s == this.pageSize);
    if (!pSize) {
      this.$emit("update:pageSize", this.limits[0]);
    }
    //监听当前页面和页码发生变化后数据
    this.hasInfiniteScroll ||
      (this.unwatch = this.$watch(
        () => {
          return this.currentPage + "," + this.pageSize;
        },
        (v, ov) => {
          let pageNum = Math.ceil(this.pageCount / this.pageSize);
          if (this.currentPage > pageNum) {
            this.currentPage = pageNum <= 0 ? 1 : pageNum;
            return;
          }
          this.loadData();
        }
      ));

    this.currentShowLayout = this.layoutInfo[0];
  },
  beforeMount() {},
  mounted() {
    this.elTable = this.getElTable();
    if (!this.isDesign) {
      if (this.mountedRequestData) {
        this.$nextTick(() => {
          //如果没有高级检索则直接查询数据，否则需要等待高级检索组件加载loaded事件中调用
          if (!this.superSearchInfo || this.superSearchInfo.length <= 0) {
            this.isShow = true;
            this.currentPage = 1;
            this.loadData("superSearch");
          }
        });
      }
    }
  },
  updated() {
    this.doLayout();
  },
  beforeDestroy() {
    this.unwatch && this.unwatch();
    if (this.$refs.pagination) {
      this.$refs.pagination.$destroy();
    }
    this.data.splice(0, this.data.length);
  },
  destroyed() {},
  methods: {
    getElTable() {
      return this.$refs.table?.[0];
    },
    // 更新slots datagrid-hide
    updateSlots() {
      if (!this.isDesign) {
        return;
      }
      let slots = [this.getSlots("datagrid-hide")];
      this.layoutInfo.forEach((l) => {
        if (l.type == "table") {
          slots = slots.concat(this.getSlotsByList());
        } else if (l.type == "card") {
          slots.push(this.getSlots(l.id));
        }
      });
      this.slots = slots;
    },
    // 更新列表slots, 更新表头
    getSlotsByList() {
      let arr = this.getLastColumns();
      let slots = [];
      _.each(arr, (c, i) => {
        let columnId = `${tableColumnSlotName}-${i}`;
        let slot = _.find(this.slots, (s) => s.name == columnId);
        if (!slot) {
          let newSlot = (slot = {
            name: columnId,
            controls: [
              //此处默认追加一层组件,el-table貌似没有行数据加载过程中的控制事件，所以需要控制单元格中的子元素只能通过追加的一层组件完成
              dsf.designer.createComponent(DsfDataGridCellContent, {
                "slot-name": "default"
              })
            ]
          });
          this.slots.push(newSlot);
          slot = newSlot;
        } else {
          slot.name = columnId;
        }
        c.columnId = columnId;
        slot.scope = "scope";
        slot.repeat = true;
        // slot.textProxy = this.tableTextProxy;
        slot.dataKey = "row";
        slots.push(slot);
      });
      return slots;
    },
    // 更新slots
    getSlots(name) {
      let slot = _.find(this.slots, (s) => s.name == name);
      if (!slot) {
        slot = {
          name,
          controls: [],
          scope: "scope",
          dataKey: "row",
          repeat: true,
          textProxy: true
        };
      }
      return slot;
    },
    // 自定义多选：全选
    handleCheckAllChange(val) {
      let isSelected = !!val;
      if (isSelected) {
        this.checkedDatas = [].concat(this.dataIds);
      } else {
        this.checkedDatas = [];
      }
      if (this.elTable) {
        this.data.forEach((item) => {
          this.elTable.toggleRowSelection(item, isSelected);
        });
        this.$nextTick(() => {
          this.multipleSelection = this.elTable.selection;
        });
      } else if (isSelected) {
        this.multipleSelection = this.multipleSelection.concat(this.data.filter((d) => this.multipleSelection.findIndex((se) => se._id == d._id) == -1));
      } else {
        dsf.array.removeWith(this.multipleSelection, (el) => {
          return this.dataIds.indexOf(el._id) > -1;
        });
      }
    },
    // 自定义多选：单个选择
    handlecheckedDataChange(row, isSelected) {
      if (this.elTable) {
        this.elTable.toggleRowSelection(row, isSelected);
        this.multipleSelection = this.elTable.selection;
      } else if (isSelected) {
        this.multipleSelection.push(row);
      } else {
        let index = this.multipleSelection.findIndex((item) => item._id == row._id);
        this.multipleSelection.splice(index, 1);
      }
      let index = this.checkedDatas.indexOf(row._id);
      if (isSelected && index == -1) {
        this.checkedDatas.push(row._id);
      } else if (!isSelected && index > -1) {
        this.checkedDatas.splice(index, 1);
      }
      this.$nextTick(() => {
        this.checkAll = this.checkedDatas.length === this.dataIds.length;
      });
    },
    getLastColumns(column) {
      return getLastColumns.call(this,column)
    },
    reset() {
      this.clearSelect();
      // this.pageCount = 0;
      this.currentPage = 1;
    },
    //更新索引
    indexMethod(index) {
      return (this.currentPage - 1) * this.pageSize + index + 1;
    },
    //检索框查询
    search() {
      if (this.isDesign) {
        return;
      }
      this.reloadData(1);
    },
    headerDragend() {
      //更新一下列表的布局，否则宽高会出问题
      this.doLayout();
    },
    resize() {
      if (this.currentShowLayout.id == "default") {
        this.doLayout();
      } else if (!this.isDesign) {
        let $layoutBox = this.$refs["layout-" + this.currentShowLayout.id];
        if ($layoutBox) {
          $layoutBox = $layoutBox.length ? $layoutBox[0] : $layoutBox;
          let $layoutItem = $layoutBox?.querySelector(".ds-datagrid-layout-item");
          if ($layoutItem && $layoutItem.offsetWidth > 0) {
            let columns = Math.round(window.innerWidth / $layoutItem.offsetWidth);
            if (!isNaN(columns) && columns > 2 && (this.currentShowLayout.alternateNum || 0) < columns - 1) {
              this.$set(this.currentShowLayout, "alternateNum", columns - 1);
            }
          }
        }
      }
      if (this.superSearchBar) {
        this.superSearchBar.resize();
      }
    },
    pageSizeChange(evt) {
      if (this.isSingleChecked) {
        this.multipleSelection = null;
      }
      this.$emit("update:pageSize", evt);
    },
    pageIndexChange(evt) {
      this.currentPage = evt;
      this.loadData();
    },
    addColumn(column, index, parentColumn) {
      let defaultColumn = {
        label: "",
        columnId: "",
        level: 0,
        isLast: false,
        hide: false,
        headerAlign: "center",
        align: "center",
        resizable: false,
        width: "auto",
        children: []
      };
      let col = dsf.mix({}, defaultColumn, column);
      if (parentColumn) {
        if (dsf.isUnDef(index)) {
          parentColumn.children.push(col);
          col.level = parentColumn.level + 1;
        } else {
          parentColumn.children.splice(index, 0, col);
        }
      } else {
        dsf.isUnDef(index) ? this.columns.push(col) : this.columns.splice(index, 0, col);
      }
      return col;
    },
    // 某些时候el-table行高计算不会被触发，导致其内部行错位，此时调用此方法触发其内部的行高计算
    doLayout() {
      this.getElTable()?.doLayout();
    },
    getRowKey(row) {
      return row._id;
    },
    getQueryInfo() {
      let orderArr = [];
      if (this.getSecrchStr) {
        this.searchStr = this.getSecrchStr();
      }
      let queryData = {
        searchValue: this.searchStr.split(/\s+/g).join(" ")
      };
      if (this.searchInfo) {
        _.each(this.searchInfo, (item) => {
          if (item.type == "1") {
            queryData[item.code] = item.valueExpress;
          }
        });
      }
      if (this.superValue) {
        _.each(this.superValue, (item, i) => {
          queryData[i] = item;
        });
      }
      if (this.defaultOrder) {
        let customOrderArr = _.map(this.customerOrder, (order) => {
          return order.sortType
            ? {
                code: order.field,
                sort: order.sortType
              }
            : undefined;
        });
        customOrderArr = _.filter(customOrderArr, (order) => order);
        let defaultOrderArr = _.map(this.defaultOrder, (order) => {
          let index = _.findIndex(customOrderArr, (f) => f.code == f.field);
          if (index < 0) {
            return {
              code: order.field,
              sort: order.sortType
            };
          }
        });
        orderArr = [...customOrderArr, ...defaultOrderArr];
      }
      return {
        order: orderArr,
        query: queryData
      };
    },
    loadData(from) {
      // console.log((new Date()).getTime())
      if (this.$vm) {
        this.loading = true;
        let listData = [];
        let queryInfo = this.getQueryInfo();
        let pageSize = this.hasInfiniteScroll ? this.scrollPageSize : !this.showPagination ? 100 : this.pageSize;
        let params = {
          pageNum: this.currentPage || 1,
          pageSize,
          query: JSON.stringify(queryInfo.query),
          order: JSON.stringify(queryInfo.order),
          namespace: this.$vm.nameSpace,
          pageName: this.$vm.pageName
        };
        let params2 = {
          pageNum: this.currentPage || 1,
          pageSize,
          query: queryInfo.query,
          namespace: this.$vm.nameSpace,
          pageName: this.$vm.pageName
        };
        let url = "/meta/list/data";
        dsf.mix(params, this.queryString);
        this.$http
          .get(url, params, true)
          .then(({ data }) => {
            if (data.state != 20000) {
              throw data;
            } else {
              listData = data.data;
              if (listData.length) {
                //验证数据是否有_id。没有直接报错，不许乱配
                if (!("_id" in listData[0])) {
                  throw "结果数据未发现“_id”";
                }
              }
              let url2 = "/meta/list/count";
              dsf.mix(params2, this.queryString);
              return this.$http.get(url2, params, true);
            }
          })
          .then(({ data }) => {
            this.pageCount = data.data.count;

            // 非滚动加载 或 检索加载
            if (!this.hasInfiniteScroll || ["reloadData", "superSearch"].indexOf(from) > -1) {
              this.$emit("update:data", listData);
            }
            // 滚动加载
            else {
              this.$emit("update:data", this.data.concat(listData));
            }
            this.hasInfiniteScroll && this.currentPage++;

            this.defaultCheckedRow();
          })
          .catch(dsf.httpError)
          .finally(() => {
            listData = null;
            window.setTimeout(() => {
              this.doLayout();
              this.$nextTick(() => {
                this.loading = false;
              });
            });
          });
      }
    },
    // _loadData:_.debounce
    //刷新数据，不变更页面等信息只重新当前页面的查询数据
    refreshData() {
      this.loadData();
    },
    reloadData(resetPageIndex) {
      this.clearSelect();
      if (this.$refs.buttonbar) {
        this.$refs.buttonbar.$forceUpdate();
      }
      if (this.$refs.superSearchBar) {
        this.$refs.superSearchBar.$forceUpdate();
      }
      if (this.hasInfiniteScroll) {
        resetPageIndex = 1;
      }
      if (resetPageIndex && this.currentPage != resetPageIndex) {
        this.currentPage = resetPageIndex;
      }
      this.loadData("reloadData");
    },
    updateSubControls(ctrlProps, ctrl) {
      return ctrlProps;
    },
    getSelectedRows() {
      return this.multipleSelection;
    },
    // elTable 多选监听(单个选项被勾选)
    handleSelection(val, row) {
      this.multipleSelection = val;
      if (!this.isSingleChecked && this.layoutInfo.length > 1) {
        let index = this.checkedDatas.indexOf(row._id);
        if (index == -1) {
          this.checkedDatas.push(row._id);
          this.checkAll = this.checkedDatas.length == this.data.length;
        } else {
          this.checkedDatas.splice(index, 1);
          this.checkAll = false;
        }
      }
    },
    // elTable 多选监听(全选)
    handleSelectionAll(val) {
      if (this.elTable && !this.isSingleChecked && this.layoutInfo.length > 1) {
        let isSelected = this.elTable.$refs.tableHeader.isAllSelected;
        // let isSelected = this.multipleSelection.length < val.length;
        if (isSelected) {
          this.checkedDatas = [].concat(this.dataIds);
          this.checkAll = true;
        } else {
          this.checkedDatas = [];
          this.checkAll = false;
        }
      }
      this.multipleSelection = val;
    },
    // todo 多选时：row 当前行数据 selected 是否选中
    toggleSelect(row, selected) {
      if (this.elTable && row) {
        let arr = !selected ? this.multipleSelection : this.data;
        for (let i = 0; i < arr.length; i++) {
          const v = arr[i];
          if (v._id == row || v._id == row._id) {
            this.elTable.toggleRowSelection(this.multipleSelection[i], !selected ? false : true);
            break;
          }
        }
      }
    },
    // 多选时：清空用户的选择
    clearSelect() {
      this.elTable && this.elTable.clearSelection();
      this.checkedDatas = [];
      this.checkAll = false;
      this.multipleSelection = null;
    },
    getHeaderClass({ row, column, rowIndex, columnIndex }) {
      let classList = [];
      if (column.columnKey) {
        let result = _.filter(this.customerOrder, (order) => order.column == column.columnKey);
        if (result.length > 0) {
          let order = result[0];
          if (order.sortType) {
            let sort = order.sortType + "ending";
            column.order = sort;
            classList.push(sort);
          } else {
            column.order = null;
          }
        }
      }
      return classList.join(" ");
    },
    tableSort({ column, prop, order }) {
      let result = _.filter(this.customerOrder, (colOrder) => {
        return colOrder.column == column.columnKey;
      });
      if (result.length > 0) {
        let columnOrder = result[0];
        let sort = order ? order.replace("ending", "") : null;
        columnOrder.sortType = sort;
        this.loadData();
      }
    },
    defaultCheckedRow() {},
    loadSelected() {},
    getReturnValue() {
      return this.getSelectedRows();
    },
    superSearchLoaded(v) {
      this.isShow = true;
      if (!this.isDesign && this.mountedRequestData) {
        this.superValue = v;
        this.currentPage = 1;
        this.loadData("superSearch");
      }
    },
    superSearchChange(v, istigger) {
      // this.$emit("update:superValue", v);
      if (istigger) {
        this.superValue = v;
        this.currentPage = 1;
        this.loadData("superSearch");
      }
    }
  },
  watch: {
    columns: {
      handler() {
        this.updateSlots();
        this.isReDrawHander = true;
        this.$nextTick(() => {
          this.isReDrawHander = false;
        });
      },
      deep: true
    },
    data: {
      handler(v) {
        let checkedDatas = [];
        for (let i = 0; i < v.length; i++) {
          !("$editing" in v[i]) && this.$set(v[i], "$editing", false);
          !("$hash" in v[i]) && this.$set(v[i], "$hash", dsf.uuid());
          if ("$order" in v[i]) {
            this.$set(v[i], "$order", i);
          } else {
            v[i]["$order"] = i;
          }
          v[i].$index = i;
          // 复现多选
          if (!this.isSingleChecked && this.layoutInfo.length > 1) {
            if (this.multipleSelection.find((item) => item._id == v[i]._id)) {
              checkedDatas.push(v[i]._id);
            }
          }
        }
        if (!this.isSingleChecked && this.layoutInfo.length > 1) {
          this.checkedDatas = checkedDatas;
          this.checkAll = checkedDatas.length == v.length;
        }
        this.$nextTick(() => {
          this.resize();
        });
      }
    },
    tableMarginTop() {
      this.$nextTick(() => {
        this.doLayout();
      });
    },
    layoutInfo(to) {
      this.updateSlots(to);
    },
    "currentShowLayout.id"(to) {
      to &&
        this.$nextTick(() => {
          this.resize();
        });
    }
  },
  design: {
    isMask: false,
    fit: true,
    layout: {
      exclude: ["value"]
    },
    metadata: {
      isDeep: false,
      create(root) {
        let childMetaData = [];
        dsf.designer.recursionLayoutTree(this, null, null, (child) => {
          if (child != this) {
            let designOptions = child.$options.design || {};
            let metadataNode = designOptions.metadata || {};
            let result = metadataNode.create && metadataNode.create.call(child, root);
            if (result && result.metadata) {
              childMetaData.push(result);
              let isDeep = dsf.isDef(metadataNode.isDeep) ? metadataNode.isDeep : true;
              if (!isDeep) {
                return false;
              }
            }
          }
        });

        let query = {};
        let searchBox = [],
          attachSearch = [];
        for (let i = 0; i < this.searchInfo.length; i++) {
          let s = this.searchInfo[i];
          let item = null;
          if (s.type == "0") {
            item = {
              code: s.code,
              operator: s.operation,
              valueKey: "searchValue"
            };
            searchBox.push(item);
          } else {
            item = {
              code: s.code,
              operator: s.operation,
              valueKey: s.code
            };
            attachSearch.push(item);
            s.metadata_fullcode = item.code;
          }
        }
        //添加将检索框条件
        let seachBoxitem = { logic: "or", items: [] };
        seachBoxitem.items.push(...searchBox);

        //添加默认附加条件
        let attachSearchItem = { logic: "and", items: [] };
        attachSearchItem.items.push(...attachSearch);

        let searchitems = { logic: "and", items: [] };
        searchitems.items.push(seachBoxitem);
        searchitems.items.push(attachSearchItem);
        // seachBoxitem.items.length > 0 && searchitems.items.push(seachBoxitem);
        // attachSearchItem.items.length > 0 && searchitems.items.push(attachSearchItem);
        let superSearchItem = { logic: "and", items: [] };
        _.each(this.superSearchInfo, (el) => {
          let code = null;
          let operator = "";
          let valueKey = null;
          if (el.searchType == "enum") {
            let useFields = [];
            if (el?.children?.length > 0) {
              useFields = _.map(el.children, (f) => f.join == 1);
              useFields = useFields.slice(0, 1);
            } else {
              useFields = [el];
            }
            if (useFields.length == 1) {
              code = useFields[0].field;
              operator = useFields[0].operator;
              valueKey = useFields[0].field;
              if (operator == "in" || operator == "eq") {
                operator = "in";
              }
              let item = {
                code: code,
                operator: operator == "in" || operator == "eq" ? "in" : operator,
                valueKey: code
              };
              superSearchItem.items.push(item);
            }
          } else if (el.searchType == "text") {
            code = el.code;
            operator = el.operator;
            valueKey = el.code;
            superSearchItem.items.push({
              code: code,
              operator: operator,
              valueKey: valueKey
            });
          } else if (el.searchType == "datetime") {
            let useFields = [];
            if (el?.children?.length > 0) {
              useFields = _.map(el.children, (f) => f.join == 1);
              useFields = useFields.slice(0, 1);
            } else {
              useFields = [el];
            }
            if (useFields.length == 1) {
              superSearchItem.items.push({
                code: useFields[0].field,
                operator: useFields[0].operator,
                valueKey: useFields[0].field,
                dataType: "datetime"
              });
            }
          }
        });
        searchitems.items.push(superSearchItem);
        query = searchitems;
        let exportRules = {};
        _.each(this.exportOptions, (v, k) => {
          if (v && v.length > 0) {
            exportRules[this.caption + "." + k] = v;
          }
        });
        let metadata = {
          //查询条件
          query: query,
          //可以导出列的配置
          export: {
            ...exportRules
          },
          //浏览范围
          browseScope: this.browseScope,
          _self: this
        };
        if (childMetaData.length) {
          this.$emit("update:dataPrefix", childMetaData[0].metadata.at.replace(/\./g, "_"));
        }
        return [metadata, ...childMetaData];
      }
    }
  }
});

function createDesignDefaultColumns() {
  if (!this.isDesign) {
    return;
  }
  if (this.columns.length == 0) {
    let size = 4;
    for (let i = 0; i < size; i++) {
      this.addColumn({
        label: `列${i}`,
        level: 0
      });
    }
  }
}
//获取列头中各个单元格最大的单元格
function getLastColumns(column) {
  let arr = [];
  deep.call(this,(c) => {
    if (!c.children || !c.children.length) {
      c.isLast = true;
      arr.push(c);
    } else {
      c.columnId = "";
      c.isLast = false;
    }
  }, column);
  return arr;
}
function deep(callback, column) {
  var fn = function (columns, level) {
    let columnIndex = 0;
    _.each(columns, (c, i) => {
      callback && callback(c, i, columnIndex, level);
      fn(c.children, level + 1);
      columnIndex++;
    });
  };
  let cols = column ? column : this.columns;
  cols = dsf.type(cols) !== "array" ? [cols] : cols;
  fn(cols, 0);
}
</script>