<template>
  <div class="ds-control ds-form-item ds-radio-box" :class="getCss">
    <template v-if="simple && readOnly && !isDesign">
      <DsfTextProxy v-model="value" v-bind="_self.$props" :show-label="false"></DsfTextProxy>
      <slot name="error">
        <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
      </slot>
    </template>
    <template v-else>
      <label v-if="showLabel" class="ds-form-label" :style="getLabelWidthStyle()">
        {{ label }}
        <i v-if="dataCapion" class="iconfont icon-bangzhuzhongxin ds-control-helper" @click="showHelper"></i>
      </label>
      <div class="ds-form-block" :class="{border: border || isButton}" :style="getFormItemBlockMarginLeft()">
        <!--只读状态只显示div-->
        <div v-if="readOnly" class="ds-form-readonly">
          <DsfTextProxy v-model="value" v-bind="_self.$props" :show-label="false"></DsfTextProxy>
        </div>
        <el-radio-group v-else-if="isButton" v-model="radioValue" :disabled="disabled">
          <template v-if="isDesign&&!items.length">
            <el-radio-button label="1">数据1</el-radio-button>
            <el-radio-button label="2">数据2</el-radio-button>
            <el-radio-button label="3">数据3</el-radio-button>
            <el-radio-button label="4">数据4</el-radio-button>
            <el-radio-button label="5">数据5</el-radio-button>
          </template>
          <template v-else>
            <el-radio-button v-for="(item, index) in items" :key="index" ref="radio" :label="item.value" :disabled="disabledItemValues.indexOf(item.value) > -1" @click.native.stop="itemClick(item, index)">
              {{ item.text }}
            </el-radio-button>
          </template>
          <el-radio-button v-if="isOther && !hasOther" label="else">
            其他
          </el-radio-button>
        </el-radio-group>
        <el-radio-group v-else v-model="radioValue" :disabled="disabled">
          <template v-if="isDesign&&!items.length">
            <el-radio :border="border" label="1">数据1</el-radio>
            <el-radio :border="border" label="2">数据2</el-radio>
            <el-radio :border="border" label="3">数据3</el-radio>
            <el-radio :border="border" label="4">数据4</el-radio>
            <el-radio :border="border" label="5">数据5</el-radio>
          </template>
          <template v-else>
            <el-radio v-for="(item, index) in items" :key="index" ref="radio" :label="item.value" :border="border" :disabled="disabledItemValues.indexOf(item.value) > -1" @click.native.stop="itemClick(item, index)">
              {{ item.text }}
            </el-radio>
          </template>
          <el-radio v-if="isOther && !hasOther" label="else">
            其他
          </el-radio>
        </el-radio-group>
        <slot name="error">
          <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
        </slot>
      </div>
    </template>
  </div>
</template>

<script>
import formControl from "_platform/mixins/formControl";

let missItem = { value: "", text: "" };

/**
 * @class DsfRadioBox
 * @augments FormControl
 * @description 单选框
 * @example
 * <DsfRadioBox :items="[{ value: '1', text: '选项1' }]" :value="value"></DsfRadioBox>
 */
export default dsf.component({
  name: "DsfRadioBox",
  mixins: [formControl],
  ctrlCaption: "单选",
  /**
   * @memberof DsfRadioBox
   * @name Props 属性
   * @property {Boolean} [simple = false] 简单模式
   * @property {Object|String|Number|Boolean} [value = false] 简单模式
   * @property {DsfRadioBoxItem[]} [items = [{ value: "1", text: "选项1" }]] 选择项
   * @property {Number} [type = 0] 模式 0: 普通；1：带边框；2：按钮
   * @property {Boolean} [isOther = false] 是否显示其它
   * @property {Object} [metadata = dsf.metadata.get("items-meta-data")] 元数据
   */
  /**
   * @namespace DsfRadioBoxItem
   * @description 单选的选项
   * @property {String} [DsfRadioBoxItem.value] 选中值
   * @property {String} [DsfRadioBoxItem.text] 显示
   */
  props: {
    simple: {
      type: Boolean,
      default: false,
    },
    value: {
      type: [Object, String, Number, Boolean],
      default() {
        return missItem;
      },
    },
    items: {
      type: Array,
      default() {
        return [];
      },
    },
    // 需要禁用选项的value值得集合
    disabledItemValues: {
      type: Array,
      default() {
        return [];
      },
    },
    type: {
      type: Number,
      validator: function (value) {
        // 0: 普通；1：带边框；2：按钮
        return [0, 1, 2].indexOf(parseInt(value)) !== -1;
      },
      default: 0,
    },
    // 是否可取消
    cancel: {
      type: Boolean,
      default: false,
    },
    // 是否其他
    isOther: {
      type: Boolean,
      default: false,
    },
    formatter: {
      type: String,
      default: "textValue^text"
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("items-meta-data");
      },
    },
  },
  data() {
    return {
      timeout: null
    }
  },
  computed: {
    radioValue: {
      get() {
        return this.value ? this.value.value : '';
      },
      set(v) {
        let selected = _.find(this.items || [], (item) => {
          return item.value === v;
        });
        let newValue = selected || missItem;
        this.emitValueChange(newValue);
        this.$nextTick(() => {
          this.emitFormValidate(newValue);
        })
      },
    },
    border() {
      return this.type === 1;
    },
    isButton() {
      return this.type === 2;
    },
    // 判断items中是否已经存在其他
    hasOther() {
      return this.items.findIndex((v) => v.value === "else") > -1;
    },
  },
  watch: {
    // 监听值
    value(v) {
      if (
        dsf.type(v) === "string" ||
        dsf.type(v) === "number" ||
        dsf.type(v) === "boolean"
      ) {
        let item = _.find(this.items, (item) => {
          return item.value === v;
        });
        this.emitValueChange(item ? item : missItem);
      }
    },
  },
  destroyed() {
    clearTimeout(this.timeout);
  },
  methods: {
    itemClick(item, index) {
      let $radio = this.$refs.radio[index].$el;
      if (!this.cancel || this.disabled || item.disabled) {
        $radio.removeAttribute("data-same");
        return;
      }
      let isSame = this.radioValue == item.value;
      let dataSame = $radio.getAttribute("data-same");
      if (dataSame == null) {
        $radio.setAttribute("data-same", isSame ? "1" : "0");
        return;
      }
      if (isSame && dataSame == "1") {
        this.radioValue = null;
      }
      $radio.removeAttribute("data-same");
    }
  },
  design: {
    layout: {
      //#开头表示该属性在预览模式下会被保留
      exclude: ["value", "#items"],
    }
  }
});
</script>
