<template>
  <div class="ds-control ds-design" :style="{height: height}">
    <el-image v-if="url" :style="{ width: width, height: height, display: 'block' }" :src="url" :preview-src-list="[url]">
      <div slot="error" class="image-slot" style="text-align: center; font-size: 30px">
        <i class="el-icon-picture-outline"></i>
      </div>
    </el-image>
  </div>
</template>
<script>
import control from "_platform/mixins/control";
export default dsf.component({
  name: "DsfUploadDesignImg",
  mixins: [control],
  ctrlCaption: "上传设计图",
  data() {
    return {};
  },
  props: {
    url: {
      type: String,
      default: ""
    },
    width: {
      type: [Number, String],
      default: "100%"
    },
    height: {
      type: [Number, String],
      default: "100%"
    }
  },
  created() {},
  methods: {},
  watch: {
    height: {
      handler(v) {
        this.isDesign && this.$dispatch("design-height-change", v);
      },
      immediate: true
    }
  }
});
</script>
