import DsfExamFill from "./fill.vue";
import DsfExamTrueFalse from "./trueFalse.vue";
import DsfExamRadioBox from "./radioBox.vue";
import DsfExamCheckBox from "./checkBox.vue";
import DsfExamSubjective from "./subjective.vue";

function install(Vue) {
    Vue.component(DsfExamFill.name, DsfExamFill);
    Vue.component(DsfExamTrueFalse.name, DsfExamTrueFalse);
    Vue.component(DsfExamRadioBox.name, DsfExamRadioBox);
    Vue.component(DsfExamCheckBox.name, DsfExamCheckBox);
    Vue.component(DsfExamSubjective.name, DsfExamSubjective);
  }
  
  if (typeof window !== "undefined" && window.Vue) {
    install(window.Vue);
  }
  
  export { install, DsfExamFill, DsfExamTrueFalse, DsfExamRadioBox, DsfExamCheckBox,DsfExamSubjective };
  export default {
    install
  };
