<template>
  <div :style="{ width: width, height: height, padding: paddingSty }" class="ds-control">
    <div class="ds-home-tab-panel">
      <div v-show="showType=='card'" class="tab-ul-box">
        <ul class="tab-ul">
          <template v-for="(item, index) in slots">
            <li
              v-if="!item.hide"
              :key="index"
              @click="changeActive(item)"
              :class="{ 'layui-this': isActive(item), 'only-one': slots.length == 1 }"
            >{{ getTitle(item) }}</li>
          </template>
        </ul>
      </div>
      <div v-show="showType!='card'" :class="{'card-warp': showType=='card', 'button-warp': showType=='button'}" class="title-warp-com">
        <div v-show="showType=='info'" :class="{ 'has-more': showMore }" class="title-info one-line">
          <span v-for="item in slots" :key="uuid(item)" :class="{ 'layui-this': isActive(item) }" @click="changeActive(item)" class="info">{{ getTitle(item) }}</span>
        </div>
        <div v-show="showType=='button'" class="tab-button-box">
          <el-button-group>
            <el-button v-for="item in slots" :key="uuid(item)" :class="{ 'active': isActive(item)}" @click="changeActive(item)" plain>{{ getTitle(item) }}</el-button>
          </el-button-group>
        </div>
      </div>
      <div v-if="showMore" @click="more" :class="{'title-more-button':showType=='button'}" class="title-more-oth">
        <slot name="right">{{ rightFont }}<i class="iconfont" :class="rightIcon"></i></slot>
      </div>
      <template v-for="item in slots">
        <div
          v-if="!item.hide && (!item.lazy || isActive(item))"
          v-show="isActive(item)"
          :key="uuid(item)"
          :slot-name="uuid(item)"
          :class="{ 'design-padding': isDesign }"
          class="tab-panel-content"
        >
          <slot :name="uuid(item)"></slot>
        </div>
      </template>
    </div>
  </div>
</template>
<script>
import control from "_platform/mixins/layout";

export default dsf.component({
  name: "DsfHomeTabPanel",
  ctrlCaption: "Tab面板",
  mixins: [control],

  /** 后代可能使用的方法
    getTabData：生成一个tab数据的方法
    getTabObj：通过唯一标识获取tab数据对象
   */
  provide() {
    return {
      $tabs: this.$slots,
      $getTabObj: this.getTabObj,
      $getTabData: this.getTabData,
    };
  },

  props: {
    // 宽度
    width: {
      type: String,
      default: "100%",
    },
    // 高度
    height: {
      type: String,
      default: "auto",
    },
    // 显示更多按钮
    showMore: {
      type: Boolean,
      default: true,
    },
    // 可拖拽容器
    slots: {
      type: Array,
      default() {
        return [];
      },
    },
    padding: {
      type: Array,
      default() {
        return [0, 0, 0, 0];
      },
    },
    rightFont: {
      type: String,
      default: "更多",
    },
    rightIcon: {
      type: String,
      default: "icon-shuangjiantouyou",
    },
    showType: {
      type: String,
      default: "info",
    },
  },

  data() {
    return {
      active: "", //激活tab的key
    };
  },

  components: {},

  computed: {
    paddingSty: function () {
      return this.padding.join("px ") + "px";
    },
  },

  mounted() {},

  created() {
    if (this.isDesign && this.slots.length == 0) {
      for (let i = 0; i < 3; i++) {
        this.slots.push(this.getTabData("Tab" + (i + 1)));
      }
    }
    !!this.slots[0] && (this.active = this.slots[0]["name"] || this.slots[0]["title"]);
  },

  methods: {
    // 提供给后代生成一个tab数据的方法
    getTabData(title, name) {
      return {
        title: title || "Tab",
        name: name || dsf.UUID().slice(8),
        hide: false,
        badge: null,
        lazy: false,
        url: "",
        controls: [],
      };
    },

    // 唯一表示，name不存在就取title
    uuid(item) {
      return item.name || item.title;
    },
    // 判断当前item是否是激活的
    isActive(item) {
      if (!item) {
        return false;
      }
      return this.uuid(item) === this.active;
    },
    // 改变当前激活
    changeActive(item) {
      this.active = this.uuid(item);
    },
    // tab列表更改后重新计算默认激活
    tabChange() {
      this.active = this.uuid(this.slots[0]);
    },
    // tab名称
    getTitle(item) {
      if (dsf.isDef(item.badge)) {
        return `${item.title}(${item.badge})`;
      }
      return `${item.title}`;
    },
    // 通过唯一标识获取tab数据对象
    getTabObj(name) {
      let tab = window._.find(this.slots, (item) => {
        return item.name === name;
      });
      return tab;
    },
    // 跳转更多
    more() {
      if (this.isDesign) {
        return false;
      }
      let tab = this.getTabObj(this.active);
      if (!!tab && tab.url) {
        window.open(dsf.url.getWebPath(tab.url), "_blank");
      }
    },
  },
});
</script>
