<template>
  <div class="ds-control ds-navtab" :class="{ 'ds-no-padding': !showPadding }" :style="{ height: isDesign ? '100%' : tabHeight }">
    <el-tabs v-model="activeName" :tab-position="tabPosition" :type="tabStyle == 'customer-tab-pad' ? '' : tabStyle" :closable="enableDelete" @tab-click="tabClick" @tab-remove="tabRemove">
      <slot></slot>
      <el-tab-pane
        v-for="d in slots.filter(tab => {
          return isHide(tab);
        })"
        :key="d.name"
        :disabled="isdisable(d)"
        :label="d.title"
        :name="d.name"
        :slot-name="d.name"
        :lazy="isLazy(d)"
        :style="{'padding':$padding}"
      >
        <span slot="label">
          {{ d.title }}
          <span v-if="d.badge !== null" class="el-badge" v-html="d.badge"></span>
        </span>
        <slot :name="d.name"></slot>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import tabControl from '_platform/mixins/tabControl';
export default dsf.component({
  name: 'DsfNavTab',
  ctrlCaption: '选项卡',
  mixins: [tabControl],
  props: {}
});
</script>
