<template>
  <ul class="ds-aside-menu" :class="['level' + level]">
    <template v-for="(item, index) in menuList">
      <dsf-default-home-menu-item
        :key="item.menu_id"
        :menu="item"
        :index="index"
        :level="level"
      />
    </template>
  </ul>
</template>

<script>
export default {
  name: 'DsfDefaultHomeMenu',
  props: {
    menuList: {
      type: Array,
      default() {
        return []
      }
    },
    level: {
      type: Number,
      default: 1
    }
  },
  data() {
    return {
      showKey: ''
    }
  },
}
</script>