<template>
  <div class="ds-control ds-form-item ds-system" :class="getCss">
    <template v-if="simple && !isDesign">{{ value }}</template>
    <template v-else>
      <label v-if="showLabel" class="ds-form-label" :style="getLabelWidthStyle()">{{ label }}</label>
      <div class="ds-form-block" :style="getFormItemBlockMarginLeft()">
        {{ value }}
      </div>
    </template>
  </div>
</template>

<script>
import formControl from "../../../mixins/formControl";

export default dsf.component({
  name: "DsfCreateUserName",
  mixins: [formControl],
  ctrlCaption: "创建人",
  props: {
    caption: {
      type: String,
      default: dsf.config.kw.createusername,
    },
    label: {
      type: String,
      default: "创建人",
    },
    simple: {
      type: Boolean,
      default: false,
    },
    showLabel: {
      type: Boolean,
      default: true,
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("system-meta-data", {
          code: dsf.config.kw.createusername,
          valueAttributes: [
            {
              name: "创建人姓名",
              code: dsf.config.kw.createusername,
              type: dsf.metadata.getDataType("string"),
              length: "50",
              defaultValue: null,
              unit: null,
            },
          ],
        });
      },
    },
  },
});
</script>
