<template>
  <div class="ds-control">
    <div
      v-if="isDesign || sPID"
      class="ds-control ds-form-item ds-flowprocess"
      :class="{ hideYj: isDesign ? hideYj : isHideYj, hideNode: isDesign ? hideNode : isHideNode }"
    >
      <div class="head">
        <div class="title">
          <label handler="title" class="ds-font-3">办理情况</label>
        </div>
        <div class="set">
          <el-checkbox v-if="isDesign" v-model="hideNode" label="隐藏异常环节"></el-checkbox>
          <el-checkbox v-if="isDesign" v-model="hideYj" label="隐藏无意见环节"></el-checkbox>
          <el-checkbox v-if="!isDesign" v-model="isHideNode" label="隐藏异常环节"></el-checkbox>
          <el-checkbox v-if="!isDesign" v-model="isHideYj" label="隐藏无意见环节"></el-checkbox>
        </div>
      </div>
      <div class="content" handler="content">
        <div
          v-for="(item, index) in data"
          :key="index"
          class="item"
          :class="{
            unusual: item.STATUS == '-4',
            noyj: !item.opinionatts || (item.opinionatts && !item.opinionatts.Content && !item.opinionatts.Attachments),
          }"
        >
          <div class="fl-box">
            <span>{{ item.reachtime || "无数据" }}</span>
            <img class="tx" :src="require('_platform/assets/styles/themes/workFlow/head_logo.png')" />
            <img class="icon" :src="require(`_platform/assets/styles/themes/workFlow/status${getStatus(item.STATUS)}.png`)" />
          </div>
          <div class="fr-box" :class="{ end: item.nodetype === '299' }">
            <div class="des">
              <span>处理环节：{{ item.nodename || "无数据" }}</span>
              <span v-if="item.nodetype !== '299'">处理人：{{ item.rusername || "无数据" }}</span>
              <span v-if="item.nodetype !== '299'">签收时间：{{ item.receivetime || "无数据" }}</span>
            </div>
            <div v-if="showResult && item.nodetype !== '299'" class="result">
              处理结果：
              {{ results[item.EXSTATUS] }}
            </div>
            <div v-if="item.EXSTATUS != '-8' && item.EXSTATUS != '-9' && item.nodetype !== '201' && item.nodetype !== '299'" class="yj">
              处理意见：
              {{ (item.opinionatts && item.opinionatts.Content) || processingOpinion }}
            </div>
            <div v-if="item.opinionatts && item.opinionatts.Attachments && item.opinionatts.Attachments.length > 0" class="ds_uploadFile">
              <table class="layui-table fileContainer" width="100%">
                <tbody>
                  <tr v-for="(file, i) in item.opinionatts.Attachments" :key="i" class="item">
                    <td class="textLeft">
                      <div class="fileName" :class="diffIcon(file.suffix || file.type)">
                        <b class="icon midIcon"></b>
                        <span>{{ file.originalFileName }}</span>
                      </div>
                    </td>
                    <td class="blank fontpostion" width="100">
                      {{ computeFileSize(file.size) }}
                    </td>
                    <td class="operate fontpostion" width="100">
                      <span class="previewBtn iconfont" title="预览" @click="preview(file)">&#xeb8d;</span>
                      <span class="downLoad iconfont downLoadBtn">
                        <i class="iconfont" title="下载" @click="downLoadFile(file)">&#xec22;</i>
                      </span>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import control from "../../../mixins/control";
export default dsf.component({
  name: "DsfFlowProcess",
  mixins: [control],
  ctrlCaption: "办理情况",
  props: {
    caption: {
      type: String,
      default() {
        return "wfprocess";
      },
    },
    hideNode: {
      type: Boolean,
      default() {
        return true;
      },
    },
    hideYj: {
      type: Boolean,
      default() {
        return true;
      },
    },
    showResult: {
      type: Boolean,
      default() {
        return false;
      },
    },
    processingOpinion: {
      type: String,
      default() {
        return "无数据";
      },
    },
  },
  data() {
    const data = (this.$dialog && this.$dialog.dialogArgs && this.$dialog.dialogArgs.data && this.$dialog.dialogArgs.data[0]) || null;
    return {
      sPID: data ? data.sPID : this.$route.query.sPID,
      sCurLinkID: data ? data.sCurLinkID : this.$route.query.sCurLinkID,
      isHideNode: this.hideNode,
      isHideYj: this.hideYj,
      data: [],
      results: {
        "-9": "提交", // 初始化提交
        "-8": "提交", // 修改后提交
        0: "审核通过", // 环节通过
        1: "审核通过", // 流程通过
        "-1": "不通过", // 流程不通过
        "-2": "返回修改", // 环节返回修改
      },
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      if (this.isDesign) {
        this.data = [
          {
            nodename: "开始",
            EXSTATUS: -9,
            STATUS: 2,
            linkid: "faedda583f7742948b0714f0f17d1085",
            opinionatts: {
              Content: "同意",
            },
            ruserid: "",
            pid: "af60e455757d48dd801d07b1538eb806",
            rusername: "平台管理员",
            rdeptname: "平台管理组",
            reachtime: "2020年01月06日 18:21:21",
            receivetime: "2020年1月2日 10:21:21",
          },
          {
            nodename: "审批",
            EXSTATUS: -1,
            STATUS: -4,
            linkid: "saedda583f7742948b0714f0f17d1085",
            opinionatts: {
              Content: "不同意",
            },
            ruserid: "",
            pid: "sf60e455757d48dd801d07b1538eb806",
            rusername: "平台管理员",
            rdeptname: "平台管理组",
            reachtime: "2020年01月06日 18:21:21",
            receivetime: "2020年1月2日 10:21:21",
          },
          {
            nodename: "拟稿",
            EXSTATUS: 0,
            STATUS: 1,
            linkid: "caedda583f7742948b0714f0f17d1085",
            opinionatts: {
              Content: "",
            },
            ruserid: "",
            pid: "cf60e455757d48dd801d07b1538eb806",
            rusername: "平台管理员",
            rdeptname: "平台管理组",
            reachtime: "2020年01月06日 18:21:21",
            receivetime: "2020年1月2日 10:21:21",
          },
        ];
      } else {
        this.$http
          .get("wfr/getProcessInfoByPID", {
            sPID: this.sPID,
          })
          .then((res) => {
            if (res.data.success) {
              res.data.data.forEach((item) => {
                item.opinionatts = JSON.parse(item.opinionatts);
                if (item.linkid === this.sCurLinkID && item.pid === this.sPID) {
                  if (item.STATUS != 0 && item.STATUS != 1) {
                    let params = dsf.mix({}, this.$route.query, {
                      isHide: true,
                    });
                    //更新url添加id参数
                    this.$router.replace({
                      path: this.$route.path,
                      query: params,
                    });
                  }
                }
              });
              this.data = res.data.data;
            }
          });
      }
    },
    getStatus(status) {
      let index;
      if (status == "-31") {
        index = "-3";
      } else if (status == "-1") {
        index = "-2";
      } else {
        index = status;
      }
      return index;
    },
    preview(file) {
      const previewUrl = dsf.url.getWebPath(`preview/file?fileId=${file.id}`);
      window.open(previewUrl);
    },
    downLoadFile(row) {
      var url = dsf.url.getWebPath("file/download");
      $(
        `<form action="${url}" method="get">
          <input type="text" name="files" value="${row.id}"/>
          <input type="text" name="zipName" value="${row.fileName || ""}"/>
        </form>`
      )
        .appendTo("body")
        .submit()
        .remove();
    },
    diffIcon(fileType) {
      let className = "";
      if (fileType.indexOf("do") > -1) {
        className = "Word";
      }
      if (fileType.indexOf("excel") > -1 || fileType.indexOf("sheet") > -1 || fileType.indexOf("xl") > -1) {
        className = "Excel";
      }
      if (fileType.indexOf("pn") > -1) {
        className = "png";
      }
      if (fileType.indexOf("plain") > -1 || fileType.indexOf("tx") > -1) {
        className = "txt";
      }
      if (fileType.indexOf("zi") > -1) {
        className = "zip";
      }
      if (fileType.indexOf("jpe") > -1) {
        className = "jpg";
      }
      if (fileType.indexOf("pdf") > -1) {
        className = "pdf";
      }
      return className;
    },
    computeFileSize(fileSize) {
      let filesizeUnit = "";
      if (parseInt(fileSize) > 1050000) {
        filesizeUnit = (fileSize / Math.pow(1024, 2)).toFixed(2) + "M";
      } else {
        filesizeUnit = (fileSize / Math.pow(1024, 1)).toFixed(2) + "K";
      }
      return filesizeUnit;
    },
  },
});
</script>
