<template>
  <div class="ds-control ds-form-item ds-datachoice" :class="getCss">
    <template v-if="simple&&readOnly&&!isDesign">
      <DsfTextProxy v-model="value" v-bind="_self.$props" :show-label="false"></DsfTextProxy>
      <slot name="error">
        <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
      </slot>
    </template>
    <template v-else>
      <label v-if="showLabel" class="ds-form-label" :style="getLabelWidthStyle()">
        {{ label }}
        <i v-if="dataCapion" class="iconfont icon-bangzhuzhongxin ds-control-helper" @click="showHelper"></i>
      </label>
      <div class="ds-form-block" :style="getFormItemBlockMarginLeft()">
        <div v-if="!readOnly" class="ds-datachoice-input" :class="{'is-disabled':disabled}" @click="clickHandler">
          <div ref="tagsInput" class="ds-datachoice-tags" :style="{'width':'calc(100% - 26px)'}">
            <span ref="tags">
              <el-tag v-for="el in value" :key="el.value" size="small" :closable="true" class="el-tag--info" :disable-transitions="true" @close="removeTag(el)">{{ el.text }}</el-tag>
            </span>
            <el-autocomplete
              v-if="autoComplete"
              ref="input"
              v-model="inputStr"
              highlight-first-item
              :popper-class="[popupClass,popupHide].join(' ')"
              :value-key="'value'"
              :trigger-on-focus="false"
              :style="{ width:inputWidth}"
              :fetch-suggestions="querySearch"
              @select="selectHandler"
            >
              <template slot-scope="{ item }">
                <slot name="autocompletelist">
                  <div>{{ item.text }}</div>
                </slot>
              </template>
            </el-autocomplete>
          </div>
          <span class="el-input__suffix" @click="openDialog">
            <span class="el-input__suffix-inner">
              <i class="el-input__icon el-icon-search"></i>
            </span>
          </span>
        </div>
        <!--只读状态只显示div-->
        <div v-else-if="readOnly" class="ds-form-readonly">
          <DsfTextProxy v-model="value" v-bind="_self.$props" :show-label="false"></DsfTextProxy>
        </div>
        <slot name="error">
          <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
        </slot>
      </div>
    </template>
  </div>
</template>
<script>
import formControl from "../../../mixins/formControl";
export default dsf.component({
  name: "DsfDataChoice",
  mixins: [formControl],
  ctrlCaption: "数据选择",
  props: {
    simple: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: "请输入"
    },
    dialogTitle: {
      type: String,
      default: "请选择"
    },
    dialogUrl: {
      type: String,
      default: ""
    },
    dialogWidth: {
      type: String,
      default: "800px"
    },
    dialogHeight: {
      type: String,
      default: "500px"
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("items-meta-data");
      }
    },
    // 值
    value: {
      type: [Array, Object],
      default() {
        return [];
      }
    },
    formatter: {
      type: String,
      default: "textValue^text"
    },
    maxTags: {
      type: Number,
      default: 0
    },
    autoComplete: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      remoteInputing: false,
      remoteItems: [],
      inputWidth: '0px',
      inputStr: "",
      popupClass: 'ds-datachoice-popup',
      popupHide: 'hide'
    }
  },
  computed: {
    selectValue: {
      get() {
        let v = this.value;
        if (dsf.isDef(v)) {
          return v.map((s) => s.value);
        }
        else {
          return [];
        }
      },
      set(v) {
        let value = [];
        _.each(v, (el) => {
          if (dsf.type(el) == 'string') {
            value.push({
              text: el,
              value: el
            });
          }
          if (dsf.hasOwn(el, 'text') && dsf.hasOwn(el, 'value')) {
            value.push(el);
          }
        })
        this.emitValueChange(value);
      }
    },
    options() {
      if (!this.remoteInputing) {
        return this.items;
      }
      else {
        return this.remoteItems;
      }
    }
  },
  mounted() {
    this.inputWidthResize();
    if (this.$refs.input) {
      let textbox = this.$refs.input.$el.querySelector("input");
      textbox.addEventListener("keydown", (evt) => {
        // var i=this.$refs.input;
        if (evt.keyCode === 8) {
          if (!this.inputStr) {
            let latTag = this.value[this.value.length - 1]
            this.removeTag(latTag);
          }
        }
        // if (evt.keyCode === 13) {
        //   if (this.inputStr) {
        //     this.addTags(this.inputStr);
        //     this.$nextTick(() => {
        //       this.inputStr = "";
        //     });
        //   }
        // }
      })
    }
  },
  updated() {
  },
  destroyed() {

  },
  watch: {
    value: {
      handler(v) {
        let nv;
        if (dsf.isUnDef(v)) {
          nv = [];
        } else if (dsf.type(v) === "string" && !v) {
          nv = [];
        } else if (dsf.type(v) === "object" && !v.value) {
          nv = [];
        } else if (!$.isArray(v)) {
          nv = [v];
        }
        if (nv) {
          this.emitValueChange(nv);
        } else {
          this.$nextTick(() => {
            this.inputWidthResize();
            this.$dispatch("change");
            this.$nextTick(() => {
              this.emitFormValidate(this.value);
            })
          })
        }
      }
    },
    inputStr: {
      handler(v) {
        if (v) {
          this.popupHide = '';
          this.$nextTick(() => {
            this.popperWidthChange();
          })
        }
        else {
          this.popupHide = 'hide'
        }
      }
    }
  },
  methods: {
    //点击后自动聚焦到文本框
    clickHandler() {
      if (this.disabled) {
        return;
      }
      this.$refs.input?.focus();
      this.inputWidthResize();
    },
    //联想选中
    selectHandler(item) {
      if (item) {
        this.inputStr = "";
        this.addTags(item)
      }
    },
    //输入框的尺寸变化
    inputWidthResize() {
      if (this.$refs.tags) {
        let w = this.$refs.tags.offsetWidth;
        let maxw = this.$refs.tagsInput.offsetWidth;
        this.inputWidth = (maxw - w - 5) + 'px';
      }
    },

    inputChangeHandler() {
      this.$nextTick(() => {
        this.$dispatch("change");
        this.emitFormValidate(this.value);
      });
    },
    getChoiceParams() {
      return {
        value: this.value
      }
    },
    openDialog() {
      if (this.disabled) {
        return;
      }
      this.remoteInputing = false;
      let _this = this;
      this.$openDialog({
        title: this.dialogTitle,
        width: this.dialogWidth,
        height: this.dialogHeight,
        top: '10vh',
        params: {
          // path:"dsfa/rm/rmlist?pid=af543da11e6e41dd8e1872755d8a5ed9&ppath=dsfa",
          path: this.$replace(this.dialogUrl, this.$vm || {}),
          choiceParams: _this.getChoiceParams(),
          selectValue: _this.value
        },
        btns: [
          {
            text: '确定',
            handler(res) {
              if (res.content) {
                let vm = res.content;
                if (vm && vm.yes && dsf.isFunction(vm.yes)) {
                  let returnValue = vm.yes();
                  if (!returnValue) {
                    return false
                  } else {
                    let result = _this.mapData(returnValue);
                    var value = _this.value || [];
                    _this.addTags(result);
                    _this.$dispatch("selected",returnValue);
                  }
                }
              }
            }
          },
          {
            text: '取消'
          }
        ]
      });
    },
    mapData(result) {
      if (!this.mapHandler) {
        let arr = dsf.type(result) == 'array' ? result : [result];
        //默认取字段名称name、_id作为组件的text、value;
        let list = [];
        list.splice(0, list.length);
        for (let i = 0; i < arr.length; i++) {
          let r = arr[i];
          let value = this.autoMapDataText(r);
          if (value) {
            list.push(value)
          }

        }
        return list;
        // this.emitValueChange(list);
      }
    },
    autoMapDataText(data) {
      let rmetakey = /(\w+)\.(\w+)/g;
      //默认检测text 和 name作为text属性
      //默认检测_id 和 value作为value属性
      let commonTextKeys = ['text', '_text', 'name', '_name', 'label', '_label'];
      let commonValueKeys = ['value', '_value', 'id', '_id', 'key', '_key'];
      let result = null;
      let text, value;
      for (let k in data) {
        let key = k;
        rmetakey.lastIndex = 0;
        let match = rmetakey.exec(k);
        if (match && match[2]) {
          key = match[2];
        }
        if (dsf.isUnDef(text) && commonTextKeys.indexOf(key) >= 0) {
          text = data[k];
        }
        if (dsf.isUnDef(value) && commonValueKeys.indexOf(key) >= 0) {
          value = data[k];
        }
      }
      if (dsf.isDef(text) || dsf.isDef(value)) {
        result = {
          text: text,
          value: value
        }
      }
      return result;
    },
    addTags(tags) {
      // let list = this.value || [];
      let list = [];
      tags = dsf.type(tags) == 'array' ? tags : [tags];
      _.each(tags, (tag, i) => {
        if (this.maxTags && this.maxTags > 0 && list.length >= this.maxTags) {
          dsf.warn('超出数据：' + JSON.stringify(tag));
          dsf.layer.message("已超出数量上限【" + this.maxTags + "】", false);
          return
        }
        if (dsf.type(tag) == "string") {
          tag = {
            text: tag,
            value: tag
          }
        }
        let index = _.findIndex(list, (el) => tag.value === el.value);
        if (index < 0) {
          list.push(tag);
        }
      })
      this.emitValueChange(list);
    },
    removeTag(tag) {
      let index = _.findIndex(this.value, (el) => {
        return el.value === tag.value
      });
      if (index >= 0) {
        this.value.splice(index, 1);
        this.emitValueChange(this.value);
      }
    },
    querySearch(queryString, cb) {
      if (queryString) {
        cb([])
        // cb([
        //   { text: "1", "value": "1" },
        //   { text: "2", "value": "2" },
        //   { text: "3", "value": "3" }
        // ])
      }
    },
    popperWidthChange() {
      if (this.$refs.input) {
        let popup = this.$refs.input.$children[1];
        let elem = this.$el.querySelector(".ds-datachoice-input");
        let rect = elem.getClientRects();
        rect = rect[0] || null;
        if (rect) {
          //强制赋予最小宽度元素不跟随文本框变小而变小
          popup.$el.style.minWidth = rect.width + "px"
        }
      }
    },
    resize() {
      this.inputWidthResize();
      this.popperWidthChange();
    }
  }
});
</script>
