<template>
  <div class="ds-control ds-label-box" :class="getCss" @dblclick="beginEdit">
    <label v-if="!isDesign" class="ds_label">
      <span :class="getLabelCss">{{ value }}</span>
      <div :style="{ 'left': paddingLeft }">
        <slot></slot>
      </div>
    </label>
    <label v-else-if="!editting" class="ds_label">
      <span :class="getLabelCss">{{ value || '无内容' }}</span>
      <div :style="{ 'left': paddingLeft }">
        <slot></slot>
      </div>
    </label>
    <div v-else ref="contenteditable" class="contenteditable" :class="getLabelCss" style="display: block" contenteditable @blur="endEdit">
      {{ value }}
    </div>
    <div v-if="!editting && fontType === 2" :contenteditable="false" class="font_4 exam-remark"></div>
    <slot name="helper"></slot>
  </div>
</template>

<script>
import text from '../../../mixins/text';

export default dsf.component({
  name: 'DsfTitle',
  mixins: [text],
    inheritAttrs: false,
  ctrlCaption: "标题",
  props: {
    value: {
      type: [String, Number],
      default: '标题'
    },
    fontType: {
      type: Number,
      default: 1
    },
    showColorLump: {
      type: Boolean,
      default: false
    },
    showUnderline: {
      type: Boolean,
      default: false
    },
    showWeight: {
      type: Boolean,
      default: false
    },
    textAlign: {
      type: String,
      default: ''
    },
    fontFamily: {
      type: String,
      default: ''
    },
    paddingLeft: {
      type: String,
      default: '0px'
    },
    // metadata: {
    //   type: Object,
    //   default() {
    //     return dsf.metadata.get("text-meta-data");
    //   }
    // }
  },
  data() {
    return {
      editting: false,
      $required: false
    }
  },
  computed: {
    getCss() {
      const $class = this.$getClass();
      $class.push(
        this.textAlign,
        this.showColorLump ? 'ds-showColorLump' : '',
        'ds_font_' + this.fontFamily,
        this.$required ? 'ds_required' : ''
      );
      return $class;
    },
    getLabelCss() {
      return [
        'ds-font' + this.fontType,
        this.showUnderline ? 'underline' : '',
        this.showWeight ? 'title' : ''
      ];
    }
  },
  methods: {
    beginEdit () {
      this.editting = true;
      this.$refs.contenteditable && setTimeout(() => {
        this.$refs.contenteditable.focus();
      }, 0);
    },
    endEdit (e) {
      if (this.isDesign) {
        this.editting = false;
        this.value = e.target.innerHTML;
      }
    }
  }
});
</script>