<template>
  <div class="ds-control ds-form-item ds-date-picker" :class="getCss">
    <template v-if="simple && readOnly && !isDesign">
      <span :title="readOnlyText">{{ readOnlyText }}</span>
      <slot name="error">
        <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
      </slot>
    </template>
    <template v-else>
      <label v-if="showLabel" class="ds-form-label" :style="getLabelWidthStyle()">
        {{ label }}
        <i v-if="dataCapion" class="iconfont icon-bangzhuzhongxin ds-control-helper" @click="showHelper"></i>
      </label>
      <div class="ds-form-block" :class="{'ds-error-border':errors.length>0}" :style="getFormItemBlockMarginLeft()">
        <el-date-picker
          v-if="!readOnly"
          v-model="value2"
          :disabled="disabled"
          :type="$chooseType"
          :editable="false"
          :format="$timeFormat"
          :clearable="clearable"
          :placeholder="placeholder"
          :value-format="valueFormat"
          :picker-options="pickerOptions"
        />
        <!--只读状态只显示div-->
        <div v-else-if="readOnly" class="ds-form-readonly">
          <span :title="readOnlyText">{{ readOnlyText }}</span>
        </div>
        <slot name="error">
          <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
        </slot>
      </div>
    </template>
  </div>
</template>

<script>
import formControl from "_platform/mixins/formControl";
import dateMixin from "./dateMixin";

export default dsf.component({
  name: "DsfDateIntervalPicker",
  mixins: [formControl, dateMixin],
  ctrlCaption: "日期区间",
  props: {
    simple: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: ""
    },
    value: {
      type: Object,
      default () {
        return {
          sdate: '',
          edate: ''
        }
      }
    },
    label: {
      type: String,
      default: "日期区间"
    },
    // 时间显示格式
    timeFormat: {
      type: String,
      default: "yyyy-MM-dd"
    },
    // 日期选择器类型
    chooseType: {
      type: String,
      default: "daterange"
    },
    // 是否显示清除按钮
    clearable: {
      type: Boolean,
      default: true
    },
    // 显示小时段
    timeInter: {
      type: Array,
      default () {
        return [
          '00:00:00',
          '23:59:59'
        ]
      }
    },
    //只读状态下格式化
    formatter: {
      type: String,
      default: "dateTime^yyyy-mm-dd"
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("date-interval-meta-data");
      }
    }
  },
  data() {
    return {
      value2: ['', '']
    }
  },
  watch: {
    'value' (to) {
      this.initValue(to);
    },
    'value2' (to) {
      const res = {
        sdate: '',
        edate: ''
      }
      if (to.length === 2) {
        res.sdate = to[0];
        res.edate = to[1];
      }
      this.emitValueChange(res);
      this.$nextTick(() => {
        this.emitFormValidate(res);
      })
    }
  },
  computed: {
    $chooseType () {
      return this.chooseType === 'datetimerange_s' ? 'datetimerange' : this.chooseType;
    },
    $timeFormat () {
      if (this.chooseType === 'datetimerange') {
        return this.timeFormat + ' HH:mm';
      }
      if (this.chooseType === 'datetimerange_s') {
        return this.timeFormat + ' HH:mm:ss';
      }
      return this.timeFormat;
    },
    valueFormat () {
      if (this.chooseType === 'datetimerange') {
        return 'yyyy-MM-dd HH:mm';
      }
      if (this.chooseType === 'datetimerange_s') {
        return 'yyyy-MM-dd HH:mm:ss';
      }
      return 'yyyy-MM-dd'
    },
    readOnlyText() {
      let proxy = Vue.filter("proxy");
      if (dsf.isDef(this.value) && dsf.type(this.value) == "object" && this.value.sdate != "" && this.value.edate != "") {
        return proxy(this.value.sdate, this.formatter) + " ~ " + proxy(this.value.edate, this.formatter);
      }
      return "";
    }
  },
  created() {
    this.initValue(this.value);
  },
  methods: {
    initValue (v) {
      if (v && v.sdate && v.edate && (this.value2[0] !== v.sdate || this.value2[1] !== v.edate)) {
        this.value2 = [v.sdate, v.edate];
      }
    }
  }
});
</script>