<template>
  <div class="ds-customer-img" :style="containerSty">
    <div class="left">
      <i v-if="required">*</i>
      <label v-if="showLabel">{{ label }}</label>
      <div class="canvas-box">
        <canvas v-if="!readOnly" ref="canvas" :width="`${width}px`" :height="`${height}px`" :style="canvasSty"></canvas>
        <img v-else ref="img" :style="canvasSty" />
      </div>
    </div>
    <div v-if="!readOnly" class="right">
      <input ref="uploadFile" type="file" accept="image/*" style="display: none" />
      <dsf-button :title="tip" icon="yunduanshangchuan" @click="preUpload">点击上传</dsf-button>
      <div class="right-item">
        <label>显示文字</label>
        <div class="input-box">
          <el-input v-model="tempValue.text" placeholder="请输入"></el-input>
        </div>
      </div>
      <div class="right-item">
        <label>字体设置</label>
        <div class="select-box">
          <el-select v-model="tempValue.fontFamily">
            <el-option v-for="item in fontList" :key="item.val" :value="item.val" :label="item.txt"></el-option>
          </el-select>
        </div>
        <el-input-number v-model="tempValue.fontSize" :controls="false" :min="0" :precision="0"></el-input-number>
        <el-button :style="boldBtnSty" @click="setBold"><span class="icon iconfont">&#xec97;</span></el-button>
        <el-button :style="italicBtnSty" @click="setItalic"><span class="icon iconfont">&#xec9c;</span></el-button>
        <el-color-picker :value="tempValue.fill" @active-change="chooseTextColor" @change="changeTextColor"></el-color-picker>
      </div>
      <div class="right-item">
        <div class="group">
          <label>文字底色</label>
          <el-color-picker :value="tempValue.textBackgroundColor" @active-change="chooseTextBgColor" @change="changeTextBgColor"></el-color-picker>
        </div>
        <div class="group">
          <label>背景颜色</label>
          <el-color-picker :value="tempValue.backgroundColor" @active-change="chooseBgColor" @change="changeBgColor"></el-color-picker>
        </div>
      </div>
    </div>
    <el-dialog title="裁剪图片" :visible.sync="dialogVisible" :close-on-click-modal="false" width="60%" @opened="dialogOpened">
      <el-row>
        <el-col :span="18" style="height: 500px">
          <vue-picture-cut ref="pictureCut" :menu-thickness="0" :src="src"></vue-picture-cut>
        </el-col>
        <el-col :span="5" :offset="1">
          <div class="btn-box">
            <label>旋转图片：</label>
            <div style="display: inline-block; white-space: nowrap">
              <el-button type="text" @click="imgLeft">
                <i class="el-icon-refresh-left"></i>
              </el-button>
              <el-button type="text" @click="imgRight">
                <i class="el-icon-refresh-right"></i>
              </el-button>
            </div>
          </div>
          <div class="btn-box">
            <label>旋转度数：</label>
            <el-input-number
              v-model="cut.angle"
              :controls="false"
              size="mini"
              :precision="0"
              :min="-180"
              :max="180"
              @blur="angleBlur"
            ></el-input-number>
          </div>
          <div class="btn-box">
            <label>翻转镜像：</label>
            <div style="display: inline-block; white-space: nowrap">
              <el-button type="text" @click="reverLevel"><i class="el-icon-sort vertial"></i></el-button>
              <el-button type="text" @click="reverVertical"><i class="el-icon-sort"></i></el-button>
            </div>
          </div>
          <div class="btn-box">
            <label>裁剪形状</label>
            <el-radio-group v-model="cut.shape">
              <el-radio label="rect">矩形</el-radio>
              <el-radio label="circle">椭圆</el-radio>
            </el-radio-group>
          </div>
          <div class="btn-box">
            <label>锁定比例</label>
            <el-radio-group v-model="cut.isRatio">
              <el-radio :label="false">否</el-radio>
              <el-radio :label="true">是</el-radio>
            </el-radio-group>
          </div>
          <div v-if="cut.isRatio" class="btn-box">
            <label>裁剪比例：</label>
            <el-input v-model="cut.ratio" size="mini" @change="changeRatio"></el-input>
            <p v-if="cut.badRatio" class="info">裁剪比例填写格式应为 宽:高</p>
          </div>
        </el-col>
      </el-row>
      <template v-slot:footer>
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="cutImg">确 定</el-button>
      </template>
    </el-dialog>
  </div>
</template>
<script>
import { VuePictureCut, createUtils } from "vue-picture-cut";
import formControl from "../../../mixins/formControl";
export default dsf.component({
  name: "DsfCustomerImg",
  mixins: [formControl],
  ctrlCaption: "封面设置",
  components: {
    VuePictureCut,
  },
  props: {
    width: {
      type: Number,
      default: 300,
    },
    height: {
      type: Number,
      default: 200,
    },
    maxFileSize: {
      type: Number,
      default: 2048,
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("customer-img-meta-data");
      },
    },
    isCutPic: {
      type: Boolean,
      default: true,
    },
    readOnly: {
      type: Boolean,
      default: false,
    },
    value: {
      type: String,
    },
  },
  data() {
    return {
      src: "",
      dialogVisible: false,
      upFileUrl: "file/upload/",
      upBase64Url: "file/base64Img",
      utils: null, //调用裁剪的工具
      cut: {
        shape: "rect",
        isRatio: false,
        badRatio: false, //判断比例的格式是否填写正确
        angle: 0,
        angleStep: 45, //每次旋转多少度
        ratio: "",
      },
      fabricCanvas: null,
      fabricText: null,
      tempValue: {
        text: "",
        fontFamily: "SimSun",
        fontSize: 18,
        fontWeight: "normal",
        fontStyle: "normal",
        backgroundImage: "",
        fill: "#000",
        borderColor: "#FF0000", //控制框属性
        cornerStrokeColor: "#FF0000", //控制框属性
        transparentCorners: false, //控制框属性
        cornerSize: 10, //控制框属性
        originX: "center",
        originY: "center",
        backgroundColor: null,
        textBackgroundColor: null,
      },
      fontList: [
        { val: "SimSun", txt: "宋体" },
        { val: "FangSong", txt: "仿宋" },
        { val: "KaiTi", txt: "楷书" },
        { val: "SimHei", txt: "黑体" },
        { val: "Microsoft YaHei", txt: "微软雅黑" },
        { val: "YouYuan", txt: "幼圆" },
      ],
      sizeList: [14, 16, 18, 22, 24],
      isFirst: true,
    };
  },
  computed: {
    canvasSty() {
      return `width: ${this.width}px; height: ${this.height}px`;
    },
    containerSty() {
      const marTop = this.isDesign ? "20px" : 0;
      return `margin-top: ${marTop}`;
    },
    tip() {
      return `只能上传图片类型的文件，大小不得超过${this.maxFileSize}kb`;
    },
    boldBtnSty() {
      if (this.tempValue.fontWeight === "bold") {
        return `background: #ff7200;color: #ffffff; border-color: #ff7200`;
      }
      return `background: #ffffff; color: #606266; border-color: #DCDFE6`;
    },
    italicBtnSty() {
      if (this.tempValue.fontStyle === "italic") {
        return `background: #ff7200;color: #ffffff; border-color: #ff7200`;
      }
      return `background: #ffffff; color: #606266; border-color: #DCDFE6`;
    },
  },
  mounted() {
    this.createFabric();
  },
  watch: {
    value(val) {
      if (this.isFirst) this.initComponent(val);
    },
    "cut.isRatio"(val) {
      this.utils.setMaskResize(!val);
    },
    "cut.shape"(val) {
      this.utils.setMaskRound(val === "circle");
    },
    tempValue: {
      handler() {
        this.renderText();
      },
      deep: true,
    },
  },
  methods: {
    preUpload() {
      const self = this;
      const inputEl = $(self.$refs.uploadFile);
      inputEl.on("change", function () {
        if (!$(this).val()) return;
        const file = $(this)[0].files[0];
        if (file.size / 1024 > self.maxFileSize) {
          dsf.layer.message(`文件大小超过${self.maxFileSize}kb!`, false);
          inputEl.val("");
          return false;
        }
        //如果需要裁剪图片
        if (self.isCutPic) {
          self.dialogVisible = true;
          self.src = URL.createObjectURL(file);
        } else {
          uploadFile.call(self, file);
        }
      });
      inputEl.click();
    },
    setBold() {
      this.tempValue.fontWeight = this.tempValue.fontWeight === "normal" ? "bold" : "normal";
    },
    setItalic() {
      this.tempValue.fontStyle = this.tempValue.fontStyle === "normal" ? "italic" : "normal";
    },
    cutImg() {
      const self = this;
      const cutData = self.utils.cut(undefined, 1, "image/png");
      uploadFile.call(self, cutData.file);
    },
    createFabric(canvasInfo) {
      const self = this;
      const url = dsf.url.getWebPath("$/js/libs/fabric/fabric.js");
      self.$http
        .importFiles([url])
        .then(() => {
          self.fabricCanvas = self.fabricCanvas || new fabric.Canvas(self.$refs.canvas);
          //如果canvas 有历史记录则使用历史记录
          !canvasInfo ? self.renderByEmpty() : self.renderByHistory(canvasInfo);
        })
        .catch((err) => {
          dsf.layer.message("加载fabric文件报错", false);
        });
    },
    changeTextColor(color) {
      this.tempValue.fill = color;
    },
    changeTextBgColor(color) {
      this.tempValue.textBackgroundColor = color;
    },
    changeBgColor(color) {
      this.tempValue.backgroundColor = color;
    },
    renderByEmpty() {
      this.renderText();
    },
    renderByHistory(canvasInfo) {
      const self = this;
      self.fabricCanvas.loadFromJSON(canvasInfo, () => {
        self.fabricText = self.fabricCanvas.getObjects("text")[0];
        self.fabricCanvas.renderAll();
        self.$dispatch("get-history", canvasInfo);
      });
    },
    renderText() {
      const self = this;
      self.fabricCanvas.set("backgroundColor", self.tempValue.backgroundColor);
      const config = _.pickBy(self.tempValue, (value, key) => {
        return key !== "backgroundImage" && key !== "backgroundColor";
      });
      if (!self.tempValue.text) {
        self.fabricCanvas.remove(self.fabricText);
        self.fabricText = null;
      }
      if (!self.fabricText) {
        _.assign(config, {
          left: Math.floor(self.width / 2),
          top: Math.floor(self.height / 2),
        });
        self.fabricText = new fabric.Text(self.tempValue.text, config);
        self.fabricCanvas.add(self.fabricText);
      } else {
        self.fabricText.set(config);
      }
      self.fabricCanvas.renderAll();
    },
    imgLeft() {
      const temp = this.cut.angle;
      const current = temp - this.cut.angleStep;
      this.cut.angle = current < -180 ? 360 + current : current;
      this.utils.rotate(this.cut.angleStep, true);
    },
    imgRight() {
      const temp = this.cut.angle;
      const current = temp + this.cut.angleStep;
      this.cut.angle = current > 180 ? current - 360 : current;
      this.utils.rotate(-this.cut.angleStep, true);
    },
    changeRatio(val) {
      try {
        let width = val.split(":")[0];
        let height = val.split(":")[1];
        if (!width || !height) {
          this.cut.badRatio = true;
          return;
        }
        if (!_.isNumber(+width) || !_.isNumber(+height)) {
          this.cut.badRatio = true;
          return;
        }
        this.utils.setMaskSize(width, height);
        this.cut.badRatio = false;
      } catch (e) {
        this.cut.badRatio = true;
      }
    },
    reverLevel() {
      this.utils.setFlipH();
    },
    reverVertical() {
      this.utils.setFlipV();
    },
    angleBlur() {
      this.utils.rotateTo(-this.cut.angle, true);
    },
    dialogOpened() {
      this.utils = createUtils(this.$refs["pictureCut"]);
      URL.revokeObjectURL(this.src);
      this.src = "";
    },
    postBefore() {
      const self = this;
      //若是只读，则不上传图片，直接返回
      if (self.readOnly) {
        return Promise.resolve();
      }
      return new Promise((resolve, reject) => {
        const baseStr = self.fabricCanvas.toDataURL();
        self.$http
          .post(self.upBase64Url, { base64ImgStr: baseStr.split(",")[1] })
          .done((res) => {
            if (!res.success) {
              dsf.layer.message(res.message, false);
              reject(res.message);
              return;
            }
            self.tempValue.outputImage = res.data[0].absolutePath;
            const obj = {
              pageInfo: self.tempValue,
              canvasInfo: self.fabricCanvas.toJSON(),
            };
            self.emitValueChange(JSON.stringify(obj));
            self.$nextTick(() => {
              resolve();
            });
          })
          .error((err) => {
            dsf.layer.message("封面上传发生未知异常", false);
            reject(err);
          });
      });
    },
    chooseTextColor(color) {
      this.tempValue.fill = colorHex(color);
    },
    chooseBgColor(color) {
      this.tempValue.backgroundColor = colorHex(color);
    },
    chooseTextBgColor(color) {
      this.tempValue.textBackgroundColor = colorHex(color);
    },
    initComponent(value) {
      this.isFirst = false;
      if (this.isDesign) return false;
      const result = JSON.parse(value) || {};
      if (!this.readOnly) {
        //如果没有历史画笔
        if (!value) {
          this.createFabric();
          return;
        }
        const { pageInfo, canvasInfo } = result;
        _.assign(this.tempValue, pageInfo);
        this.createFabric(canvasInfo);
      } else {
        const { pageInfo } = result;
        if (pageInfo && pageInfo.outputImage) this.$refs.img.src = pageInfo.outputImage;
      }
    },
  },
});

function colorHex(rgb) {
  const aColor = rgb.replace(/(?:\(|\)|rgb|RGB)*/g, "").split(",");
  let strHex = "#";
  for (let i = 0; i < aColor.length; i++) {
    let hex = Number(aColor[i]).toString(16);
    if (hex.length < 2) {
      hex = "0" + hex;
    }
    strHex += hex;
  }
  if (strHex.length !== 7) {
    strHex = rgb;
  }
  return strHex;
}

function uploadFile(file) {
  const self = this;
  const formData = new FormData();
  formData.append("file", file);
  const config = {
    headers: { "Content-Type": "multipart/form-data" },
    transformRequest: [
      function (data, headers) {
        return data;
      },
    ],
  };

  self.$http
    .post(self.upFileUrl, formData, config)
    .done((res) => {
      if (!res.success) {
        dsf.layer.message(res.message, false);
        return;
      }
      self.dialogVisible = false;
      self.tempValue.backgroundImage = res.data[0]["absolutePath"];
      const img = new Image();
      img.onload = () => {
        self.fabricCanvas.setBackgroundImage(img.src, self.fabricCanvas.renderAll.bind(self.fabricCanvas), {
          scaleX: self.width / img.width,
          scaleY: self.height / img.height,
        });
      };
      img.src = res.data[0]["absolutePath"];
    })
    .error((err) => {
      dsf.layer.message("上传图片失败", false);
    })
    .always(() => {
      $(self.$refs["uploadFile"]).val("");
    });
}
</script>
