<template>
  <dsf-exam-page :res-data="resData" :exam-mark-active="examMarkActive">
    <template slot="body" slot-scope="{subjects}">
      <div class="exam-body">
        <el-checkbox-group
          v-model="checkValue[subjects.id]"
          :disabled="readOnly"
          @change="handleCheck(subjects.id, subjects.type)"
        >
          <el-checkbox
            v-for="(item, index) in subjects.items"
            :key="item.value"
            :label="item.value + '[-]' + item.text"
            :style="`width:${getColumnWidth(subjects.column)};margin-right:0;`"
          >
            {{ dsf.math.numToEnglishLetter(index + 1) }}.{{ item.text }}
          </el-checkbox>
        </el-checkbox-group>
      </div>
    </template>
  </dsf-exam-page>
</template>

<script>
import DsfExamPage from './page'
import examControl from '_platform/mixins/examControl'

export default dsf.component({
  name: 'DsfExamCheckBox',
  mixins: [examControl],
  ctrlCaption: '多选题',
  inject: ['exam'],
  props: {
    readOnly: {
      type: Boolean,
      default: false,
    },
    visible: {
      type: Boolean,
      default: true,
    },
    colNum: {
      type: Number,
      default: 0,
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get('exam-items-meta-data')
      },
    },
  },
  data() {
    return {

    }
  },
  created() {
    for (let subjects of this.resData) {
      this.$set(this.checkValue, subjects.id, [])
    }
  },
  components: {
    DsfExamPage
  }
})
</script>
