import { Loading, Message, MessageBox } from "element-ui";

export default {
  openWindow(options) {
    if (dsf.type(options) == "string") {
      let url = "";
      url = options;
      options = {};
      options.url = url;
    }
    let defaultOptions = {
      openerVm: (this._isVue ? this : null),
      url: "",
      dialogArgs: null
    };
    options = dsf.mix(defaultOptions, options);
    if (options.url.startsWith("#/")) {
      options.url = './page.html' + options.url;
    }
    let windowName = dsf.uuid();
    options.url = dsf.url.getWebPath(options.url);
    let newWin = window.open(options.url, windowName);
    if (!newWin.opener.dialogArgs) {
      newWin.opener.dialogArgs = {};
    }
    if (!newWin.opener.openerVms) {
      newWin.opener.openerVms = {};
    }
    newWin.opener.dialogArgs[windowName] = JSON.stringify(options.dialogArgs);
    newWin.opener.openerVms[windowName] = options.openerVm;
    return newWin;
  },
  openDialog(options) {
    let openerVm = this._isVue ? this : null;
    let comp = null;
    if (options)
      if (!options.template) {
        if (options.params && options.params.path && !options.content) {
          options.content = "DsfViewPart";
          options.params.showModel = "full";
          options.params.isOpenDialog=true;
        }

        if (options.content && dsf.type(options.content) !== 'string' && options.content.name) {
          comp = Object.create(options.content);
          comp.mixins = [{
            inject: {
              $dialog: {
                default: null
              }
            }
          }];
          options.content = options.content.name + "DialogCopy";
        }
      }
    if (options.version == "4.0") {
      if (options.height.endsWith("%")) {
        options.height = "calc(" + options.height.replace("%", "vh") + " - " + (options.btns ? "94px" : "44px") + ")";
      }
    }
    const elConfig = {
      router: this.$router,
      // provide() {
      //   return {
      //     openerVm: this
      //   }
      // },
      data: function () {
        return {
          index: dsf.uuid(),
          dialogVisible: true,
          //标题
          title: options.title || "对话框",
          //组件名称
          content: options.content,
          width: options.width || "50%",
          height: options.height || "auto",
          destroyOnClose: true,
          params: options.params || {},
          overflow: options.overflow || "auto",
          // 底部left居左,center居中,right居右
          footerAlign: options.footerAlign || 'right',
          btns: options.btns || [],
          events: options.event,
          fullscreen: options.fullscreen,
          openerVm: openerVm,
          dialogArgs: options.dialogArgs || null,
          top: options.top || "10vh",
          yes: options.yes || dsf.noop,
          showClose: dsf.isDef(options.showClose) ? options.showClose : true,
          modal: dsf.isDef(options.modal) ? options.modal : true,
          center: dsf.isDef(options.center) ? options.center : false,
          closeBefore: options.close || dsf.noop,
          // template:options.template||""
        };
      },
      computed: {
        options() {
          return options;
        }
      },
      methods: {
        dialogSure(result) {
          if (result !== false) {
            this.yes(result);
            this.close();
          }
        },
        async clickHandler(button) {
          let result = true;
          if (button.handler) {
            //如果是4.0版本老系统调用,必须要兼容，无奈 :-(
            try {
              if (options.version == "4.0") {
                result = await button.handler(null, null, this.$refs.content.contentWindow);
              } else {
                result = await button.handler(this.$refs.content);
              }
            } catch (err) {
              result = false;
            }
          }
          if (result !== false) {
            this.close();
          }
        },
        close() {
          if (options.closeBefore) {
            if (options.version == "4.0") {
              options.closeBefore(this.index, $(this.$el))
            } else {
              options.closeBefore(this.$refs.content)
            }
          }
          this.dialogVisible = false;
        },
        cancel() {
          let _this = this;
          if (component) {
            component = null;
          }
          window.setTimeout(() => {
            options = null;
            openerVm = null;
            _this.$destroy();
          }, 0);
        }
      },
      template: `
        <DsfDialog :title="title" :top="top" :index="index" :options="options" ref="instance" :class="{'has-button-bar':btns.length>0}" :visible.sync="dialogVisible"
                   :fullscreen="fullscreen" :width="width" :close-on-click-modal="false" :append-to-body="true"
                   @close="close" @closed="cancel" :show-close="showClose" :close-on-press-escape="false" :dialog-args="dialogArgs"
                   :modal="modal" :center="center" :opener-vm="openerVm" :footer-align="footerAlign">
          <div :style="{'position':'relative','height':(fullscreen?'100%':height),'overflow':(overflow||'auto')}">
            ${options.template ? options.template : `<component :is="content" v-bind="params" ref="content" v-on="events" @dialog-yes="dialogSure"></component>`
        }
          </div>
          <template v-slot:footer>
            <DsfButton v-for="(button,$index) in btns" :key="$index" :class="button.class" :text="button.text" :type="$index==0?'':'plain'" @click="clickHandler(button)"></DsfButton>
          </template>
        </DsfDialog>`
    }
    if (comp) {
      elConfig.components = {
        [options.content]: comp
      };
    }
    const Dialog = Vue.extend(elConfig);
    let component = new Dialog().$mount();
    document.body.appendChild(component.$el);
    return component;
  },
  loading: function (options) {
    let opts = {
      background: "rgba(255,255,255,0.5)",
      text: ""
    };
    let loadingInstance = Loading.service(dsf.mix(opts, options || {}));
    return loadingInstance;
  },
  closeLoading: function (loaaInstance) {
    loaaInstance && loaaInstance.close();
  },
  message: function (options, type) {
    if (dsf.type(options) == "string") {
      options = {
        message: options
      };
    }
    if (dsf.isUnDef(type) && dsf.isDef(options.type)) {
      Message(options);
    } else {
      if (type !== false) {
        Message[dsf.config.message.success](options);
      } else {
        Message[dsf.config.message.error](options);
      }
    }

  },
  alert: function (options) {
    if (dsf.type(options) == "string") {
      options = {
        message: options
      };
    }
    let defaultOptions = {
      confirmButtonText: "确定",
      type: "",
      top: "10vh",
      dangerouslyUseHTMLString: true,
      title: "提示",
      message: "",
      customClass: dsf.config.message.box.customClass.join(" ")
    };
    dsf.mix(defaultOptions, options);
    let msgBox = MessageBox;
    return msgBox.alert(defaultOptions.message, defaultOptions.title, defaultOptions);
  },
  confirm: function (options) {
    if (dsf.type(options) == "string") {
      options = {
        message: options
      };
    }
    let defaultOptions = {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "",
      top: "10vh",
      dangerouslyUseHTMLString: true,
      title: "提示",
      message: "",
      customClass: dsf.config.message.box.customClass.join(" ")
    };
    dsf.mix(defaultOptions, options);
    let msgBox = MessageBox;
    return msgBox.confirm(defaultOptions.message, defaultOptions.title, defaultOptions);
  },
  prompt: function (...options) {
    return MessageBox.prompt(options[0], options[1], options[2]);
  },
};