<template>
  <div class="page-home-default-aside">
    <div class="content">
      <dsf-default-home-menu ref="menu" :menu-list="menuList"></dsf-default-home-menu>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DsfDefaultHomeAsideMenu',
  inject: ['homeRoot'],
  provide() {
    return {
      homeAside: this
    };
  },
  props: {
    position: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      active: null,
      show: null
    }
  },
  computed: {
    menuList () {
      let ma = this.homeRoot.menuAction;
      ma = ma && ma.length ? ma[0] : {};
      let list = [];
      if (ma && ma.children) {
        list = ma.children;
      }
      return list;
    }
  },
  watch: {
    'menuList' (to) {
      if (!to.length) {
        this.homeRoot.setPosition(this.position, '0');
      } else {
        this.homeRoot.setPosition(this.position, '205px');
      }
    },
    'homeRoot.menuIndex'(to) {
      this.$nextTick(() => {
        this.init(to);
      });
    }
  },
  created() {
    if (!this.menuList.length) {
      this.homeRoot.setPosition(this.position, '0');
    } else {
      this.homeRoot.setPosition(this.position, '205px');
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.init(this.homeRoot.menuIndex);
    });
  },
  methods: {
    init (menuIndex) {
      let menu = this.$refs.menu;
      let active = null;
      for (let i = 1; i < menuIndex.length; i++) {
        let index = menuIndex[i];
        if (index < 0) break;
        menu = menu.$children[index];
        if (menu) {
          active = menu;
          menu = menu.$children;
          if (menu && menu.length) {
            menu = menu[0];
          } else {
            break;
          }
        } else {
          break;
        }
      }
      if (active) {
        this.setActive(active, active.level);
        if (active.$parent && active.$parent.$parent &&
          active.$parent.$parent.$options._componentTag === 'dsf-default-home-menu-item') {
          this.setShow(active.$parent.$parent);
        }
      }
    },
    setActive(active) {
      let parent = this.active;
      while (parent && parent.$options._componentTag === 'dsf-default-home-menu-item') {
        parent.isActive = false;
        parent = parent.$parent;
        if (parent) {
          parent = parent.$parent;
        } else {
          break;
        }
      }
      parent = active;
      while (parent && parent.$options._componentTag === 'dsf-default-home-menu-item') {
        parent.isActive = true;
        parent = parent.$parent;
        if (parent) {
          parent = parent.$parent;
        } else {
          break;
        }
      }
      this.active = active;
    },
    /**
     * 设置菜单是否展开
     * @param show    点击的菜单项
     * @param isShow  true展开，false关闭
     */
    setShow(show, isShow = false) {
      let parent = this.show;
      if (isShow) {
        while (parent && parent.$options._componentTag === 'dsf-default-home-menu-item') {
          parent.showChildren = false;
          parent = parent.$parent;
          if (parent) {
            parent = parent.$parent;
          } else {
            break;
          }
        }
      }
      parent = show;
      while (parent && parent.$options._componentTag === 'dsf-default-home-menu-item') {
        parent.showChildren = true;
        parent = parent.$parent;
        if (parent) {
          parent = parent.$parent;
        } else {
          break;
        }
      }
      this.show = show;
    }
  }
}
</script>